import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../store/actions'
import * as wsActions from '../websocketActions'

import pullData from '../utils/pullData'
import Layout from './Layout/Layout'
import { createStyles, makeStyles, Typography } from '@material-ui/core'
import { useTranslation } from 'react-i18next'
import ResourcePosts from './Post/ResourcePosts'
import SimilarResources from './SimilarResources'
import Helmet from 'react-helmet'
import ResourceHeader from './ResourceHeader'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const ResourceCore = props => {
    const { t } = useTranslation()

    const resourceId = props.resourceId
    if (!resourceId) return null
    const { type } = props

    const { resource, account, friendData, posts } = props

    if (!resource || !resource._id) return null

    return friendData ? (
        <div
            style={{
                display: 'flex',
                flex: 1,
                background: 'white',
                overflow: 'hidden',
            }}
        >
            <div style={{ flex: 1, overflow: 'auto' }}>
                <ResourceHeader
                    account={account}
                    resource={resource}
                    friendData={friendData}
                    type={type}
                    navi={props.navi}
                />
                {props.children}
            </div>
            <div style={{ width: '10px' }} />
            <div style={{ flex: 1, overflow: 'auto' }}>
                <ResourcePosts resource={resource} posts={posts} type={type} />
            </div>
        </div>
    ) : null
}
const mapStateToProps = state => {
    return {
        account: state.account,
        posts: state.post,
        friendData: state.friendData,
    }
}

export default withRouter(connect(mapStateToProps)(ResourceCore))
