import React from 'react'

import Select from 'antd/es/select'
import UserButton from '../UserButton/UserButton'
import { useTranslation } from 'react-i18next'
import { connect } from 'react-redux'

const ConnectionsSelect = props => {
    const { t } = useTranslation()
    const { account, friendData } = props
    return account._id && friendData ? (
        <Select
            mode="multiple"
            style={{ width: '100%' }}
            placeholder={t('g.pleaseSelect')}
            value={props.value}
            onChange={props.onChange}
            getPopupContainer={trigger => trigger.parentNode}
        >
            {[
                props.account,
                ...props.account.friends
                    .filter(friend => friend.status === 'friend')
                    .map(friend => props.friendData[friend.friend]),
            ].map(
                item =>
                    item &&
                    item._id && (
                        <Select.Option key={item._id}>
                            <UserButton id={item._id} noClick={true} />
                        </Select.Option>
                    )
            )}
        </Select>
    ) : (
        <span>Please login to share with your Addspire friends.</span>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
    }
}

export default connect(mapStateToProps)(ConnectionsSelect)
