import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const saveGoal = (accountId, goalId, value) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'saveGoal',
                accountId,
                goalId,
                value,
            })
        )
    }
}

export const deleteGoal = (accountId, goalId) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'deleteGoal',
                accountId,
                goalId,
            })
        )
    }
}

