import React, { useState } from 'react'

import * as wsActions from '../websocketActions'

import { IconButton, Menu, MenuItem, Button, Box } from '@material-ui/core'
import { MoreVertOutlined } from '@material-ui/icons'
import { useTranslation } from 'react-i18next'

const FriendActionButton = props => {
    const { t } = useTranslation()
    const [anchorEl, setAnchorEl] = useState(null)
    const { invitingFriends, invitedFriends, realFriends, _id, account } = props
    const accountId = account ? account._id : ''
    if (!accountId) return null
    const handleClose = () => {
        setAnchorEl(null)
    }
    const handleClick = event => {
        setAnchorEl(event.currentTarget)
    }

    if (accountId === _id)
        return props.additionalButtons ? (
            <>
                <IconButton onClick={e => handleClick(e)}>
                    <MoreVertOutlined />
                </IconButton>
                <Menu
                    anchorEl={anchorEl}
                    keepMounted
                    open={Boolean(anchorEl)}
                    onClose={handleClose}
                >
                    {props.additionalButtons.map((button, index) => (
                        <MenuItem
                            key={index + 100}
                            onClick={() => {
                                button.onClick()
                                handleClose()
                            }}
                        >
                            {button.title}
                        </MenuItem>
                    ))}
                </Menu>
            </>
        ) : null

    return (
        <>
            {props.minimized ? (
                props.asText ? (
                    <Button
                        onClick={e => handleClick(e)}
                        variant="contained"
                        color="primary"
                    >
                        {t('g.actions')}
                    </Button>
                ) : (
                    <IconButton onClick={e => handleClick(e)}>
                        <MoreVertOutlined />
                    </IconButton>
                )
            ) : invitingFriends.find(item => item.friend === _id) ? (
                <Box>
                    <Button
                        color="primary"
                        onClick={e => {
                            e.preventDefault()
                            e.stopPropagation()
                            wsActions.acceptFriend(accountId, _id)
                        }}
                    >
                        {t('g.acceptFriendship')}
                    </Button>
                    <IconButton onClick={e => handleClick(e)}>
                        <MoreVertOutlined />
                    </IconButton>
                </Box>
            ) : ![...realFriends, ...invitingFriends, ...invitedFriends].find(
                  item => item.friend === _id
              ) ? (
                <Box>
                    <Button
                        color="primary"
                        onClick={e => {
                            e.preventDefault()
                            e.stopPropagation()
                            wsActions.addFriend(accountId, _id)
                        }}
                    >
                        {t('g.becomeFriends')}
                    </Button>
                    <IconButton onClick={e => handleClick(e)}>
                        <MoreVertOutlined />
                    </IconButton>
                </Box>
            ) : props.asText ? (
                <Button
                    onClick={e => handleClick(e)}
                    variant="contained"
                    color="primary"
                >
                    {t('g.actions')}
                </Button>
            ) : (
                <IconButton onClick={e => handleClick(e)}>
                    <MoreVertOutlined />
                </IconButton>
            )}
            <Menu
                anchorEl={anchorEl}
                keepMounted
                open={Boolean(anchorEl)}
                onClose={handleClose}
            >
                {props.minimized &&
                    invitingFriends.find(item => item.friend === _id) && (
                        <MenuItem
                            key={7}
                            onClick={() => {
                                wsActions.acceptFriend(accountId, _id)
                                handleClose()
                            }}
                        >
                            {t('g.acceptFriendship')}
                        </MenuItem>
                    )}
                {props.minimized &&
                    ![
                        ...realFriends,
                        ...invitingFriends,
                        ...invitedFriends,
                    ].find(item => item.friend === _id) && (
                        <MenuItem
                            key={7}
                            onClick={() => {
                                wsActions.addFriend(accountId, _id)
                                handleClose()
                            }}
                        >
                            {t('g.becomeFriends')}
                        </MenuItem>
                    )}
                {props.additionalButtons &&
                    props.additionalButtons.map((button, index) => (
                        <MenuItem
                            key={index + 100}
                            onClick={() => {
                                button.onClick
                                handleClose()
                            }}
                        >
                            {button.title}
                        </MenuItem>
                    ))}
                {invitingFriends.find(item => item.friend === _id) ? (
                    <MenuItem
                        key="2"
                        onClick={() => {
                            wsActions.unfriend(accountId, _id)
                            handleClose()
                        }}
                    >
                        {t('g.decline')}
                    </MenuItem>
                ) : invitedFriends.find(item => item.friend === _id) ? (
                    <MenuItem
                        key="3"
                        onClick={() => {
                            wsActions.unfriend(accountId, _id)
                            handleClose()
                        }}
                    >
                        {t('g.cancelInvitation')}
                    </MenuItem>
                ) : realFriends.find(item => item.friend === _id) ? (
                    <MenuItem
                        key="4"
                        onClick={() => {
                            wsActions.unfriend(accountId, _id)
                            handleClose()
                        }}
                    >
                        {t('g.unfriend')}
                    </MenuItem>
                ) : null}
                {props.noFollow ? null : account.followAccounts.includes(
                      _id
                  ) ? (
                    <MenuItem
                        key="5"
                        onClick={() => {
                            wsActions.unfollowAccount(accountId, _id)
                            handleClose()
                        }}
                    >
                        {t('g.unfollow')}
                    </MenuItem>
                ) : (
                    <MenuItem
                        key="6"
                        onClick={() => {
                            wsActions.followAccount(accountId, _id)
                            handleClose()
                        }}
                    >
                        {t('g.follow')}
                    </MenuItem>
                )}
            </Menu>
        </>
    )
}

export default FriendActionButton
