import message from 'antd/es/message'
import i18n from '../translations/i18n'

export default (res, props) => {
    const { t } = i18n
    if (res.data.logout) props.history.push('/logout')
    if (res.data.home) props.history.push('/')
    if (res.data.success === false) {
        if (res.data.errorCode) {
            switch (res.data.errorCode) {
                case 'no account':
                    message.warning(t('processRes.accountNotFound'))
                    break
                case 'bad data':
                    message.error(t('processRes.error'))
                    break
                default:
                    message.warning(t('processRes.generalError'))
                    break
            }
        } else {
            message.error(t('processRes.generalError'))
        }
    } else {
        if (res.data.successCode) {
            switch (res.data.successCode) {
                case 'avatar saved':
                    message.success(t('processRes.avaSaved'))
                    break
                case 'success':
                    message.success(t('processRes.success'))
                    break
                case 'item saved':
                    message.success(t('processRes.saved'))
                    break
                case 'progress created':
                    message.success(t('processRes.goalStarted'))
                    break
                case 'reward created':
                    message.success(t('processRes.rewardStarted'))
                    break
                case 'activity created':
                    message.success(t('processRes.activityStarted'))
                    break
                default:
                    message.success(t('processRes.success'))
                    break
            }
        }
    }
    if (res.data.status && res.data.status === 'no account')
        props.history.push('/activate-account')
}
