import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const getFriendsData = (progress, accountFriends) => {
    if (webSocket && webSocket.readyState === 1) {
        if (progress) {
            let accountIds = [
                progress.owner,
                progress.worker,
                ...accountFriends,
            ]

            accountIds = [...new Set(accountIds)]

            webSocket.send(
                JSON.stringify({
                    messageCode: 'getFriendsData',
                    accountIds,
                })
            )
        }
    }
}

export const editGoalInProgress = (data, value) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'editProgress',
                progressId: data.progress._id,
                accountId: data.account._id,
                value,
            })
        )
    }
}

