import React, { useEffect, useRef, useState } from 'react'
import { withRouter } from 'react-router-dom'
import * as wsActions from '../../websocketActions'
import Message, { spanStyle } from './Message'
import AddMessage from './AddMessage'
import {
    Button,
    Card,
    CardContent,
    Chip,
    Divider,
    makeStyles,
    Typography,
} from '@material-ui/core'
import DialogFullscreen from '../UI/DialogFullscreen'
import MasonryCard from '../MasonryCard'
import FollowButton from '../FollowButton'
import UserButton from '../UserButton/UserButton'
import {
    AddCommentOutlined,
    ThumbUp,
    ThumbUpOutlined,
} from '@material-ui/icons'
import ShareButton from '../UI/ShareButton'
import { useTranslation } from 'react-i18next'
import message from 'antd/es/message'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const Post = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const { post, account, friendData, progressData } = props
    const isPostPage = props.history.location.pathname.indexOf(post._id) > -1
    const [postOpen, setPostOpen] = useState(props.showReplies || isPostPage)
    const [likes, setLikes] = useState(null)
    const getNumberOfMessages = messages => {
        let result = 0
        for (let item of messages) {
            result = result + 1 + item.replies.length
        }
        return result
    }
    const postRef = useRef(null)

    useEffect(() => {
        if (isPostPage && postRef.current) {
            window.scrollTo({
                top: postRef.current.offsetTop - 70,
                behavior: 'smooth',
            })
            setPostOpen(true)
        }
    }, [isPostPage])
    return post ? (
        <>
            {isPostPage && (
                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    {t('post.mainPost')}
                </Typography>
            )}
            <Card elevation={isPostPage && 3}>
                <CardContent ref={postRef}>
                    {!props.noStartMessage && (
                        <>
                            <Message
                                message={post.startMessage}
                                post={post}
                                account={account}
                                friendData={friendData}
                                progressData={progressData}
                                replies={[]}
                                isStart={true}
                            />

                            {post.startMessage.likes.length > 0 && (
                                <Chip
                                    icon={<ThumbUp />}
                                    color="primary"
                                    label={post.startMessage.likes.length}
                                    size="small"
                                    onClick={() =>
                                        setLikes(post.startMessage.likes)
                                    }
                                    style={{ marginBottom: '10px' }}
                                />
                            )}
                            <Divider />
                        </>
                    )}

                    <div
                        style={{
                            textAlign: 'left',
                            // marginBottom: '200px',
                            position: 'relative',
                        }}
                    >
                        {!props.noPostControls && (
                            <>
                                <div
                                    style={{
                                        display: 'flex',
                                        justifyContent: 'space-around',
                                    }}
                                >
                                    <span key="comment-basic-like">
                                        <Button
                                            onClick={() => {
                                                if (account._id)
                                                    wsActions.changeLikesMessage(
                                                        post.startMessage.likes.includes(
                                                            account._id
                                                        )
                                                            ? 'dislikeMessage'
                                                            : 'likeMessage',
                                                        post.startMessage
                                                            .messageId,
                                                        account._id,
                                                        post._id
                                                    )
                                                else
                                                    message.warning(
                                                        t('g.needLogin')
                                                    )
                                            }}
                                            startIcon={<ThumbUpOutlined />}
                                        >
                                            <span style={spanStyle}>
                                                {post.startMessage.likes.includes(
                                                    account._id
                                                )
                                                    ? t('g.unlike')
                                                    : t('g.like')}
                                            </span>
                                        </Button>
                                    </span>
                                    <span>
                                        <Button
                                            onClick={() => setPostOpen(true)}
                                            startIcon={<AddCommentOutlined />}
                                        >
                                            <span style={spanStyle}>
                                                {t('post.comment')}
                                            </span>
                                        </Button>
                                    </span>
                                    {/* <span key="comment-basic-like">
                                        <ShareButton
                                            post
                                            link={
                                                post.parent.parentType ===
                                                    'progress' &&
                                                'https://addspire.com/goals/' +
                                                    post.parent.parentId +
                                                    '/post/' +
                                                    post._id
                                            }
                                            media={
                                                post.startMessage.image &&
                                                post.startMessage.image.length >
                                                    0 &&
                                                post.startMessage.image[0]
                                            }
                                            title={t('post.shareText')}
                                            item={{
                                                postId: post._id,
                                            }}
                                        />
                                    </span> */}
                                </div>
                                <Divider />
                            </>
                        )}
                        {!postOpen ? (
                            post.messages.length > 0 && (
                                <Button onClick={() => setPostOpen(true)}>
                                    <span
                                        style={{
                                            ...spanStyle,
                                            fontSize: '15px',
                                        }}
                                    >
                                        {`${t('g.show')} 
                                        ${getNumberOfMessages(post.messages)} 
                                        ${t('post.comments')}`}
                                    </span>
                                </Button>
                            )
                        ) : (
                            <>
                                <AddMessage
                                    post={post}
                                    account={account}
                                    isTop={true}
                                />
                                {post.messages && post.messages.length
                                    ? post.messages.map((message, index) => (
                                          <Message
                                              key={index}
                                              message={message}
                                              post={post}
                                              account={account}
                                              friendData={friendData}
                                              replies={message.replies}
                                              isTop={true}
                                          />
                                      ))
                                    : null}
                            </>
                        )}
                    </div>
                </CardContent>
            </Card>
            <DialogFullscreen
                open={Boolean(likes)}
                close={() => setLikes(null)}
                title={t('post.messageLikes')}
            >
                {likes &&
                    likes.map(item => (
                        <MasonryCard
                            friends={true}
                            noLink={true}
                            key={item}
                            actions={
                                <FollowButton
                                    account={account}
                                    id={item}
                                    type="account"
                                />
                            }
                            body={
                                <UserButton span={true} id={item} size={50} />
                            }
                        />
                    ))}
            </DialogFullscreen>
            {isPostPage && (
                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    {t('post.otherPosts')}
                </Typography>
            )}
        </>
    ) : null
}

export default withRouter(Post)
