import { Menu, MenuItem, Tooltip, Button } from '@material-ui/core'
import { makeStyles, createStyles } from '@material-ui/core/styles'

import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import LanguageIcon from '@material-ui/icons/Language'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import { connect } from 'react-redux'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        language: {
            margin: theme.spacing(0, 0.5, 0, 1),
        },
        languageText: {
            display: 'none',
            [theme.breakpoints.up('sm')]: {
                display: 'block',
            },
        },
        languageIcon: {
            display: 'block',
            [theme.breakpoints.up('sm')]: {
                display: 'none',
            },
        },
    })
)
const LanguageSwitcher = props => {
    const { currentLanguage } = props
    const { i18n, t } = useTranslation()
    const classes = useStyles()

    const [languageMenu, setLanguageMenu] = useState(null)
    const handleLanguageIconClick = event => {
        setLanguageMenu(event.currentTarget)
    }
    const handleLanguageMenuClose = code => {
        if (code) i18n.changeLanguage(code)
        setLanguageMenu(null)
    }
    const LANGUAGES_LABEL = [
        { code: 'en', text: 'English' },
        { code: 'ru', text: '' },
    ]

    return (
        <>
            <Tooltip title={t('changeLanguage')} enterDelay={300}>
                <Button
                    color="inherit"
                    aria-owns={languageMenu ? 'language-menu' : undefined}
                    aria-haspopup="true"
                    onClick={handleLanguageIconClick}
                    data-ga-event-category="header"
                    data-ga-event-action="language"
                >
                    <LanguageIcon className={classes.languageIcon} />
                    <span
                        className={
                            props.settings
                                ? classes.language
                                : [classes.language, classes.languageText].join(
                                      ' '
                                  )
                        }
                    >
                        {LANGUAGES_LABEL.filter(
                            language => language.code === currentLanguage
                        ).length > 0
                            ? LANGUAGES_LABEL.filter(
                                  language => language.code === currentLanguage
                              )[0].text
                            : LANGUAGES_LABEL[0].text}
                    </span>
                    <ExpandMoreIcon fontSize="small" />
                </Button>
            </Tooltip>

            <Menu
                id="language-menu"
                anchorEl={languageMenu}
                open={Boolean(languageMenu)}
                onClose={() => handleLanguageMenuClose()}
            >
                {LANGUAGES_LABEL.map(language => (
                    <MenuItem
                        component="a"
                        data-no-link="true"
                        key={language.code}
                        selected={i18n.language === language.code}
                        onClick={() => handleLanguageMenuClose(language.code)}
                    >
                        {language.text}
                    </MenuItem>
                ))}
            </Menu>
        </>
    )
}

const mapStateToProps = state => {
    return {
        currentLanguage: state.language,
    }
}

export default connect(mapStateToProps)(LanguageSwitcher)
