import React, { useRef, useState, useEffect } from 'react'

import Form from 'antd/es/form'

import { nameRules } from '../../constants/fieldRules'
import ConnectionsSelect from './ConnectionsSelect'
import Select from 'antd/es/select'
import { Map, Marker, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import * as wsActions from '../../websocketActions'
import * as ELG from 'esri-leaflet-geocoder'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import LocateControl from '../../containers/Explore/LocateControl'
import { GestureHandling } from 'leaflet-gesture-handling'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import ImagePickerMulti from '../UI/ImagePickerMulti'
import {
    AppBar,
    createStyles,
    IconButton,
    makeStyles,
    Theme,
    Toolbar,
    Typography,
    Button,
    Input,
    Tabs,
    Tab,
    Paper,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
import TinyEditor from '../UI/TinyEditor'
import 'react-leaflet-fullscreen/dist/styles.css'
import FullscreenControl from 'react-leaflet-fullscreen'
import { useTranslation } from 'react-i18next'
import GoalCategories from './GoalCategories'
import { KeyboardDatePicker } from '@material-ui/pickers'
import WeekDayPicker from '../UI/WeekDayPicker'
const { Option } = Select

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            marginLeft: theme.spacing(1),
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'fixed',
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const EditResource = props => {
    const { t } = useTranslation()
    const map = useRef(null)
    useEffect(() => {
        if (map && map.current && map.current.leafletElement) {
            new ELG.Geosearch({
                useMapBounds: false,
                placeholder: t('g.mapPlaceholder'),
                title: t('g.mapTitle'),
            }).addTo(map.current.leafletElement)
            new GestureHandling(map.current.leafletElement)
        }
    }, [])
    const {
        resource,
        account,
        type,
        structure,
        setStructure,
        selectedBox,
    } = props

    const defPos = [40.689078, -74.044526]
    const positionIsProvided =
        resource && resource.position && resource.position.coordinates
    const [position, setPosition] = useState(
        positionIsProvided
            ? [
                  resource.position.coordinates[1],
                  resource.position.coordinates[0],
              ]
            : defPos
    )
    const [initialPosition, setInitialPosition] = useState(position)
    const [positionRead, setPositionRead] = useState(false)
    const [currentTab, setCurrentTab] = useState(0)

    const [descriptionPlain, setDescriptionPlain] = useState(
        resource && resource.descriptionText
    )

    const onFinish = value => {
        let structureId
        if (!(resource && resource._id) && structure && selectedBox) {
            structureId = 'b' + structure.currentId
            setStructure({
                currentId: structure.currentId + 1,
                items: {
                    ...structure.items,
                    ['b' + structure.currentId]: {
                        type: type,
                        children: [],
                        parent: selectedBox,
                        temp: true,
                        sizePos: {
                            x: 200,
                            y: 0,
                            width: 320,
                            height: 200,
                        },
                    },
                },
            })
        }
        wsActions.saveResource(
            account._id,
            {
                ...value,
                descriptionText: descriptionPlain,
                position: {
                    type: 'Point',
                    coordinates: [position[1], position[0]],
                },
                _id: resource && resource._id,
            },
            type,
            props.goToResource,
            structureId
        )

        props.cancel()
    }

    const layout = {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 },
    }

    const [form] = Form.useForm()

    const mapDragEnd = e => {
        const latlng = e.target.getLatLng()
        setPosition([latlng.lat, latlng.lng])
        form.setFieldsValue({ nomap: false })
    }
    const mapClick = e => {
        setPosition([e.latlng.lat, e.latlng.lng])
        form.setFieldsValue({ nomap: false })
    }
    const classes = useStyles()

    return !props.goalId || resource ? (
        <>
            <AppBar className={classes.appBar} color="primary">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={props.cancel}
                        aria-label="close"
                    >
                        <Close />
                    </IconButton>
                    <Typography
                        variant="h6"
                        className={classes.title}
                        color="inherit"
                    >
                        {t(
                            'g.' +
                                (resource ? 'edit' : 'add') +
                                type.charAt(0).toUpperCase() +
                                type.slice(1)
                        )}
                    </Typography>
                    <Button
                        autoFocus
                        color="inherit"
                        onClick={() => form.submit()}
                    >
                        {t('g.save')}
                    </Button>
                </Toolbar>
            </AppBar>
            <Toolbar />

            <Form
                style={{
                    maxWidth: '1200px',
                    margin: '0 auto',
                    padding: '15px',
                    width: '100%',
                }}
                form={form}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={
                    resource || {
                        name: t(
                            'g.new' +
                                type.charAt(0).toUpperCase() +
                                type.slice(1)
                        ),
                        repeat: 'no',
                        days: ['0'],
                        nomap: false,
                        users: [account._id],
                        dueDate: new Date(),
                    }
                }
                {...layout}
                onValuesChange={(changedFields, allValues) => {
                    allValues.days &&
                        form.setFieldsValue({
                            days: [...allValues.days].sort(),
                        })
                }}
            >
                <Form.Item
                    label={t('g.name')}
                    name="name"
                    rules={nameRules}
                    getValueFromEvent={event => event.target.value}
                >
                    <Input fullWidth={true} />
                </Form.Item>
                <Form.Item label={t('g.categories')} name="category">
                    <GoalCategories />
                </Form.Item>
                <Form.Item label={t('g.place')}>
                    <div
                        style={{
                            position: 'relative',
                            height: '300px',
                        }}
                    >
                        <Map
                            ref={map}
                            center={initialPosition}
                            zoom={11}
                            onclick={mapClick}
                            style={{
                                width: '100%',
                                height: '100%',
                                position: 'relative',
                                zIndex: 1,
                            }}
                            gestureHandling={true}
                            onlocationfound={e => {
                                if (!positionIsProvided && !positionRead) {
                                    mapClick(e)
                                    setInitialPosition([
                                        e.latlng.lat,
                                        e.latlng.lng,
                                    ])
                                    setPositionRead(true)
                                }
                            }}
                        >
                            <LocateControl
                                setView={
                                    resource &&
                                    resource.position &&
                                    resource.position.coordinates
                                        ? 'no'
                                        : true
                                }
                            />
                            <TileLayer
                                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                            />
                            <Marker
                                position={position}
                                draggable
                                ondragend={mapDragEnd}
                            />
                            <FullscreenControl position="topright" />
                        </Map>
                    </div>
                </Form.Item>
                {type == 'goal' ? (
                    <>
                        <Form.Item label={t('g.detailsFor')}>
                            <Paper square>
                                <Tabs
                                    value={currentTab}
                                    onChange={(event, value) =>
                                        setCurrentTab(value)
                                    }
                                    indicatorColor="primary"
                                    textColor="primary"
                                >
                                    <Tab label={t('g.fromStart')} />
                                    <Tab label={t('g.toGoal')} />
                                    <Tab label={t('g.resultFinish')} />
                                </Tabs>
                            </Paper>
                        </Form.Item>
                        <Form.Item
                            name="startImages"
                            label={t('g.images')}
                            valuePropName="images"
                            trigger="onValueChange"
                            hidden={currentTab !== 0}
                        >
                            <ImagePickerMulti />
                        </Form.Item>
                        <Form.Item
                            label={t('progress.date')}
                            name="startDate"
                            hidden={currentTab !== 0}
                        >
                            <KeyboardDatePicker
                                format="ddd, MMM D, YYYY"
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                            />
                        </Form.Item>
                        <Form.Item
                            label={t('g.desc')}
                            name="startDescription"
                            // rules={descriptionRules}
                            trigger="onEditorChange"
                            hidden={currentTab !== 0}
                        >
                            <TinyEditor />
                        </Form.Item>
                        <Form.Item
                            name="images"
                            label={t('g.images')}
                            valuePropName="images"
                            trigger="onValueChange"
                            hidden={currentTab !== 1}
                        >
                            <ImagePickerMulti />
                        </Form.Item>
                        <Form.Item
                            label={t('progress.date')}
                            name="dueDate"
                            hidden={currentTab !== 1}
                        >
                            <KeyboardDatePicker
                                format="ddd, MMM D, YYYY"
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                            />
                        </Form.Item>
                        <Form.Item
                            label={t('g.desc')}
                            name="description"
                            // rules={descriptionRules}
                            trigger="onEditorChange"
                            hidden={currentTab !== 1}
                        >
                            <TinyEditor
                                onEditorChangePlain={value =>
                                    setDescriptionPlain(value)
                                }
                            />
                        </Form.Item>
                        <Form.Item
                            name="finishImages"
                            label={t('g.images')}
                            valuePropName="images"
                            trigger="onValueChange"
                            hidden={currentTab !== 2}
                        >
                            <ImagePickerMulti />
                        </Form.Item>
                        <Form.Item
                            label={t('progress.date')}
                            name="finishDate"
                            hidden={currentTab !== 2}
                        >
                            <KeyboardDatePicker
                                format="ddd, MMM D, YYYY"
                                KeyboardButtonProps={{
                                    'aria-label': 'change date',
                                }}
                            />
                        </Form.Item>
                        <Form.Item
                            label={t('g.desc')}
                            name="finishDescription"
                            // rules={descriptionRules}
                            trigger="onEditorChange"
                            hidden={currentTab !== 2}
                        >
                            <TinyEditor />
                        </Form.Item>
                    </>
                ) : (
                    <>
                        <Form.Item
                            name="images"
                            label={t('g.images')}
                            valuePropName="images"
                            trigger="onValueChange"
                        >
                            <ImagePickerMulti />
                        </Form.Item>
                        <Form.Item
                            label={t('g.desc')}
                            name="description"
                            // rules={descriptionRules}
                            trigger="onEditorChange"
                        >
                            <TinyEditor
                                onEditorChangePlain={value =>
                                    setDescriptionPlain(value)
                                }
                            />
                        </Form.Item>
                    </>
                )}

                {type === 'activity' && (
                    <>
                        <Form.Item label={t('progress.achievers')} name="users">
                            <ConnectionsSelect />
                        </Form.Item>
                        <Form.Item label={t('g.repeats')} name="repeat">
                            <Select
                                getPopupContainer={trigger =>
                                    trigger.parentNode
                                }
                            >
                                <Option value="no">{t('g.noRepeat')}</Option>
                                <Option value="day">{t('g.everyDay')}</Option>
                                <Option value="weekday">
                                    {t('g.everyDayOfWeek')}
                                </Option>
                                <Option value="week">{t('g.everyWeek')}</Option>
                                <Option value="month">
                                    {t('g.everyMonth')}
                                </Option>
                                <Option value="year">{t('g.everyYear')}</Option>
                            </Select>
                        </Form.Item>
                        <Form.Item
                            noStyle
                            shouldUpdate={(prevValues, currentValues) =>
                                prevValues.repeat !== currentValues.repeat
                            }
                        >
                            {({ getFieldValue }) => {
                                return getFieldValue('repeat') === 'weekday' ? (
                                    <Form.Item label="Days of week" name="days">
                                        <WeekDayPicker />
                                    </Form.Item>
                                ) : null
                            }}
                        </Form.Item>
                    </>
                )}

                <div style={{ textAlign: 'right', marginTop: '50px' }}>
                    <Button size="large" onClick={() => props.cancel(false)}>
                        {t('g.cancel')}
                    </Button>
                    <Button
                        color="primary"
                        variant="contained"
                        type="submit"
                        size="large"
                        style={{
                            marginLeft: '10px',
                            marginRight: '50px',
                        }}
                    >
                        {t('g.save')}
                    </Button>
                </div>
            </Form>
        </>
    ) : null
}
export default EditResource
