import fieldLength from './fieldLength'
import i18n from '../translations/i18n'

export const nicknameRules = [
    {
        required: true,
        message: i18n.t('rules.nicknameShort'),
        whitespace: true,
        min: 2,
    },
    {
        message: `${i18n.t('rules.nicknameLong')} ${fieldLength.name}.`,
        whitespace: true,
        max: fieldLength.name,
    },
    {
        pattern: /^[a-zA-Z0-9_-]*$/,
        message: i18n.t('rules.nicknameType'),
    },
]

export const nameRules = [
    {
        required: true,
        min: 1,
        whitespace: true,
        message: i18n.t('rules.nameShort'),
    },
    {
        max: fieldLength.name,
        message: `${i18n.t('rules.nameLong')} ${fieldLength.name}.`,
    },
]

export const accountNameRules = [
    {
        required: true,
        min: 2,
        whitespace: true,
        message: i18n.t('rules.accNameShort'),
    },
    {
        max: fieldLength.name,
        message: `${i18n.t('rules.accNameLong')} ${fieldLength.name}.`,
    },
]

export const descriptionRules = [
    {
        max: fieldLength.description,
        message: `${i18n.t('rules.descLong')} ${fieldLength.description}.`,
    },
]

export const messageRules = [
    {
        min: 1,
        whitespace: true,
        message: i18n.t('rules.messageShort'),
    },
    {
        max: fieldLength.message,
        message: `${i18n.t('rules.messageLong')} ${fieldLength.message}.`,
    },
]
