import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Helmet from 'react-helmet'

import Layout from '../../components/Layout/Layout'
import {
    Card,
    CardActionArea,
    CardContent,
    CardMedia,
    createStyles,
    Grid,
    makeStyles,
    Typography,
    Button,
} from '@material-ui/core'
import Welcome from '../../components/Welcome'
import dayjs from 'dayjs'
import 'dayjs/locale/ru'
import { useTranslation } from 'react-i18next'

dayjs().format()

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
        root: {
            flexGrow: 1,
            marginTop: '20px',
        },
        media: {
            height: 0,
            paddingTop: '56.25%', // 16:9\
            backgroundSize: 'contain',
        },
    })
)

const Blog = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [posts, setPosts] = useState([])
    const [title, setTitle] = useState('')
    const [all, setAll] = useState(false)
    useEffect(() => {
        fetch('https://addspire-blog.herokuapp.com/graphql', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                query: `
             query {
   posts (
       ${
           !all
               ? `where: { language:"${
                     props.history.location.pathname.indexOf('/ru') === 0
                         ? 'ru'
                         : 'en'
                 }" },`
               : ''
       } 
   sort:"published_at:desc"){
    url
    title
    published_at
   previewImage {
    url
  }
    subtitle
  }
}`,
            }),
        })
            .then(res => res.json())
            .then(data => {
                setPosts(data.data.posts)
                setTitle(t('blog.title'))
            })
    }, [props.history.location.pathname, all])
    return (
        <>
            {title && (
                <Helmet>
                    <title>{title}</title>
                </Helmet>
            )}
            <Layout white>
                {/* <Welcome /> */}
                <Button onClick={() => setAll(!all)} color="primary">
                    {all ? t('blog.hide') : t('blog.show')}
                </Button>
                <div className={classes.root}>
                    <Grid container spacing={3}>
                        {posts.map(post => (
                            <Grid item xs={12} md={6} xl={4}>
                                <Card>
                                    <Link
                                        to={
                                            props.history.location.pathname +
                                            '/' +
                                            post.url.split('.')[0]
                                        }
                                    >
                                        <CardActionArea>
                                            <CardMedia
                                                className={classes.media}
                                                image={
                                                    post.previewImage &&
                                                    post.previewImage.url
                                                }
                                                title={post.title}
                                            />
                                            <CardContent>
                                                <Typography
                                                    gutterBottom
                                                    variant="h5"
                                                    component="h2"
                                                >
                                                    {post.title}
                                                </Typography>
                                                <Typography
                                                    gutterBottom
                                                    variant="subtitle1"
                                                >
                                                    {post.subtitle}
                                                </Typography>

                                                <Typography
                                                    align="right"
                                                    variant="button"
                                                    color="textSecondary"
                                                    component="p"
                                                >
                                                    {dayjs(
                                                        post.published_at
                                                    ).format('MMMM D[,] YYYY')}
                                                </Typography>
                                            </CardContent>
                                        </CardActionArea>
                                    </Link>
                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </div>
            </Layout>
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Blog)
)
