import React, { useEffect, useRef } from 'react'
import FolderMM from './FolderMM'
import ProfileMM from './ProfileMM'
import Line from './Line'
import { Rnd } from 'react-rnd'
import { dragHandler, resizeHandler } from './actions'
import RewardMM from './old/RewardMM'
import GoalMM from './old/GoalMM'
import ActivityMM from './old/ActivityMM'
import { Button } from '@material-ui/core'
import ChooseWrapMM from './ChooseWrapMM'

export const resizeStyle = {
    height: '20px',
    width: '20px',
    background: '#eee',
    border: '1px #ccc solid',
    borderRadius: '10px',
    zIndex: 1,
}

const ContainerMM = props => {
    const myRef1 = useRef(null)

    const myRef4 = useRef(null)
    const {
        setSelectedBox,
        selectedBox,
        structure,
        setStructure,
        scale,
        myRef2,
        id,
        editingFolder,
        editingElement,
        selectedIndex,
        setSelectedIndex,
        setEditingElement,
        addDragStep,
        editorMode,
        setChosenResource,
        setEditingFolder,
    } = props
    const el = structure.items[props.id]

    useEffect(() => {
        const handler = e => {
            // e.stopPropagation()
        }
        if (myRef4 && myRef4.current) {
            myRef4.current.addEventListener('touchstart', handler, {
                passive: false,
            })
            myRef4.current.addEventListener('touchmove', handler, {
                passive: false,
            })
            addDragStep()
        }

        return () => {
            if (myRef4 && myRef4.current) {
                myRef4.current.removeEventListener('touchstart', handler)
                myRef4.current.removeEventListener('touchmove', handler)
            }
        }
    }, [Boolean(myRef4 && myRef4.current)])
    return el &&
        !el.temp &&
        (!el.unsorted || (el.unsorted && el.children.length > 0)) ? (
        <div>
            {el.type !== 'profile' && (
                <Line start={myRef2} end={myRef1} collapsed={props.collapsed} />
            )}
            <div
                ref={myRef4}
                style={{
                    position: el.type === 'profile' ? 'relative' : 'absolute',
                }}
            >
                <Rnd
                    scale={scale}
                    default={el.type !== 'profile' && el.sizePos}
                    disableDragging={
                        el.type === 'profile' || editingFolder || !editorMode
                    }
                    enableResizing={el.type !== 'profile' && editorMode}
                    resizeHandleStyles={
                        selectedBox === id
                            ? {
                                  bottomLeft: resizeStyle,
                                  bottomRight: resizeStyle,
                                  topLeft: resizeStyle,
                                  topRight: resizeStyle,
                              }
                            : {}
                    }
                    onMouseDown={e => {
                        if (editorMode) setSelectedBox(id)
                    }}
                    style={{
                        outline: selectedBox === id ? '1px solid blue' : 'none',
                        // overflow: 'hidden',
                    }}
                    onDrag={(e, data) => {
                        props.addDragStep()
                        dragHandler(id, data, structure, setStructure)
                    }}
                    onResize={(e, dir, elRef, delta, position) => {
                        props.addDragStep()
                        resizeHandler(
                            id,
                            elRef,
                            position,
                            structure,
                            setStructure
                        )
                    }}
                >
                    <div
                        ref={myRef1}
                        style={{
                            width: '100%',
                            height: '100%',
                            display: 'flex',
                            position: 'relative',
                            background: 'white',
                        }}
                    >
                        {/* {el.children.length > 0 && (
                            <Button
                                style={{
                                    position: 'absolute',
                                    top: '-40px',
                                    right: '0px',
                                }}
                                variant="contained"
                            >{`${el.collapsed ? 'Expand' : 'Collapse'} ${
                                el.children.length
                            }`}</Button>
                        )} */}
                        {el.type === 'folder' && (
                            <FolderMM
                                el={el}
                                id={id}
                                selectedBox={selectedBox}
                                structure={structure}
                                setStructure={setStructure}
                                scale={scale}
                                editingFolder={editingFolder}
                                editingElement={editingElement}
                                selectedIndex={selectedIndex}
                                setSelectedIndex={setSelectedIndex}
                                setEditingElement={setEditingElement}
                                setEditingFolder={setEditingFolder}
                            />
                        )}
                        {el.type === 'profile' && (
                            <ChooseWrapMM onClick={() => setChosenResource(id)}>
                                <ProfileMM />
                            </ChooseWrapMM>
                        )}

                        {el.type === 'reward' && (
                            <ChooseWrapMM onClick={() => setChosenResource(id)}>
                                <RewardMM id={id} />
                            </ChooseWrapMM>
                        )}
                        {el.type === 'goal' && (
                            <ChooseWrapMM onClick={() => setChosenResource(id)}>
                                <GoalMM id={id} />
                            </ChooseWrapMM>
                        )}
                        {el.type === 'activity' && (
                            <ChooseWrapMM onClick={() => setChosenResource(id)}>
                                <ActivityMM id={id} />
                            </ChooseWrapMM>
                        )}
                    </div>
                </Rnd>

                <div
                    style={{
                        position: 'absolute',
                        // visibility:
                        //     props.collapsed || el.collapsed
                        //         ? 'hidden'
                        //         : 'visible',
                        transform:
                            'translate(' +
                            el.sizePos.x +
                            'px, ' +
                            el.sizePos.y +
                            'px)',
                    }}
                >
                    {el.children &&
                        el.children.map(itemId => (
                            <ContainerMM
                                key={itemId}
                                id={itemId}
                                setSelectedBox={setSelectedBox}
                                selectedBox={selectedBox}
                                structure={structure}
                                setStructure={setStructure}
                                scale={scale}
                                myRef2={myRef1}
                                addDragStep={addDragStep}
                                editingFolder={editingFolder}
                                editingElement={editingElement}
                                selectedIndex={selectedIndex}
                                setSelectedIndex={setSelectedIndex}
                                setEditingElement={setEditingElement}
                                collapsed={props.collapsed || el.collapsed}
                                editorMode={editorMode}
                                setChosenResource={setChosenResource}
                                setEditingFolder={setEditingFolder}
                            />
                        ))}
                </div>
            </div>
        </div>
    ) : null
}

export default ContainerMM
