import React, { useState } from 'react'
import { connect } from 'react-redux'

import InfiniteScroll from 'react-infinite-scroller'
import message from 'antd/es/message'

import MasonryGrid from '../../components/MasonryGrid'
import MasonryCard, {
    getPH,
    placeholderImages,
} from '../../components/MasonryCard'
import Helmet from 'react-helmet'

import 'leaflet/dist/leaflet.css'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import {
    Badge,
    Box,
    CardActionArea,
    CardContent,
    CardMedia,
    createStyles,
    Divider,
    IconButton,
    InputBase,
    makeStyles,
    Paper,
    Tab,
    Tabs,
    Theme,
    Tooltip,
    Typography,
} from '@material-ui/core'
import { LocationOn, SearchOutlined } from '@material-ui/icons'
import MapSettings from './MapSettings'
import Layout from '../../components/Layout/Layout'
import { Link } from 'react-router-dom'
import ShareButton from '../../components/UI/ShareButton'
import HorizontalScrollList from '../../components/UI/HorizontalScrollList'
import Welcome from '../../components/Welcome'
import axios from 'axios'
import { useTranslation } from 'react-i18next'
import GoalCategories from '../../components/EditResource/GoalCategories'
import ReadMore from '../../components/UI/ReadMore'
import UserButton from '../../components/UserButton/UserButton'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'relative',
        },
        title: {
            fontSize: 13,
            margin: '30px 15px 10px',
            textTransform: 'uppercase',
            fontWeight: 600,
        },
    })
)

export const sortProgresses = (a, b) => {
    const valA = a.views || 0
    const valB = b.views || 0
    return valA > valB ? -1 : 1
}
const Explore = props => {
    const { t } = useTranslation()

    const [mapVisible, setMapVisible] = useState(false)
    const [foundProgresses, setFoundProgresses] = useState([])

    const [noMore, setNoMore] = useState(false)
    const [value, setValue] = useState(false)
    const [positionChanged, setPositionChanged] = useState(false)

    const [search, setSearch] = useState({
        value: '',
        withMap: false,
        distance: 20,
        zoom: 7,
        position: [40.689078, -74.044526],
        units: 'mi',
        categories: [],
        type: 'goal',
    })

    const onSearch = value => {
        let liveFoundProcesses = [...foundProgresses]
        if (value) {
            setSearch(value)
            setNoMore(false)
            setFoundProgresses([])
            liveFoundProcesses = []
        }
        if (!noMore || value) {
            setNoMore(true)
            axios
                .post('/api/explore/search', {
                    value: value || search,
                    skip: value ? 0 : liveFoundProcesses.length,
                })
                .then(res => {
                    if (res.data.success) {
                        setNoMore(res.data.noMore)
                        setFoundProgresses([
                            ...liveFoundProcesses,
                            ...res.data.progresses,
                        ])
                        if (res.data.progresses.length === 0)
                            message.warning(t('explore.noGoals'))
                        const newFriendData = {}
                        for (let friend of res.data.friends) {
                            newFriendData[friend._id] = friend
                        }
                    } else {
                        if (res.data.errorCode) {
                            message.error(t('explore.wrongData'))
                        }
                    }
                })
                .catch(err => {})
        }
    }

    const classes = useStyles()

    const urlDir =
        search.type === 'goal'
            ? 'goals'
            : search.type === 'activity'
            ? 'activities'
            : 'rewards'
    return (
        <Layout white>
            <div>
                <Helmet>
                    <title>{t('explore.title')}</title>
                    <meta name="description" content={t('explore.desc')} />
                </Helmet>

                <MapSettings
                    mapVisible={mapVisible}
                    setMapVisible={setMapVisible}
                    setFoundProgresses={setFoundProgresses}
                    onSearch={onSearch}
                    setSearch={setSearch}
                    search={search}
                    positionChanged={positionChanged}
                    setPositionChanged={setPositionChanged}
                />
                <Welcome />

                {!(props.account && props.account._id) && (
                    <Typography
                        variant="h2"
                        align="center"
                        style={{ margin: '50px auto 30px', fontWeight: 500 }}
                    >
                        {t('explore.topGoalsTitle')}
                    </Typography>
                )}
                <Tabs
                    value={search.type}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={(event, type) => onSearch({ ...search, type })}
                    style={{ marginBottom: '15px' }}
                >
                    <Tab label={t('g.goals')} value="goal" />
                    <Tab label={t('g.activities')} value="activity" />
                    <Tab label={t('g.rewards')} value="reward" />
                </Tabs>

                <Paper className={classes.root} variant="outlined">
                    <InputBase
                        className={classes.input}
                        placeholder={t(
                            'explore.' +
                                (search.type === 'goal'
                                    ? 'mapPHGoals'
                                    : search.type === 'activity'
                                    ? 'mapPHActivities'
                                    : 'mapPHRewards')
                        )}
                        onChange={e => {
                            if (!e.target.value && !search.withMap) {
                                setSearch({ ...search, value: '' })
                            }
                            setValue(e.target.value || '')
                        }}
                    />
                    <IconButton
                        className={classes.iconButton}
                        onClick={() => {
                            if (value && value !== search.value) {
                                onSearch({
                                    ...search,
                                    value,
                                })
                            }
                        }}
                    >
                        <SearchOutlined />
                    </IconButton>
                    <Divider
                        className={classes.divider}
                        orientation="vertical"
                    />
                    <IconButton
                        className={classes.iconButton}
                        onClick={() => {
                            setMapVisible(true)
                            setSearch({ ...search, withMap: true })
                        }}
                        color={search.withMap ? 'primary' : 'default'}
                    >
                        <LocationOn />
                    </IconButton>
                </Paper>
                <Box margin="10px 0px 50px">
                    <ReadMore
                        initialHeight={135}
                        addTitle={t('g.spaceCategories')}
                    >
                        <GoalCategories
                            nolim
                            value={search.categories}
                            onChange={categories =>
                                onSearch({ ...search, categories })
                            }
                        />
                    </ReadMore>
                </Box>

                {/* {loadedProgresses.length > 0 ? ( */}
                {/* ) : null} */}

                <InfiniteScroll
                    pageStart={0}
                    loadMore={() => onSearch()}
                    hasMore={!noMore}
                    loader={
                        <div className="loader" key={0}>
                            <span>{t('explore.loading')}</span>
                        </div>
                    }
                >
                    <MasonryGrid>
                        {foundProgresses
                            .sort(sortProgresses)
                            .map((item, index) => {
                                return (
                                    <MasonryCard
                                        id={item._id}
                                        key={index}
                                        onClickLink={
                                            '/' + urlDir + '/' + item._id
                                        }
                                        images={item.images}
                                        body={
                                            <>
                                                <Box
                                                    style={{
                                                        position: 'absolute',
                                                        top: '10px',
                                                    }}
                                                >
                                                    <UserButton
                                                        noName={true}
                                                        id={item.owner}
                                                        friendDataExp={
                                                            props.friendData
                                                        }
                                                    />
                                                </Box>
                                                {item.status && (
                                                    <Tooltip
                                                        title={t(
                                                            'progress.' +
                                                                (item.status ===
                                                                'process'
                                                                    ? 'inProcess'
                                                                    : item.status)
                                                        )}
                                                    >
                                                        <Box
                                                            style={{
                                                                display:
                                                                    'inline-block',
                                                                width: '10px',
                                                                height: '10px',
                                                                borderRadius:
                                                                    '5px',
                                                                marginRight:
                                                                    '5px',
                                                                background:
                                                                    item.status ===
                                                                    'process'
                                                                        ? 'blue'
                                                                        : item.status ===
                                                                          'fail'
                                                                        ? 'red'
                                                                        : item.status ===
                                                                          'dismissed'
                                                                        ? 'orange'
                                                                        : item.status ===
                                                                          'complete'
                                                                        ? 'green'
                                                                        : 'white',
                                                            }}
                                                        />
                                                    </Tooltip>
                                                )}
                                                <span
                                                    style={{
                                                        fontWeight: 'bold',
                                                    }}
                                                >
                                                    {item.name}
                                                </span>
                                            </>
                                        }
                                        actions={
                                            <ShareButton
                                                link={
                                                    'https://addspire.com/' +
                                                    urlDir +
                                                    '/' +
                                                    item._id
                                                }
                                                title={
                                                    item.name +
                                                    t('explore.shareText')
                                                }
                                                media={
                                                    item.images &&
                                                    item.images[0]
                                                }
                                                item={{
                                                    progressId: item._id,
                                                    progressName: item.name,
                                                }}
                                                profile={item.owner}
                                                linkHash={item._id}
                                            />
                                        }
                                    />
                                )
                            })}
                    </MasonryGrid>
                </InfiniteScroll>
            </div>
        </Layout>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        progressData: state.progressData,
        friendData: state.friendData,
    }
}

export default connect(mapStateToProps)(Explore)
