import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const cancelTransaction = (accountId, id) => {
    if (accountId && id && webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'cancelTransaction',
                id,
                accountId,
            })
        )
    }
}

export const confirmTransaction = (accountId, id, status) => {
    if (accountId && id && webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'confirmTransaction',
                id,
                accountId,
                status,
            })
        )
    }
}

export const deleteTransaction = (accountId, id) => {
    if (accountId && id && webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'deleteTransaction',
                accountId,
                id,
            })
        )
    }
}
