import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

const ProgressButton = props => {
    const { id, type } = props
    const dataProp =
        type === 'goals'
            ? 'progressData'
            : type === 'rewards'
            ? 'rewardData'
            : 'activityData'
    if (!props[dataProp][id]) return null

    const handleClick = () => props.history.push('/' + type + '/' + id)
    return (
        <span
            style={{
                cursor: 'pointer',
                fontWeight: 'bold',
                ...props.style,
            }}
            onClick={e => {
                e.stopPropagation()
                handleClick()
            }}
        >
            {props[dataProp][id].name}
        </span>
    )
}

const mapStateToProps = state => {
    return {
        progressData: state.progressData,
        rewardData: state.rewardData,
        activityData: state.activityData,
    }
}

export default withRouter(connect(mapStateToProps)(ProgressButton))
