import React from 'react'
import * as wsActions from '../websocketActions'
import { Button } from '@material-ui/core'
import { useTranslation } from 'react-i18next'

const FollowButton = props => {
    const { t } = useTranslation()
    const { account, type, id } = props
    if (account && account._id && account._id !== id) {
        if (type !== 'account') {
            return account[
                type === 'goal'
                    ? 'followProgresses'
                    : type === 'reward'
                    ? 'followRewards'
                    : 'followActivities'
            ].includes(id) ? (
                <Button
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.unfollowResource(account._id, id, type)
                    }}
                >
                    {t('g.unfollow')}
                </Button>
            ) : (
                <Button
                    color="primary"
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.followResource(account._id, id, type)
                    }}
                >
                    {t('g.follow')}
                </Button>
            )
        } else {
            return account.followAccounts.includes(id) ? (
                <Button
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.unfollowAccount(account._id, id)
                    }}
                >
                    {t('g.unfollow')}
                </Button>
            ) : (
                <Button
                    color="primary"
                    variant="contained"
                    onClick={e => {
                        e.stopPropagation()
                        e.preventDefault()
                        wsActions.followAccount(account._id, id)
                    }}
                >
                    {t('g.follow')}
                </Button>
            )
        }
    }
    return null
}

export default FollowButton
