import React, { useEffect, useState } from 'react'

import { TransformComponent } from 'react-zoom-pan-pinch'
import ContainerMM from './ContainerMM'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'
import {
    addFolder,
    changeParent,
    handleSetSelectedBox,
    deleteElement,
    saveValue,
    addElement,
    addBlock,
} from './actions'
import 'tui-image-editor/dist/tui-image-editor.css'
import Upload from 'antd/es/upload'
import { uploadFile } from '../../utils/uploadFile'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import Goal from '../Goal/Goal'
import Reward from '../Reward/Reward'
import Activity from '../Activity/Activity'
import BottomMenu from '../../components/Layout/BottomMenu'
import { Button, Dialog, Menu, MenuItem, Slide } from '@material-ui/core'
import { ArrowBack } from '@material-ui/icons'
import EditResource from '../../components/EditResource/EditResource'
import Profile from '../Profile/Profile'
import Layout from '../../components/Layout/Layout'
import { withRouter } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

const TransitionComponent = props => <Slide direction="up" {...props} />

const MindMap = props => {
    const { t } = useTranslation()
    const [selectedBox, setSelectedBoxDo] = useState('')

    const [changeParentMode, setChangeParentMode] = useState('')
    const [editingFolder, setEditingFolder] = useState(false)
    const [editingElement, setEditingElement] = useState(false)
    const [selectedIndex, setSelectedIndex] = useState('')
    const [chosenResource, setChosenResource] = useState('')
    const [editorMode, setEditorMode] = useState(false)
    const [anchorEl, setAnchorEl] = useState(null)
    const [startResourceVisible, setStartResourceVisible] = useState('')
    const {
        structure,
        setStructure,
        account,
        profile,
        setEventIsFromDraggable,
        scale,
        structureNotSaved,
    } = props
    const isOwner =
        account &&
        account._id &&
        profile &&
        profile._id &&
        account._id === profile._id
    useEffect(() => {
        if (profile && profile.structure && profile.structure.items) {
            setStructure(profile.structure, true)
            if (props.history.location.hash.length > 0) {
                const hash = props.history.location.hash.substring(1)
                if (hash === 'profile') setChosenResource('b0')
                else setChosenResource(hash)
            }
        }
    }, [profile && profile._id])
    useEffect(() => {
        document.body.style.overflow = 'hidden'
        return () => {
            document.body.style.overflow = 'auto'
        }
    }, [])
    if (!structure || !structure.items) return null
    const selectedElement = selectedBox && structure.items[selectedBox]
    const chosenElement = chosenResource && structure.items[chosenResource]
    const setSelectedBox = id => {
        if (id !== selectedBox) {
            setEditingElement(false)
            setEditingFolder(false)
            setSelectedIndex('')
            // setChosenResource('')
        }
        setSelectedBoxDo(id)
    }
    const handleMouseDown = e => {
        if (editorMode && !editingElement) {
            const path = e.nativeEvent.composedPath()
            for (let el of path) {
                if (el) {
                    if (
                        el.getAttribute('id') === 'panWrap' ||
                        el === document.body
                    ) {
                        setEventIsFromDraggable(false)
                        return
                    }
                    const classN = el.getAttribute('class')
                    if (classN && classN.indexOf('react-draggable') > -1) {
                        setEventIsFromDraggable(true)
                        return
                    }
                }
            }
        }
        setEventIsFromDraggable(false)
    }

    const handleClick = e => {
        if (!changeParentMode && !editingElement) {
            if (editorMode) {
                const path = e.nativeEvent.composedPath()
                for (let el of path) {
                    if (el) {
                        if (
                            el.getAttribute('id') === 'panWrap' ||
                            el === document.body
                        ) {
                            setEditingElement(false)
                            setEditingFolder(false)
                            setSelectedIndex('')
                            // setChosenResource('')
                            setSelectedBox('')
                            return
                        }
                        const classN = el.getAttribute('class')
                        if (classN && classN.indexOf('react-draggable') > -1) {
                            return
                        }
                    }
                }
            }
            setEditingElement(false)
            setEditingFolder(false)
            setSelectedIndex('')

            // setChosenResource('')
            setSelectedBox('')
        }
    }

    const actionsProps = {
        structure,
        setStructure,
        selectedBox,
        changeParentMode,
        setChangeParentMode,
        setSelectedBox,
        selectedElement,
        selectedIndex,
        setSelectedIndex,
        editingFolder,
    }

    return (
        <>
            <div
                style={{
                    display: 'flex',
                    width: '100%',
                    height: '100%',
                    flexDirection: 'column',
                }}
            >
                <div
                    style={{
                        width: '100%',
                        background: 'white',
                        zIndex: 1,
                        display: 'flex',
                    }}
                >
                    {isOwner ? (
                        <Button
                            onClick={() => {
                                if (editingFolder) {
                                    setEditingFolder(!editingFolder)
                                } else {
                                    setEditorMode(!editorMode)
                                    setSelectedBox('')
                                }
                                setSelectedIndex('')
                            }}
                            color="primary"
                        >
                            {editorMode ? <ArrowBack /> : t('g.edit')}
                        </Button>
                    ) : (
                        <Layout noContent />
                    )}
                    <div style={{ flex: 1, overflow: 'auto' }}>
                        <div>
                            {isOwner && editorMode && (
                                <>
                                    {structureNotSaved && (
                                        <Button
                                            onClick={() =>
                                                wsActions.saveStructure(
                                                    account._id,
                                                    structure
                                                )
                                            }
                                            color="primary"
                                            variant="contained"
                                        >
                                            {t('g.save')}
                                        </Button>
                                    )}
                                    <Button
                                        onClick={e =>
                                            setAnchorEl(e.currentTarget)
                                        }
                                    >
                                        {t('g.add')}
                                    </Button>
                                    <Menu
                                        anchorEl={anchorEl}
                                        keepMounted
                                        open={Boolean(anchorEl)}
                                        onClose={() => setAnchorEl(null)}
                                    >
                                        {!editingFolder && (
                                            <MenuItem
                                                onClick={() => {
                                                    setAnchorEl(null)
                                                    addBlock(actionsProps)
                                                }}
                                            >
                                                {t('g.block')}
                                            </MenuItem>
                                        )}
                                        {!editingFolder && (
                                            <MenuItem
                                                onClick={() => {
                                                    setStartResourceVisible(
                                                        'goal'
                                                    )
                                                    setAnchorEl(null)
                                                }}
                                            >
                                                {t('g.goal')}
                                            </MenuItem>
                                        )}
                                        {!editingFolder && (
                                            <MenuItem
                                                onClick={() => {
                                                    setStartResourceVisible(
                                                        'activity'
                                                    )
                                                    setAnchorEl(null)
                                                }}
                                            >
                                                {t('g.activity')}
                                            </MenuItem>
                                        )}
                                        {!editingFolder && (
                                            <MenuItem
                                                onClick={() => {
                                                    setStartResourceVisible(
                                                        'reward'
                                                    )
                                                    setAnchorEl(null)
                                                }}
                                            >
                                                {t('g.reward')}
                                            </MenuItem>
                                        )}
                                        {!editingElement && (
                                            <MenuItem
                                                onClick={() => {
                                                    if (editingFolder)
                                                        addElement(
                                                            actionsProps,
                                                            'text'
                                                        )
                                                    else
                                                        addBlock(
                                                            actionsProps,
                                                            'text'
                                                        )
                                                    setAnchorEl(null)
                                                }}
                                            >
                                                {t('g.text')}
                                            </MenuItem>
                                        )}
                                        {!editingElement && (
                                            <MenuItem
                                                onClick={() => {
                                                    if (editingFolder)
                                                        addElement(
                                                            actionsProps,
                                                            'image'
                                                        )
                                                    else
                                                        addBlock(
                                                            actionsProps,
                                                            'image'
                                                        )
                                                    setAnchorEl(null)
                                                }}
                                            >
                                                {t('g.image')}
                                            </MenuItem>
                                        )}
                                    </Menu>
                                </>
                            )}
                            {selectedElement && editorMode && (
                                <>
                                    {/* <Divider orientation="vertical" /> */}

                                    {!['profile'].includes(
                                        selectedElement.type
                                    ) &&
                                        !editingFolder && (
                                            <Button
                                                onClick={() =>
                                                    changeParent(actionsProps)
                                                }
                                                variant={
                                                    changeParentMode
                                                        ? 'contained'
                                                        : 'text'
                                                }
                                                color={
                                                    changeParentMode
                                                        ? 'primary'
                                                        : 'default'
                                                }
                                            >
                                                {t('g.changeParent')}
                                            </Button>
                                        )}

                                    {/* <button onClick={() => collapse(actionsProps)}>
                                Collapse
                            </button> */}
                                    {['folder'].includes(
                                        selectedElement.type
                                    ) && (
                                        <Button
                                            onClick={() =>
                                                deleteElement(actionsProps)
                                            }
                                        >
                                            {t('g.delete')}
                                        </Button>
                                    )}
                                    {['folder'].includes(
                                        selectedElement.type
                                    ) &&
                                        selectedElement.folderType !==
                                            'image' &&
                                        !editingFolder && (
                                            <Button
                                                onClick={() => {
                                                    setEditingFolder(true)
                                                    if (
                                                        selectedElement.folderType ===
                                                        'text'
                                                    ) {
                                                        setSelectedIndex(
                                                            selectedElement
                                                                .innerChildren[0]
                                                        )
                                                        setEditingElement(true)
                                                    }
                                                }}
                                            >
                                                {t('g.edit')}
                                            </Button>
                                        )}
                                    {['folder'].includes(
                                        selectedElement.type
                                    ) &&
                                        selectedIndex.toString() &&
                                        structure.items[selectedIndex].type ===
                                            'text' && (
                                            <Button
                                                onClick={() => {
                                                    setEditingElement(
                                                        !editingElement
                                                    )
                                                }}
                                            >
                                                {t('g.edit')}
                                            </Button>
                                        )}

                                    {selectedElement.folderType === 'image' ||
                                    (['folder'].includes(
                                        selectedElement.type
                                    ) &&
                                        selectedIndex.toString() &&
                                        structure.items[selectedIndex].type ===
                                            'image') ? (
                                        <Upload
                                            accept="image/*"
                                            showUploadList={false}
                                            customRequest={({ file }) => {
                                                uploadFile(
                                                    file,
                                                    800,
                                                    undefined,
                                                    url => {
                                                        if (url) {
                                                            saveValue(
                                                                selectedElement.folderType ===
                                                                    'image'
                                                                    ? selectedElement
                                                                          .innerChildren[0]
                                                                    : selectedIndex,
                                                                url,
                                                                structure,
                                                                setStructure
                                                            )
                                                        }
                                                    }
                                                )
                                            }}
                                        >
                                            <Button>{t('g.edit')}</Button>
                                        </Upload>
                                    ) : null}
                                </>
                            )}
                        </div>
                    </div>
                </div>

                <div
                    style={{
                        overflow: 'hidden',
                        flex: 1,
                        display: 'flex',
                    }}
                    id="panWrap"
                    onMouseDown={handleMouseDown}
                    onTouchStart={handleMouseDown}
                    onTouchEnd={() => setEventIsFromDraggable(false)}
                    onMouseUp={() => setEventIsFromDraggable(false)}
                    onClick={handleClick}
                >
                    <TransformComponent>
                        <div
                            style={{
                                cursor: 'grabbing',
                            }}
                        >
                            <ContainerMM
                                id="b0"
                                setSelectedBox={id =>
                                    handleSetSelectedBox({
                                        ...actionsProps,
                                        id,
                                    })
                                }
                                selectedBox={selectedBox}
                                structure={structure}
                                setStructure={setStructure}
                                scale={scale}
                                addDragStep={props.addDragStep}
                                editingFolder={editingFolder}
                                editingElement={editingElement}
                                selectedIndex={selectedIndex}
                                setSelectedIndex={setSelectedIndex}
                                setEditingElement={setEditingElement}
                                editorMode={editorMode}
                                setChosenResource={setChosenResource}
                                setEditingFolder={setEditingFolder}
                            />
                        </div>
                    </TransformComponent>
                </div>
            </div>
            <DialogFullscreen
                open={Boolean(chosenElement)}
                close={() => setChosenResource('')}
                title="Opened res"
            >
                {chosenElement.type === 'profile' ? (
                    <Profile setChosenResource={setChosenResource} />
                ) : chosenElement.type === 'reward' ? (
                    <Reward
                        id={chosenElement.id}
                        setChosenResource={setChosenResource}
                    />
                ) : chosenElement.type === 'activity' ? (
                    <Activity
                        id={chosenElement.id}
                        setChosenResource={setChosenResource}
                    />
                ) : chosenElement.type === 'goal' ? (
                    <Goal
                        id={chosenElement.id}
                        setChosenResource={setChosenResource}
                    />
                ) : null}
            </DialogFullscreen>
            {account._id && editorMode && (
                <>
                    {/* <DialogFullscreen
                        open={addFriendOpen}
                        close={() => {
                            setAddFriendOpen(false)
                        }}
                        title={t('g.addFriend')}
                    >
                        <AddFriends
                            close={() => setAddFriendOpen(false)}
                            open={addFriendOpen}
                            account={account}
                            friendData={props.friendData}
                        />
                    </DialogFullscreen> */}

                    <Dialog
                        fullScreen
                        open={Boolean(startResourceVisible)}
                        onClose={() => {
                            setStartResourceVisible('')
                        }}
                        TransitionComponent={TransitionComponent}
                        scroll="paper"
                    >
                        <EditResource
                            account={account}
                            friendData={props.friendData}
                            cancel={() => setStartResourceVisible('')}
                            type={startResourceVisible}
                            structure={structure}
                            setStructure={setStructure}
                            selectedBox={selectedBox}
                        />
                    </Dialog>
                </>
            )}
            <BottomMenu />
        </>
    )
    {
        /* <SpeedDial
                                    ariaLabel="SpeedDial"
                                    className={classes.speedDial}
                                    icon={<SpeedDialIcon />}
                                    onClose={(event, reason) => {
                                        if (reason !== 'mouseLeave') {
                                            domRef.current.focus()
                                            setSpeedDialOpen(false)
                                        }
                                    }}
                                    onOpen={(event, reason) =>
                                        reason === 'toggle' &&
                                        setSpeedDialOpen(true)
                                    }
                                    open={speedDialOpen}
                                >
                                    <SpeedDialAction
                                        icon={<Person />}
                                        tooltipTitle={
                                            <span
                                                style={{
                                                    whiteSpace: 'nowrap',
                                                }}
                                            >
                                                {t('g.addFriend')}
                                            </span>
                                        }
                                        tooltipOpen
                                        onClick={() => {
                                            domRef.current.focus()
                                            setAddFriendOpen(true)
                                            setSpeedDialOpen(false)
                                        }}
                                    />
                                    <SpeedDialAction
                                        icon={<Cake />}
                                        tooltipTitle={
                                            <span
                                                style={{
                                                    whiteSpace: 'nowrap',
                                                }}
                                            >
                                                {t('g.addReward')}
                                            </span>
                                        }
                                        tooltipOpen
                                        onClick={() => {
                                            domRef.current.focus()
                                            setStartResourceVisible(
                                                'reward'
                                            )
                                            setSpeedDialOpen(false)
                                        }}
                                    />
                                    <SpeedDialAction
                                        icon={<FitnessCenter />}
                                        tooltipTitle={
                                            <span
                                                style={{
                                                    whiteSpace: 'nowrap',
                                                }}
                                            >
                                                {t('g.addActivity')}
                                            </span>
                                        }
                                        tooltipOpen
                                        onClick={() => {
                                            domRef.current.focus()
                                            setStartResourceVisible(
                                                'activity'
                                            )
                                            setSpeedDialOpen(false)
                                        }}
                                    />
                                    <SpeedDialAction
                                        icon={<Star />}
                                        tooltipTitle={
                                            <span
                                                style={{
                                                    whiteSpace: 'nowrap',
                                                }}
                                            >
                                                {t('g.addGoal')}
                                            </span>
                                        }
                                        tooltipOpen
                                        onClick={() => {
                                            domRef.current.focus()
                                            setStartResourceVisible('goal')
                                            setSpeedDialOpen(false)
                                        }}
                                    />
                                </SpeedDial> */
    }
}
const mapStateToProps = state => {
    return {
        structure: state.structureMM,
        account: state.account,
        profile: state.profile,
        structureNotSaved: state.structureNotSaved,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        addDragStep: () => dispatch(actions.addDragStep()),
        setStructure: (structure, noNeedToSave) =>
            dispatch(actions.setStructure(structure, noNeedToSave)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(MindMap)
)
