import { createReducer } from '@reduxjs/toolkit'

export const initialState = {
    isAuthed: false,
    user: null,
    currentAccount: '',
    account: {},
    friendData: {},
    transactionData: {},
    progressData: {},
    currentProfile: '',
    profile: {},
    currentGoal: '',
    currentPosts: [],
    progress: {},
    post: {},
    postData: {},
    reward: {},
    activity: {},
    currentReward: '',
    currentActivity: '',
    rewardData: {},
    activityData: {},
    pageTitle: {},
    friendSearchResult: [],
    accountNotActive: false,
    lastNotifications: [],
    isConnectingWS: false,
    onlineUsers: [],
    language: 'en',
    recentShown: false,
    dragStep: 0,
    structureMM: {
        currentId: 9,
        items: {
            b0: {
                type: 'profile',
                children: ['b00'],
                parent: '',
                sizePos: { x: 0, y: 0 },
            },
            b00: {
                type: 'folder',
                unsorted: true,
                children: [],
                innerChildren: [],
                parent: 'b0',
                sizePos: {
                    x: -600,
                    y: -300,
                    width: 200,
                    height: 200,
                },
            },
        },
    },
    structureNotSaved: false,
}

const reducer = createReducer(initialState, {
    SET_DATA: (state: Object, action: Object) => {
        if (action.data) {
            for (let key of [
                'user',
                'account',
                'isAuthed',
                'currentAccount',
                'currentProfile',
                'currentPosts',
                'currentReward',
                'currentActivity',
                'profile',
                'currentGoal',
                'lastNotifications',
                'onlineUsers',
            ]) {
                if (typeof action.data[key] !== 'undefined') {
                    state[key] = action.data[key]
                }
            }

            for (let key of [
                'progress',
                'reward',
                'activity',
                'friendData',
                'progressData',
                'post',
                'postData',
                'rewardData',
                'activityData',
                'transactionData',
            ]) {
                if (typeof action.data[key] !== 'undefined') {
                    for (let attr in action.data[key]) {
                        if (action.data[key][attr]) {
                            state[key][attr] = action.data[key][attr]
                        }
                    }
                }
            }
            if (action.data.newOnlineUsers) {
                state.onlineUsers = [
                    ...state.onlineUsers,
                    ...action.data.newOnlineUsers,
                ]
            }
        }
        if (state.user) {
            if (state.user.currentAccount) {
                state.currentAccount = state.user.currentAccount
                state.accountNotActive = false
            } else if (state.user.myAccount) {
                state.currentAccount = state.user.myAccount
                state.accountNotActive = false
            } else {
                state.accountNotActive = true
            }
        }
    },
    REMOVE_DATA: (state: Object, action: Object) => {
        if (action.data) {
            for (let key of [
                'user',
                'account',
                'profile',
                'progress',
                'reward',
                'activity',
            ]) {
                for (let item of action.data[key]) {
                    if (state[key]._id === item) {
                        state[key] = {}
                    }
                }
            }
            for (let key of [
                'post',
                'friendData',
                'progressData',
                'postData',
                'rewardData',
                'activityData',
                'transactionData',
            ]) {
                for (let item of action.data[key]) {
                    delete state[key][item]
                }
            }
        }
    },
    ADD_DRAG_STEP: (state: Object) => {
        state.dragStep = state.dragStep + 1
    },
    SET_STRUCTURE: (state: Object, action: Object) => {
        state.structureMM = action.structure
        if (!action.noNeedToSave) state.structureNotSaved = true
    },
    SET_FRIEND_SEARCH_RESULT: (state: Object, action: Object) => {
        state.friendSearchResult = action.data
    },

    SET_FRIENDS_DATA: (state: Object, action: Object) => {
        if (action.data) {
            state.account.friendsData = action.data
        }
        if (action.data.perk) state.perk = action.data.perk
    },
    SET_AVATAR_V: (state: Object, action: Object) => {
        state.account.image = action.v
    },
    SET_LANGUAGE: (state: Object, action: Object) => {
        state.language = action.language
    },
    INCREASE_AVATAR_V: (state: Object) => {
        state.account.image = state.account.image + 1
    },
    SET_IS_AUTHED: (state: Object, action: Object) => {
        state.isAuthed = action.id
    },
    SET_PAGE_TITLE: (state: Object, action: Object) => {
        state.pageTitle = action.title
    },
    SET_ACCOUNT_ACTIVATED: (state: Object, action: Object) => {
        state.accountNotActive = action.value
    },
    SET_CONNECTING_WS: (state: Object, action: Object) => {
        state.isConnectingWS = action.data
    },
    SET_RECENT_SHOWN: (state: Object) => {
        state.recentShown = true
    },
    ADD_TO_STRUCTURE: (state: Object, action: Object) => {
        if (
            state.profile._id === action.accountId &&
            state.structureMM.items[action.structureId]
        ) {
            const parentId = state.structureMM.items[action.structureId].parent
            if (state.structureMM.items[parentId]) {
                state.structureMM.items[parentId] = {
                    ...state.structureMM.items[parentId],
                    children: [
                        ...state.structureMM.items[parentId].children,
                        action.resourceId,
                    ],
                }
                state.structureMM.items[action.resourceId] = {
                    ...state.structureMM.items[action.structureId],
                    id: action.resourceId,
                    temp: false,
                }
                state.structureNotSaved = true
            }
        }
        delete state.structureMM.items[action.structureId]
    },
    STRUCTURE_IS_SAVED: (state: Object) => {
        state.structureNotSaved = false
    },
})

export default reducer
