import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const deleteAccount = id => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'deleteAccount',
                accountId: id,
            })
        )
    }
}

export const editAccount = (value, id) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'editAccount',
                name: value.name,
                accountId: id,
            })
        )
    }
}

