import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const leaveProgress = data => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'leaveProgress',
                progressId: data.progress._id,
                accountId: data.account._id,
            })
        )
    }
}

export const saveResource = (
    accountId,
    value,
    type,
    goToResource,
    structureId
) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'saveResource',
                accountId,
                value,
                type,
                goToResource,
                structureId,
            })
        )
    }
}

export const leaveResource = (resourceId, type) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'leaveResource',
                resourceId,
                type,
            })
        )
    }
}
