import React, { useState } from 'react'

import * as wsActions from '../../websocketActions'
import UserButton from '../../components/UserButton/UserButton'
import Tabs from 'antd/es/tabs'
import Badge from 'antd/es/badge'
import dayjs from 'dayjs'
import {
    Button,
    CardContent,
    CardMedia,
    Chip,
    createMuiTheme,
    List,
    ListItem,
    ListItemAvatar,
    ListItemSecondaryAction,
    ListItemText,
    MenuItem,
    MuiThemeProvider,
    Select,
    Typography,
} from '@material-ui/core'
import { blue, green, orange, purple, red } from '@material-ui/core/colors'
import { fade } from '@material-ui/core/styles/colorManipulator'
import HorizontalScrollList from '../../components/UI/HorizontalScrollList'
import { getPH, placeholderImages } from '../../components/MasonryCard'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { useTranslation } from 'react-i18next'
import getNowStageId from '../../utils/getNowStageId'

dayjs().format()

const redTheme = createMuiTheme({
    palette: { primary: { main: red[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: red[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(red[600], 0.05),
                },
            },
        },
    },
})
const blueTheme = createMuiTheme({
    palette: { primary: { main: blue[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: blue[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(blue[600], 0.05),
                },
            },
        },
    },
})
const greenTheme = createMuiTheme({
    palette: { primary: { main: green[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: green[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(green[600], 0.05),
                },
            },
        },
    },
})
const orangeTheme = createMuiTheme({
    palette: { primary: { main: orange[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: orange[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(orange[600], 0.05),
                },
            },
        },
    },
})

const customTheme = createMuiTheme({
    palette: {
        primary: {
            main: purple[500],
        },
    },
    overrides: {
        MuiListItem: {
            secondaryAction: {
                paddingRight: '133px',
                paddingLeft: 0,
            },
        },
    },
})

const ActivityTable = props => {
    const { t } = useTranslation()
    const [showEarnedRewards, setShowEarnedRewards] = useState(false)
    const { account, rewardData, activityData, activityId, activity } = props

    if (!activityId) return null
    const activityObj =
        activityId === activity._id ? activity : activityData[activityId]
    if (!activityObj) return null

    const isOwner = account && account._id === activityObj.owner

    const nowStageId = getNowStageId(activityObj)
    const stages = activityObj.stages

    return stages.length === 0 ? null : (
        <MuiThemeProvider theme={customTheme}>
            <Tabs
                // activeKey={activeTab}
                type="card"
                defaultActiveKey={
                    nowStageId
                        ? stages
                              .findIndex(item => item.stageId === nowStageId)
                              .toString()
                        : '2'
                }
                // onTabClick={key => setActiveTab(key === activeTab ? -1 : key)}
            >
                {stages.map((stage, index) => {
                    const getStatus = id => {
                        if (isOwner && stage.dismissed) return 'dismissed'
                        if (stage.failBy.find(item => item.accountId === id))
                            return 'fail'
                        if (
                            stage.approvedBy.find(item => item.accountId === id)
                        )
                            return 'complete'
                        return 'process'
                    }

                    const getStatusText = id => {
                        const status = getStatus(id)
                        switch (status) {
                            case 'fail':
                                return t('progress.failed')
                            case 'complete':
                                return t('progress.completed')
                            case 'dismissed':
                                return t('progress.dismissed')
                            default:
                                return t('progress.inProcess')
                        }
                    }
                    const myStatus =
                        account && account._id && getStatus(account._id)

                    const stageStatus = getStatus(activityObj.owner)

                    const getTag = id => {
                        return id === account._id ? (
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={myStatus}
                                onChange={e =>
                                    wsActions.changeStage(
                                        e.target.value,
                                        account._id,
                                        activityObj._id,
                                        stage.stageId
                                    )
                                }
                            >
                                <MenuItem value="complete">
                                    {t('progress.complete')}
                                </MenuItem>
                                <MenuItem value="process">
                                    {t('progress.inProcess')}
                                </MenuItem>
                                <MenuItem value="fail">
                                    {t('progress.fail')}
                                </MenuItem>
                                <MenuItem value="dismissed">
                                    {t('progress.dismiss')}
                                </MenuItem>
                            </Select>
                        ) : (
                            <Chip color="primary" label={getStatusText(id)} />
                        )
                    }

                    const users = activityObj.users
                        .filter(item => item !== activityObj.owner)
                        .map(user => {
                            const userStatus = getStatus(user)
                            return (
                                <ListItem>
                                    <ListItemAvatar>
                                        <UserButton noName={true} id={user} />
                                    </ListItemAvatar>
                                    <ListItemText
                                        primary={
                                            <UserButton
                                                id={user}
                                                noAva={true}
                                                span={true}
                                                onlyName={true}
                                            />
                                        }
                                    />
                                    <ListItemSecondaryAction>
                                        <MuiThemeProvider
                                            theme={
                                                userStatus === 'process'
                                                    ? blueTheme
                                                    : userStatus === 'complete'
                                                    ? greenTheme
                                                    : userStatus === 'fail'
                                                    ? redTheme
                                                    : orangeTheme
                                            }
                                        >
                                            {getTag(user)}
                                        </MuiThemeProvider>
                                    </ListItemSecondaryAction>
                                </ListItem>
                            )
                        })

                    const owner = [
                        <ListItem>
                            <ListItemAvatar>
                                <UserButton
                                    noName={true}
                                    id={activityObj.owner}
                                />
                            </ListItemAvatar>
                            <ListItemText
                                primary={
                                    <Typography noWrap variant="body2">
                                        <UserButton
                                            id={activityObj.owner}
                                            noAva={true}
                                            span={true}
                                            onlyName={true}
                                        />
                                    </Typography>
                                }
                            />
                            <ListItemSecondaryAction>
                                <MuiThemeProvider
                                    theme={
                                        stageStatus === 'process'
                                            ? blueTheme
                                            : stageStatus === 'complete'
                                            ? greenTheme
                                            : stageStatus === 'fail'
                                            ? redTheme
                                            : orangeTheme
                                    }
                                >
                                    {getTag(activityObj.owner)}
                                </MuiThemeProvider>
                            </ListItemSecondaryAction>
                        </ListItem>,
                    ]
                    let stageDate
                    let tabTitle = <div>One time goal</div>
                    if (stage.day) {
                        stageDate = dayjs()
                            .year(stage.year)
                            .dayOfYear(stage.day)
                        tabTitle = (
                            <>
                                <div>
                                    <span>{stageDate.format('dddd')}</span>
                                </div>
                                <div>
                                    <span>{stageDate.format('D MMM YY')}</span>
                                </div>
                            </>
                        )
                    } else if (stage.week) {
                        stageDate = dayjs()
                            .year(stage.year)
                            .week(stage.week)
                        tabTitle = (
                            <>
                                <div>
                                    <span>{stageDate.format('wo')} week</span>
                                </div>
                                <div>
                                    <span>
                                        {stageDate
                                            .startOf('week')
                                            .format('D MMM YY')}
                                    </span>
                                </div>
                                <div>
                                    <span>
                                        {stageDate
                                            .endOf('week')
                                            .format('D MMM YY')}
                                    </span>
                                </div>
                            </>
                        )
                    } else if (stage.month) {
                        stageDate = dayjs()
                            .year(stage.year)
                            .month(stage.month)
                        tabTitle = (
                            <>
                                <div>
                                    <span>{stageDate.format('MMMM')}</span>
                                </div>
                                <div>
                                    <span>{stageDate.format('YYYY')}</span>
                                </div>
                            </>
                        )
                    } else if (stage.year) {
                        stageDate = dayjs().year(stage.year)

                        tabTitle = (
                            <div>
                                <span>{stageDate.format('YYYY')}</span>
                            </div>
                        )
                    }

                    return (
                        <Tabs.TabPane
                            tab={
                                <Badge
                                    color={
                                        stageStatus === 'complete'
                                            ? 'green'
                                            : stageStatus === 'dismissed'
                                            ? 'orange'
                                            : stageStatus === 'fail'
                                            ? 'volcano'
                                            : 'blue'
                                    }
                                >
                                    <span
                                        style={{
                                            fontSize: '12px',
                                            textTransform: 'uppercase',
                                            textAlign: 'center',
                                        }}
                                    >
                                        {tabTitle}
                                    </span>
                                </Badge>
                            }
                            key={index}
                        >
                            <List dense={true}>
                                {owner}
                                {users}
                            </List>

                            {stage.paid.length > 0 && (
                                <>
                                    {/* {showEarnedRewards && (
                                        // <Rewards
                                        //     rewards={stage.paid}
                                        //     readOnly
                                        // />
                                    )} */}
                                    <Button
                                        onClick={() =>
                                            setShowEarnedRewards(
                                                !showEarnedRewards
                                            )
                                        }
                                        color="primary"
                                        style={{}}
                                    >
                                        {(showEarnedRewards
                                            ? t('g.hide')
                                            : t('g.show')) +
                                            t('progress.earnedRewards')}
                                    </Button>
                                    {showEarnedRewards && (
                                        <HorizontalScrollList
                                            items={stage.paid
                                                .map(item => rewardData[item])
                                                .filter(item => item)}
                                            content={item => (
                                                <CardContent
                                                    style={{
                                                        textAlign: 'center',
                                                        position: 'relative',
                                                        padding: '5px',
                                                    }}
                                                >
                                                    <Link
                                                        to={
                                                            '/rewards/' +
                                                            item._id
                                                        }
                                                    >
                                                        <CardMedia
                                                            style={{
                                                                height: '150px',
                                                                backgroundSize:
                                                                    'contain',
                                                            }}
                                                            image={
                                                                item.images
                                                                    .length > 0
                                                                    ? item
                                                                          .images[0]
                                                                    : placeholderImages[
                                                                          getPH(
                                                                              item._id
                                                                          )
                                                                      ]
                                                            }
                                                            title={item.name}
                                                        />
                                                        <CardContent
                                                            style={{
                                                                padding: '5px',
                                                            }}
                                                        >
                                                            <Typography
                                                                variant="body2"
                                                                noWrap
                                                            >
                                                                {item.name}
                                                            </Typography>
                                                        </CardContent>
                                                    </Link>
                                                </CardContent>
                                            )}
                                        />
                                    )}
                                </>
                            )}
                        </Tabs.TabPane>
                    )
                })}
            </Tabs>
        </MuiThemeProvider>
    )
}

const mapStateToProps = state => {
    return {
        rewardData: state.rewardData,
        activityData: state.activityData,
        activity: state.activity,
        account: state.account,
    }
}

export default withRouter(connect(mapStateToProps)(ActivityTable))
