import Upload from 'antd/es/upload'
import React, { useState, useEffect } from 'react'
import PlusOutlined from '@ant-design/icons/PlusOutlined'

import { uploadFile } from '../../utils/uploadFile'
import {
    Button,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
} from '@material-ui/core'
import { useTranslation } from 'react-i18next'

const ImagePickerMulti = props => {
    const { t } = useTranslation()
    const [images, setImages] = useState(props.images || [])
    const [previewImage, setPreviewImage] = useState()

    useEffect(() => {
        props.onValueChange(images)
    }, [images])

    return (
        <>
            <Upload
                accept="image/*"
                listType="picture-card"
                fileList={images.map((image, index) => ({
                    url: image,
                    uid: index,
                }))}
                onPreview={file => {
                    setPreviewImage(file.url)
                }}
                onChange={({ fileList }) =>
                    setImages(fileList.map(item => item.url))
                }
                customRequest={({ file }) => {
                    uploadFile(file, 800, undefined, url => {
                        if (!url) {
                            setImages([...images])
                        } else {
                            setImages([...images, url])
                        }
                    })
                }}
            >
                {images.length >= 8 ? null : (
                    <div
                        style={{
                            flex: 1,
                            display: 'flex',
                            height: '100%',
                            alignItems: 'center',
                            justifyContent: 'center',
                            flexDirection: 'column',
                        }}
                        onClick={e => {
                            if (window.isRNWebView) {
                                e.stopPropagation()
                                window.ReactNativeWebView.postMessage(
                                    'uploadFile'
                                )
                                window.callback = url => {
                                    setImages([...images, url])
                                }
                            }
                        }}
                    >
                        <PlusOutlined />
                        <div className="ant-upload-text">{t('g.upload')}</div>
                    </div>
                )}
            </Upload>
            <Dialog
                open={Boolean(previewImage)}
                onClose={() => setPreviewImage()}
            >
                <DialogTitle id="scroll-dialog-title">
                    {t('g.previewImage')}
                </DialogTitle>
                <DialogContent dividers={true}>
                    <img src={previewImage} style={{ width: '100%' }} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setPreviewImage()} color="primary">
                        {t('g.close')}
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    )
}

export default ImagePickerMulti
