import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const setLastSeenNot = (accountId, notId) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'setLastSeenNot',
                accountId,
                notId,
            })
        )
    }
}
