import Cookies from 'universal-cookie'
import jwt_decode from 'jwt-decode'

import processRes from '../../utils/processRes'
import axios from 'axios'

const cookies = new Cookies()

export const tryAuth = token => (dispatch: Object) => {
    sessionStorage.setItem('systemRefresh', '0')
    if (!token) {
        token = cookies.get('auth_token')
        const rememberMe = cookies.get('rememberme')
        if (rememberMe) {
            cookies.remove('rememberme', {
                path: '/',
            })
            if (rememberMe === 'false') {
                cookies.remove('auth_token', {
                    path: '/',
                })
            }
        }
    }

    const userId = jwt_decode(token)
    axios.defaults.headers.post['Content-Type'] = 'application/json'
    axios.defaults.headers.put['Content-Type'] = 'application/json'
    axios.defaults.headers.delete['Content-Type'] = 'application/json'
    axios.defaults.headers.common.Accept = 'application/json'
    if (token && userId) {
        axios.defaults.headers.common['x-auth-token'] = token
        axios.defaults.headers.post['x-auth-token'] = token
        axios.defaults.headers.get['x-auth-token'] = token
        axios.defaults.headers.delete['x-auth-token'] = token
        axios.defaults.headers.put['x-auth-token'] = token
        dispatch(setIsAuthed(userId._id))
    } else {
        dispatch(setIsAuthed(null))
    }
}

const setIsAuthed = id => ({
    type: 'SET_IS_AUTHED',
    id,
})

export const setLanguage = language => ({
    type: 'SET_LANGUAGE',
    language,
})

export const markRecentShown = () => ({
    type: 'SET_RECENT_SHOWN',
})

export const logout = () => dispatch => {
    cookies.remove('rememberme', {
        path: '/',
    })
    cookies.remove('auth_token', {
        path: '/',
    })
    delete axios.defaults.headers.common['x-auth-token']
    delete axios.defaults.headers.post['x-auth-token']
    delete axios.defaults.headers.get['x-auth-token']
    delete axios.defaults.headers.delete['x-auth-token']
    delete axios.defaults.headers.put['x-auth-token']
    dispatch(setIsAuthed(false))
}

