export const buyPerk = (account, perk, owner, props) => dispatch => {
    const currency = account.wallet.find(item => item.user === owner._id)
    if (currency && currency.amount >= perk.price) {
        axios
            .post('/api/shop/buy', {
                perkId: perk.perkId,
                ownerId: owner._id,
            })
            .then(res => {
                processRes(res)
                if (res.data.success) {
                    dispatch(setData(res.data, props.history.location.pathname))
                }
            })
            .catch(err => {})
    } else {
        alert(
            'You cannot buy this item as you do not have enough money from this user.'
        )
    }
}

