import React, { useEffect, useRef, useCallback } from 'react'
import { Route, Switch, Redirect, withRouter } from 'react-router-dom'
import { PageView, initGA } from './components/TrackingGA'

import AuthTest from './containers/Auth/AuthTest'
import './components/UI/loadStyles'
import Blog from './containers/Blog/Blog'

import ActivateAccount from './containers/ActivateAccount/ActivateAccount'

import { connect } from 'react-redux'
import * as actions from './store/actions'
import ConnectWebsocket from './containers/ConnectWebsocket/ConnectWebsocket'
import NotificationsTracker from './containers/Dashboard/NotificationsTracker'

import RNBridge from './RNBridge'
import DashboardPosts from './containers/Dashboard/DashboardPosts'
import Explore from './containers/Explore/Explore'
import BlogArticle from './containers/Blog/BlogArticle'
import { useTranslation } from 'react-i18next'
import dayjs from 'dayjs'
import 'dayjs/locale/ru'
import MindMapWrap from './containers/MindMap/MindMapWrap'

const App = props => {
    const mountedRef = useRef(true)
    const { i18n } = useTranslation()

    const { isAuthed } = props

    const auth = useCallback(async () => {
        try {
            if (!mountedRef.current) return null
            props.tryAuth()
        } catch (error) {}
    }, [mountedRef])
    useEffect(() => {
        if (!isAuthed) {
            auth()
        }
        return () => {
            mountedRef.current = false
        }
    }, [isAuthed])

    useEffect(() => {
        initGA('UA-45758937-4')
    }, [])

    useEffect(() => {
        if (props.history.location.pathname.indexOf('/post/') === -1) {
            window.scrollTo(0, 0)
        }
        PageView()
    }, [props.history.location.pathname])

    useEffect(() => {
        const currentLanguage = i18n.language
            ? i18n.language.split('-')[0]
            : 'en'
        props.setLanguage(currentLanguage)
        dayjs.locale(currentLanguage)
    }, [i18n.language])

    const Logout = () => {
        props.logout()
        window.isRNWebView && window.ReactNativeWebView.postMessage('logout')
        return <Redirect to="/explore" />
    }

    let routes = (
        <Switch>
            <Route
                path="/"
                exact
                component={isAuthed ? MindMapWrap : Explore}
            />
            {isAuthed && <Route path="/explore" exact component={Explore} />}
            {/* <Route path="/goals" component={Goal} />
            <Route path="/rewards" component={Reward} />
            <Route path="/activities" component={Activity} />
            <Route path="/wishlist" component={Reward} /> */}
            <Route path="/profile" component={MindMapWrap} />
            {isAuthed && <Route path="/events" component={DashboardPosts} />}
            {/* s */}
            {/* <Route path="/shop" component={Perk} /> */}
            {/* <Route path="/wishlist" component={WishlistItem} /> */}
            <Route path="/logout" exact component={Logout} />
            <Route path="/ru/blog" exact component={Blog} />
            <Route path="/blog" exact component={Blog} />
            <Route path="/ru/blog" component={BlogArticle} />
            <Route path="/blog" component={BlogArticle} />
            <Route path="/activate-account" exact component={ActivateAccount} />
            {process.env.NODE_ENV === 'development' && !isAuthed && (
                <Route path="/logintest" exact component={AuthTest} />
            )}
            <Redirect to="/" />
        </Switch>
    )

    if (props.accountNotActive) {
        routes = (
            <Switch>
                <Route path="/logout" exact component={Logout} />

                <Route
                    path="/activate-account"
                    exact
                    component={ActivateAccount}
                />

                {process.env.NODE_ENV === 'development' && !isAuthed && (
                    <Route path="/logintest" exact component={AuthTest} />
                )}
                {isAuthed ? (
                    <Redirect to="/activate-account" />
                ) : (
                    <Redirect to="/" />
                )}
            </Switch>
        )
    }

    return (
        <>
            {routes}
            {isAuthed && <ConnectWebsocket />}
            {isAuthed && <NotificationsTracker />}
            <RNBridge />
        </>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        accountNotActive: state.accountNotActive,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        tryAuth: () => dispatch(actions.tryAuth()),
        logout: () => dispatch(actions.logout()),
        setLanguage: language => dispatch(actions.setLanguage(language)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(App)
)
