import React, { useState } from 'react'
import { Rnd } from 'react-rnd'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import TinyEditor from '../../components/UI/TinyEditor'
import { dragHandler, resizeHandler, saveValue } from './actions'
import { resizeStyle } from './ContainerMM'

const FolderMM = props => {
    const {
        selectedBox,
        structure,
        setStructure,
        el,
        scale,
        id,
        editingFolder,
        editingElement,
        selectedIndex,
        setSelectedIndex,
        setEditingElement,
        setEditingFolder,
    } = props
    const [textValue, setTextValue] = useState()

    return (
        <div style={{ background: 'white', flex: 1 }}>
            {el.innerChildren &&
                el.innerChildren.map(childId => {
                    const child = structure.items[childId]
                    const content = child ? (
                        <div
                            style={{
                                width: '100%',
                                height: '100%',
                                overflow: 'auto',
                                background: 'white',
                            }}
                        >
                            {child.type === 'text' ? (
                                <>
                                    <TinyEditor
                                        MM
                                        value={child.value}
                                        readOnly
                                    />
                                    <DialogFullscreen
                                        open={
                                            editingElement &&
                                            selectedBox === id &&
                                            selectedIndex === childId
                                        }
                                        close={() => {
                                            setEditingElement(false)
                                            if (el.folderType) {
                                                setSelectedIndex('')
                                                setEditingFolder(false)
                                            }
                                        }}
                                        save={() => {
                                            saveValue(
                                                childId,
                                                textValue,
                                                structure,
                                                setStructure
                                            )
                                            setEditingElement(false)
                                            if (el.folderType) {
                                                setSelectedIndex('')
                                                setEditingFolder(false)
                                            }
                                        }}
                                    >
                                        {editingElement &&
                                            selectedBox === id &&
                                            selectedIndex === childId && (
                                                <TinyEditor
                                                    onEditorChange={value =>
                                                        setTextValue(value)
                                                    }
                                                    value={child.value}
                                                />
                                            )}
                                    </DialogFullscreen>
                                </>
                            ) : child.type === 'image' ? (
                                <div
                                    style={{
                                        width: '100%',
                                        height: '100%',
                                        backgroundSize: 'contain',
                                        backgroundRepeat: 'no-repeat',
                                        backgroundPosition: 'center',
                                        backgroundImage:
                                            'url("' + child.value + '")',
                                    }}
                                />
                            ) : null}
                        </div>
                    ) : null
                    return child ? (
                        el.folderType ? (
                            content
                        ) : (
                            <Rnd
                                scale={scale}
                                default={
                                    child.sizePos || {
                                        width: el.sizePos.width - 10,
                                        height: 50,
                                        x: 5,
                                        y: 5,
                                    }
                                }
                                disableDragging={
                                    !editingFolder ||
                                    (selectedIndex === childId &&
                                        editingElement)
                                }
                                enableResizing={editingFolder}
                                onMouseDown={e => {
                                    if (editingFolder) {
                                        setSelectedIndex(childId)
                                    }
                                }}
                                style={{
                                    outline:
                                        selectedBox === id &&
                                        selectedIndex === childId
                                            ? '1px solid blue'
                                            : 'none',
                                }}
                                bounds="parent"
                                onDrag={(e, data) => {
                                    dragHandler(
                                        selectedIndex,
                                        data,
                                        structure,
                                        setStructure
                                    )
                                }}
                                onResize={(e, dir, elRef, delta, position) => {
                                    resizeHandler(
                                        selectedIndex,
                                        elRef,
                                        position,
                                        structure,
                                        setStructure
                                    )
                                }}
                                resizeHandleStyles={
                                    selectedIndex === childId
                                        ? {
                                              bottomLeft: resizeStyle,
                                              bottomRight: resizeStyle,
                                              topLeft: resizeStyle,
                                              topRight: resizeStyle,
                                          }
                                        : {}
                                }
                            >
                                {content}
                            </Rnd>
                        )
                    ) : null
                })}
        </div>
    )
}

export default FolderMM
