import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import * as wsActions from '../../websocketActions'
import { Box, Card, CardContent } from '@material-ui/core'
import UserButton from '../UserButton/UserButton'

import EditPost from './EditPost'
import { useTranslation } from 'react-i18next'

const AddPost = props => {
    const { t } = useTranslation()
    const [postOpen, setPostOpen] = useState(false)

    const { parent, account, parentType } = props

    const handleSave = (images, messageText) => {
        wsActions.addPost(account._id, parent, parentType, messageText, images)
        if (props.onSend) props.onSend()
        handleCancel()
    }

    const handleCancel = () => {
        setPostOpen(false)
    }

    return account._id ? (
        <>
            <Card elevation={3}>
                <CardContent>
                    <Box style={{ display: 'flex', width: '100%' }}>
                        <Box style={{ marginRight: '20px' }}>
                            <UserButton
                                noName={true}
                                id={account._id}
                                span={true}
                                size={36}
                            />
                        </Box>
                        <Box style={{ flex: 1 }}>
                            <Box
                                style={{
                                    background: '#efefef',
                                    padding: '5px 15px',
                                    borderRadius: '15px',
                                    color: '#7d7d7d',
                                    cursor: 'pointer',
                                }}
                                onClick={() => setPostOpen(true)}
                            >
                                {t('post.postNew')}
                            </Box>
                        </Box>
                    </Box>
                    <EditPost
                        open={postOpen}
                        cancel={handleCancel}
                        title={t('post.createNewPost')}
                        save={handleSave}
                    />
                </CardContent>
            </Card>
        </>
    ) : null
}

export default withRouter(AddPost)
