import React, { useState, memo } from 'react'
import { Rnd } from 'react-rnd'
import DialogFullscreen from '../../../components/UI/DialogFullscreen'
import TinyEditor from '../../../components/UI/TinyEditor'
import { dragHandler, resizeHandler, saveValue } from '../actions'
import { withRouter } from 'react-router-dom'

import ReadMore from '../../../components/UI/ReadMore'
import {
    Box,
    makeStyles,
    Paper,
    Tab,
    Tabs,
    Typography,
} from '@material-ui/core'

import { useTranslation } from 'react-i18next'
import ResourceCore from '../../../components/ResourceCore'
import MapPosition from '../../../components/MapPosition/MapPosition'
import ResourcePosts from '../../../components/Post/ResourcePosts'
import ResourceCoreMM from '../../../components/ResourceCoreMM'
import { connect } from 'react-redux'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const RewardMM = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const { id } = props
    const { reward } = props
    const [posts, setPosts] = useState([])

    return (
        <ResourceCoreMM
            type="reward"
            setPosts={setPosts}
            resourceId={id}
            resource={reward}
        >
            {reward && (
                <>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.desc')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <ReadMore
                            initialHeight={200}
                            contentEditor={reward.description}
                        />
                    </Paper>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.position')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <MapPosition position={reward.position} />
                    </Paper>
                    {/* <SimilarResources resource={progress} type="activity" /> */}

                    {/* Used in goals
                    Associated rewards */}
                </>
            )}
        </ResourceCoreMM>
    )
}

const mapStateToProps = (state, ownProps) => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        reward: state.reward[ownProps.id],
        friendData: state.friendData,
    }
}

export default withRouter(connect(mapStateToProps)(memo(RewardMM)))
