import React, { Component } from 'react'
import { withLeaflet } from 'react-leaflet'
import Locate from 'leaflet.locatecontrol'
import 'leaflet.locatecontrol/dist/L.Control.Locate.min.css'
import { withTranslation } from 'react-i18next'

class LocateControl extends Component {
    componentDidMount() {
        const { map } = this.props.leaflet

        const lc = new Locate({
            position: 'topleft',
            strings: {
                title: this.props.t('explore.myLocation'),
            },

            drawCircle: false,
            drawMarker: false,
            keepCurrentZoomLevel: true,
            setView: this.props.setView === 'no' ? false : 'untilPanOrZoom',
            flyTo: true,
        })
        lc.addTo(map)
        navigator.geolocation.getCurrentPosition(() => lc.start())
    }

    render() {
        return null
    }
}

export default withLeaflet(withTranslation()(LocateControl))
