export default props => {
    const { store } = props
    const resources = {
        user: {},
        account: {},
        progress: {},
        post: {},
        reward: {},
        activity: {},
        transactionData: {},
        friendData: {},
        progressData: {},
        postData: {},
        rewardData: {},
        activityData: {},
    }

    if (store.user && store.user._id) {
        resources.user[store.user._id] = store.user.__v
    }

    if (store.account._id) {
        resources.account[store.account._id] = store.account.__v
    }
    if (store.profile._id) {
        resources.account[store.profile._id] = store.profile.__v
    }
    if (store.progress._id) {
        resources.progress[store.progress._id] = store.progress.__v
    }
    if (store.activity._id) {
        resources.activity[store.activity._id] = store.activity.__v
    }
    if (store.activity._id) {
        resources.activity[store.activity._id] = store.activity.__v
    }

    const addData = key => {
        for (let item in store[key]) {
            if (typeof store[key][item] !== 'undefined') {
                if (store[key][item]._id) {
                    resources[key][store[key][item]._id] = store[key][item].__v
                }
            }
        }
    }

    addData('post')
    addData('friendData')
    addData('postData')
    addData('progressData')
    addData('rewardData')
    addData('activityData')
    addData('TransactionData')

    return resources
}
