import React, { memo, useState } from 'react'
import { withRouter } from 'react-router-dom'

import ReadMore from '../../../components/UI/ReadMore'
import {
    AppBar,
    Box,
    makeStyles,
    Paper,
    Tab,
    Tabs,
    Typography,
} from '@material-ui/core'

import Rewards from '../../../components/Rewards/Rewards'
import Activities from '../../../components/Activities/Activities'
import { useTranslation } from 'react-i18next'
import ResourceCore from '../../../components/ResourceCore'
import MapPosition from '../../../components/MapPosition/MapPosition'
import SimilarResources from '../../../components/SimilarResources'
import ResourcePosts from '../../../components/Post/ResourcePosts'
import ResourceCoreMM from '../../../components/ResourceCoreMM'
import { connect } from 'react-redux'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const GoalMM = props => {
    const { t } = useTranslation()
    const { id } = props
    const classes = useStyles()
    const { progress } = props

    return (
        <ResourceCoreMM type="goal" resourceId={id} resource={progress}>
            {progress && (
                <>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.desc')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <ReadMore
                            initialHeight={200}
                            contentEditor={progress.description}
                        />
                    </Paper>
                    <Activities progress={progress} />
                    <Rewards progress={progress} />

                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.position')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <MapPosition position={progress.position} />
                    </Paper>
                    {/* <SimilarResources resource={progress} type="activity" /> */}

                    {/* Used in goals
                    Associated rewards */}
                </>
            )}
        </ResourceCoreMM>
    )
}
const mapStateToProps = (state, ownProps) => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        progress: state.progress[ownProps.id],
        friendData: state.friendData,
    }
}

export default withRouter(connect(mapStateToProps)(memo(GoalMM)))
