import React, { useEffect, useState, useRef } from 'react'

import { Map, Marker, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'
import * as ELG from 'esri-leaflet-geocoder'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import { GestureHandling } from 'leaflet-gesture-handling'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'

import { useTranslation } from 'react-i18next'
import LocateControl from '../../containers/Explore/LocateControl'

import icon from 'leaflet/dist/images/marker-icon.png'
import iconShadow from 'leaflet/dist/images/marker-shadow.png'
import L from 'leaflet'
import 'react-leaflet-fullscreen/dist/styles.css'
import FullscreenControl from 'react-leaflet-fullscreen'

let DefaultIcon = L.icon({
    iconUrl: icon,
    shadowUrl: iconShadow,
    iconSize: [25, 41],
    iconAnchor: [10, 41],
})

L.Marker.prototype.options.icon = DefaultIcon

const MapPosition = props => {
    const { t } = useTranslation()
    const map = useRef(null)

    useEffect(() => {
        if (map && map.current && map.current.leafletElement) {
            new ELG.Geosearch({
                useMapBounds: false,
                placeholder: t('g.mapPlaceholder'),
                title: t('g.mapTitle'),
            }).addTo(map.current.leafletElement)
            new GestureHandling(map.current.leafletElement)
        }
    }, [])
    return props.position ? (
        <Map
            ref={map}
            style={{
                width: '100%',
                // maxWidth: '400px',
                height: '200px',
                position: 'relative',
                zIndex: 1,
            }}
            gestureHandling={true}
            center={[...props.position.coordinates].reverse()}
            zoom={11}
        >
            <TileLayer
                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            <LocateControl setView="no" />
            <Marker position={[...props.position.coordinates].reverse()} />
            <FullscreenControl position="topright" />
        </Map>
    ) : null
}

export default MapPosition
