import React, { useState } from 'react'
import { connect } from 'react-redux'

import { TransformWrapper } from 'react-zoom-pan-pinch'
import MindMap from './MindMap'
import * as actions from '../../store/actions'
import { throttle } from 'throttle-debounce'

const MindMapWrap = props => {
    const [scale, setScale] = useState(0.5)
    const [eventIsFromDraggable, setEventIsFromDraggable] = useState(false)
    const throttleFunc = throttle(10, () => props.addDragStep())
    return (
        <TransformWrapper
            defaultScale={0.5}
            defaultPositionX={200}
            defaultPositionY={100}
            options={{
                limitToBounds: false,
                minScale: 0.1,
                maxScale: 100,
                disabled: eventIsFromDraggable,
            }}
            pan={{
                velocity: false,
            }}
            wheel={{
                step: 300,
            }}
            onZoomChange={value => {
                setScale(value.scale)
                throttleFunc()
            }}
            onPanning={throttleFunc}
            onPanningStop={throttleFunc}
            onPinching={throttleFunc}
            onPinchingStop={throttleFunc}
        >
            <MindMap
                setEventIsFromDraggable={setEventIsFromDraggable}
                scale={scale}
            />
        </TransformWrapper>
    )
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        addDragStep: () => dispatch(actions.addDragStep()),
    }
}

export default connect(
    null,
    mapDispatchToProps
)(MindMapWrap)
