import React, { useState } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter } from 'react-router-dom'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'
import {
    Badge,
    Box,
    Button,
    CardActionArea,
    CardContent,
    CardMedia,
    MenuItem,
    Select,
    Typography,
} from '@material-ui/core'
import { getPH, placeholderImages } from '../MasonryCard'
import { useTranslation } from 'react-i18next'
import ActivityTable from '../../containers/Goal/ActivityTable'
import dayjs from 'dayjs'
import { spanStyle } from '../Post/Message'
import ConfirmAction from '../UI/ConfirmAction'
import UserButton from '../UserButton/UserButton'
dayjs().format()

const Transactions = props => {
    const { t } = useTranslation()
    // const [anchorEl, setAnchorEl] = useState(null)

    const [selectedTransaction, setSelectedTransaction] = useState('')
    const [deleteTransactionVisible, setDeleteTransactionVisible] = useState(
        false
    )

    const {
        transactionData,
        rewardData,
        account,
        transactions,
        setChosenResource,
    } = props

    // const handleClose = () => {
    //     setAnchorEl(null)
    // }
    // const handleClick = event => {
    //     setAnchorEl(event.currentTarget)
    // }

    const transactionObj =
        selectedTransaction && transactionData[selectedTransaction]
    const rewardObj = transactionObj && rewardData[transactionObj.reward]
    return (
        <>
            <HorizontalScrollList
                title={t('profile.receivedAndGivenRewards')}
                items={transactions
                    .map(item => transactionData[item])
                    .filter(item => item)
                    .filter(item => rewardData[item.reward])
                    .map(item => ({
                        ...item,
                        rewardObject: rewardData[item.reward],
                    }))
                    .sort((a, b) => {
                        if (
                            a.status === 'confirmed' &&
                            b.status !== 'confirmed'
                        )
                            return 1
                        if (
                            a.status !== 'confirmed' &&
                            b.status === 'confirmed'
                        )
                            return -1
                        if (a.status === 'process' && b.status !== 'process')
                            return 1
                        if (a.status !== 'process' && b.status === 'process')
                            return -1
                        // if (
                        //     a.status !== 'confirmed' &&
                        //     b.status !== 'confirmed'
                        // ) {
                        //     if (a.to === owner._id && b.to !== owner._id)
                        //         return 1
                        //     if (a.to !== owner._id && b.to === owner._id)
                        //         return -1
                        // }
                        return new Date(a.date).getTime() <
                            new Date(b.date).getTime()
                            ? 1
                            : -1
                    })}
                content={item => (
                    <CardContent
                        style={{
                            textAlign: 'center',
                            position: 'relative',
                            padding: '5px',
                        }}
                    >
                        <CardActionArea
                            onClick={() => setSelectedTransaction(item._id)}
                        >
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    item.rewardObject.images.length > 0
                                        ? item.rewardObject.images[0]
                                        : placeholderImages[getPH(item._id)]
                                }
                                title={item.rewardObject.name}
                            />
                            <CardContent style={{ padding: '5px' }}>
                                <Typography
                                    variant="body2"
                                    noWrap
                                    color={
                                        selectedTransaction === item._id
                                            ? 'primary'
                                            : undefined
                                    }
                                    style={{
                                        fontWeight:
                                            selectedTransaction === item._id
                                                ? 600
                                                : 'initial',
                                    }}
                                >
                                    {item.rewardObject.name}
                                </Typography>
                                <span style={spanStyle}>
                                    {dayjs(item.date).fromNow()}
                                </span>
                                <Box />
                            </CardContent>
                        </CardActionArea>
                        <Box
                            style={{
                                position: 'absolute',
                                top: '5px',
                                right: '5px',
                            }}
                        >
                            <Box
                                style={{
                                    width: '10px',
                                    height: '10px',
                                    borderRadius: '10px',
                                    background:
                                        item.status === 'confirmed'
                                            ? 'green'
                                            : item.status === 'cancelled'
                                            ? 'red'
                                            : 'blue',
                                }}
                            />
                        </Box>
                    </CardContent>
                )}
                bottomContent={
                    transactionObj && rewardObj ? (
                        <>
                            <Typography variant="h4" gutterBottom>
                                <span
                                    onClick={() =>
                                        setChosenResource(rewardObj._id)
                                    }
                                >
                                    {rewardObj.name || 'Selected activity'}
                                </span>
                            </Typography>
                            <Box style={{ ...spanStyle, margin: '10px' }}>
                                {dayjs(transactionObj.date).format(
                                    'DD/MM/YYYY'
                                )}
                            </Box>
                            <Typography gutterBottom>
                                {t('trans.from')}
                                <UserButton
                                    size={20}
                                    id={transactionObj.from}
                                />
                                {t('trans.to')}
                                <UserButton size={20} id={transactionObj.to} />
                                {t('trans.inGoal')}
                                <Link
                                    to={'/goals/' + transactionObj.progressId}
                                >
                                    <span style={{ fontWeight: 500 }}>
                                        {transactionObj.progress}
                                    </span>
                                </Link>
                                {t('trans.inActivity')}
                                <Link
                                    to={
                                        '/activities/' +
                                        transactionObj.activityId
                                    }
                                >
                                    <span style={{ fontWeight: 500 }}>
                                        {transactionObj.activity}
                                    </span>
                                </Link>
                            </Typography>
                            {t('trans.status')}

                            {account._id === transactionObj.to ||
                            account._id === transactionObj.from ? (
                                <>
                                    <Select
                                        value={transactionObj.status}
                                        onChange={e =>
                                            wsActions.confirmTransaction(
                                                account._id,
                                                transactionObj._id,
                                                e.target.value
                                            )
                                        }
                                        style={{ margin: '15px 0px' }}
                                    >
                                        <MenuItem value="confirmed">
                                            {t('trans.confirmed')}
                                        </MenuItem>
                                        <MenuItem value="pending">
                                            {t('trans.pending')}
                                        </MenuItem>
                                        <MenuItem value="cancelled">
                                            {t('trans.cancelled')}
                                        </MenuItem>
                                    </Select>
                                    <Box>
                                        <Button
                                            onClick={() =>
                                                setDeleteTransactionVisible(
                                                    true
                                                )
                                            }
                                        >
                                            {t('trans.deleteItem')}
                                        </Button>
                                    </Box>
                                </>
                            ) : (
                                transactionObj.status
                            )}
                        </>
                    ) : null
                }
            />

            <ConfirmAction
                open={deleteTransactionVisible}
                title={t('trans.confirmDelete')}
                cancel={() => setDeleteTransactionVisible(false)}
                confirm={() =>
                    wsActions.deleteTransaction(
                        account._id,
                        selectedTransaction
                    )
                }
            />
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
        transactionData: state.transactionData,
        rewardData: state.rewardData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Transactions)
)
