import React, { useEffect, useState } from 'react'
import { Box, Button, makeStyles, Typography } from '@material-ui/core'
import DialogFullscreen from '../UI/DialogFullscreen'
import TinyEditor from '../UI/TinyEditor'
import ImagePickerMulti from '../UI/ImagePickerMulti'
import { useTranslation } from 'react-i18next'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const EditPost = props => {
    const { t } = useTranslation()
    const classes = useStyles()

    const [images, setImages] = useState(props.images || [])

    const [messageText, setMessageText] = useState(props.content || '')

    useEffect(() => {
        setImages(props.images || [])
        setMessageText(props.content || '')
    }, [props.open])

    const handleSave = () => {
        props.save(images, messageText)
        handleCancel()
    }

    const handleCancel = () => {
        setImages([])
        setMessageText('')
        props.cancel()
    }

    return (
        <DialogFullscreen
            open={props.open}
            close={handleCancel}
            title={props.title}
            save={handleSave}
        >
            <Box
                style={{
                    padding: '0 16px',
                    maxWidth: '1200px',
                    margin: '0 auto',
                    width: '100%',
                }}
            >
                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    {t('g.desc')}
                </Typography>
                <Box>
                    <TinyEditor
                        value={messageText}
                        onEditorChange={value => setMessageText(value)}
                    />
                </Box>
                <Typography
                    className={classes.title}
                    color="textSecondary"
                    gutterBottom
                >
                    {t('g.images')}
                </Typography>
                <ImagePickerMulti
                    images={images}
                    onValueChange={value => setImages(value)}
                />
                <Box
                    style={{
                        textAlign: 'right',
                        marginTop: '50px',
                    }}
                >
                    <Button size="large" onClick={handleCancel}>
                        {t('g.cancel')}
                    </Button>
                    <Button
                        color="primary"
                        variant="contained"
                        size="large"
                        style={{
                            marginLeft: '10px',
                            marginRight: '50px',
                        }}
                        onClick={handleSave}
                    >
                        {t('g.save')}
                    </Button>
                </Box>
            </Box>
        </DialogFullscreen>
    )
}

export default EditPost
