const isDecedant = (structure, parentId, id) => {
    if (structure.items[parentId] && structure.items[id]) {
        if (structure.items[parentId].children.includes(id)) return true
        else {
            for (let child of structure.items[parentId].children) {
                const result = isDecedant(structure, child, id)
                if (result) {
                    return true
                }
            }
        }
    }
}

export const handleSetSelectedBox = props => {
    const {
        structure,
        setStructure,
        selectedBox,
        id,
        changeParentMode,
        setChangeParentMode,
        setSelectedBox,
    } = props
    if (changeParentMode) {
        const parentEl = structure.items[id]

        if (
            structure.items[selectedBox] &&
            parentEl &&
            selectedBox !== id &&
            !isDecedant(structure, selectedBox, id) &&
            structure.items[selectedBox].parent !== id &&
            ['profile', 'folder'].includes(parentEl.type)
        ) {
            setStructure({
                ...structure,
                items: {
                    ...structure.items,
                    [id]: {
                        ...structure.items[id],
                        children: [
                            ...structure.items[id].children,
                            selectedBox,
                        ],
                    },
                    [selectedBox]: {
                        ...structure.items[selectedBox],
                        parent: id,
                    },
                    [structure.items[selectedBox].parent]: {
                        ...structure.items[structure.items[selectedBox].parent],
                        children: structure.items[
                            structure.items[selectedBox].parent
                        ].children.filter(item => item !== selectedBox),
                    },
                },
            })
            setChangeParentMode('')
        }
    } else setSelectedBox(id)
}

export const addBlock = (props, type) => {
    const { structure, setStructure, selectedBox, selectedElement } = props
    if (selectedElement) {
        setStructure({
            items: {
                ...structure.items,
                [selectedBox]: {
                    ...selectedElement,
                    children: [
                        ...selectedElement.children,
                        'b' + structure.currentId,
                    ],
                },
                ['b' + structure.currentId]: {
                    type: 'folder',
                    folderType: type,
                    children: [],
                    parent: selectedBox,
                    innerChildren: type
                        ? ['b' + (structure.currentId + 1)]
                        : [],
                    sizePos: {
                        x: 200,
                        y: 0,
                        width: 320,
                        height: 200,
                    },
                },
                ...(type
                    ? {
                          ['b' + (structure.currentId + 1)]: {
                              type,
                              children: [],
                              value:
                                  type === 'text'
                                      ? 'Add text here'
                                      : 'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/image+addspire-02.jpg',
                              parent: 'b' + structure.currentId,
                              sizePos: {
                                  x: 5,
                                  y: 5,
                                  width: selectedElement.sizePos.width - 10,
                                  height: 100,
                              },
                          },
                      }
                    : {}),
            },
            currentId: structure.currentId + (type ? 2 : 1),
        })
    }
}

export const addElement = (props, type) => {
    const { structure, setStructure, selectedBox, selectedElement } = props
    if (selectedElement) {
        setStructure({
            currentId: structure.currentId + 1,
            items: {
                ...structure.items,
                [selectedBox]: {
                    ...selectedElement,
                    innerChildren: [
                        ...selectedElement.innerChildren,
                        'b' + structure.currentId,
                    ],
                },
                ['b' + structure.currentId]: {
                    type,
                    children: [],
                    value:
                        type === 'text'
                            ? 'Add text here'
                            : 'https://websiter.s3.us-east-2.amazonaws.com/!wishlist/image+addspire-02.jpg',
                    parent: selectedBox,
                    sizePos: {
                        x: 5,
                        y: 5,
                        width: selectedElement.sizePos.width - 10,
                        height: 100,
                    },
                },
            },
        })
    }
}

export const changeParent = props => {
    const {
        selectedBox,
        changeParentMode,
        setChangeParentMode,
        selectedElement,
    } = props
    if (changeParentMode) {
        setChangeParentMode('')
    } else {
        if (selectedElement) {
            setChangeParentMode(selectedBox)
        }
    }
}
export const collapse = props => {
    const { selectedBox, structure, setStructure } = props
    setStructure({
        ...structure,
        items: {
            ...structure.items,
            [selectedBox]: {
                ...structure.items[selectedBox],
                collapsed: !structure.items[selectedBox].collapsed,
            },
        },
    })
}

export const deleteElement = props => {
    const {
        selectedBox,
        structure,
        setStructure,
        selectedIndex,
        setSelectedIndex,
        editingFolder,
    } = props
    if (!editingFolder) {
        const newChildren = {}
        for (let child of structure.items[selectedBox].children) {
            newChildren[child] = {
                ...structure.items[child],
                parent: structure.items[selectedBox].parent,
            }
        }
        const newItems = {
            ...structure.items,
            [structure.items[selectedBox].parent]: {
                ...structure.items[structure.items[selectedBox].parent],
                children: [
                    ...structure.items[
                        structure.items[selectedBox].parent
                    ].children.filter(item => item !== selectedBox),
                    ...structure.items[selectedBox].children,
                ],
            },
            ...newChildren,
        }
        delete newItems[selectedBox]
        setStructure({
            ...structure,
            items: newItems,
        })
    } else {
        let { [selectedIndex]: el, ...newItems } = structure.items
        if (el) {
            setStructure({
                ...structure,
                items: {
                    ...newItems,
                    [selectedBox]: {
                        ...newItems[selectedBox],
                        innerChildren: newItems[
                            selectedBox
                        ].innerChildren.filter(item => item !== selectedIndex),
                    },
                },
            })
        }
    }
    setSelectedIndex('')
}

export const dragHandler = (id, data, structure, setStructure) => {
    setStructure({
        ...structure,
        items: {
            ...structure.items,
            [id]: {
                ...structure.items[id],
                sizePos: {
                    ...structure.items[id].sizePos,
                    x: data.x,
                    y: data.y,
                },
            },
        },
    })
}

export const resizeHandler = (id, elRef, position, structure, setStructure) => {
    setStructure({
        ...structure,
        items: {
            ...structure.items,
            [id]: {
                ...structure.items[id],
                sizePos: {
                    ...structure.items[id].sizePos,
                    x: position.x,
                    y: position.y,
                    width: parseInt(elRef.style.width),
                    height: parseInt(elRef.style.height),
                },
            },
        },
    })
}

export const saveValue = (id, value, structure, setStructure) => {
    if (structure.items[id])
        setStructure({
            ...structure,
            items: {
                ...structure.items,
                [id]: {
                    ...structure.items[id],
                    value,
                },
            },
        })
}

export const addUnsorted = (structure, setStructure, profile) => {
    const unsortedItems = {}
    const unsortedIds = []
    let newCurrentId = structure.currentId
    for (let item of profile.progresses) {
        if (!structure.items[item]) {
            unsortedIds.push(item)
            unsortedItems[item] = {
                type: 'goal',
                id: item,
                sizePos: { x: -800, y: 0, width: 500, height: 500 },
                children: [],
                parent: 'b00',
            }
        }
    }
    for (let item of profile.rewards) {
        if (!structure.items[item]) {
            unsortedIds.push(item)
            unsortedItems[item] = {
                type: 'reward',
                id: item,
                sizePos: { x: -800, y: 0, width: 500, height: 500 },
                children: [],
                parent: 'b00',
            }
        }
    }
    for (let item of profile.activities) {
        if (!structure.items[item]) {
            unsortedIds.push(item)
            unsortedItems[item] = {
                type: 'activity',
                id: item,
                sizePos: { x: -800, y: 0, width: 500, height: 500 },
                children: [],
                parent: 'b00',
            }
        }
    }
    if (unsortedIds.length > 0) {
        setStructure(
            {
                ...structure,
                currentId: newCurrentId,
                items: {
                    ...structure.items,
                    ...unsortedItems,
                    b00: {
                        ...structure.items.b00,
                        children: [
                            ...structure.items.b00.children,
                            ...unsortedIds,
                        ],
                    },
                },
            },
            true
        )
    }
}
