import React from 'react'
import { withRouter, Link } from 'react-router-dom'
import { connect } from 'react-redux'

import Button from 'antd/es/button'
import Avatar from 'antd/es/avatar'
import getAvatar from '../../utils/getAvatar'
import { Typography } from '@material-ui/core'
import Badge from 'antd/es/badge'

const UserButton = props => {
    const { account, profile, id, friendSearchResult, onlineUsers } = props
    const friendData = props.friendDataExp || props.friendData
    const getUserData = id =>
        id === account._id
            ? account
            : id === profile._id
            ? profile
            : id === friendSearchResult._id
            ? friendSearchResult
            : friendData[id]
    const userName = id => {
        const text = getUserData(id) ? getUserData(id).name : id
        return props.typography ? <Typography noWrap>{text}</Typography> : text
    }
    const userAvatar = id => {
        const ava = getUserData(id) ? (
            <Avatar
                src={getAvatar(id, getUserData(id).image)}
                size={props.size || (props.noClick ? 20 : 40)}
                style={props.group || props.noName ? {} : { margin: ' 0 5px' }}
            />
        ) : null
        return getUserData(id) ? (
            onlineUsers.includes(id) ? (
                <span key={id}>
                    <Badge color="green">{ava}</Badge>
                </span>
            ) : (
                <span key={id}>{ava}</span>
            )
        ) : null
    }

    return props.onlyName ? (
        userName(id)
    ) : props.group ? (
        <span onClick={e => e.stopPropagation()}>
            <Avatar.Group style={{ margin: ' 0 5px' }}>
                {props.ids.map(id => userAvatar(id))}
            </Avatar.Group>
        </span>
    ) : props.noClick ? (
        <>
            {!props.noAva && userAvatar(id)}
            {!props.noName && userName(id)}
        </>
    ) : props.span ? (
        <Link
            style={{
                cursor: 'pointer',
                fontWeight: 'bold',
                ...props.style,
                // display: 'flex',
            }}
            onClick={e => {
                e.stopPropagation()
            }}
            to={'/profile/' + (id || props.id)}
        >
            {!props.noAva && userAvatar(id)}
            {!props.noName && userName(id)}
        </Link>
    ) : props.inline ? (
        <Link
            style={{
                cursor: 'pointer',
                fontWeight: 'bold',
                ...props.style,
            }}
            onClick={e => {
                e.stopPropagation()
            }}
            to={'/profile/' + (id || props.id)}
        >
            {!props.noAva && userAvatar(id)}
            {!props.noName && userName(id)}
        </Link>
    ) : (
        <Link
            onClick={e => {
                e.stopPropagation()
            }}
            to={'/profile/' + (id || props.id)}
        >
            <Button
                type="link"
                style={{ padding: '0px 5px', height: 'auto', ...props.style }}
            >
                {!props.noAva && userAvatar(id)}

                {!props.noName && userName(id)}
            </Button>
        </Link>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        profile: state.profile,
        friendData: state.friendData,
        friendSearchResult: state.friendSearchResult,
        onlineUsers: state.onlineUsers,
    }
}

export default withRouter(connect(mapStateToProps)(UserButton))
