import React, { useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import UserButton from '../../components/UserButton/UserButton'
import Comment from 'antd/es/comment'
import Divider from 'antd/es/divider'
import dayjs from 'dayjs'
import { Card, CardContent } from '@material-ui/core'
import * as wsActions from '../../websocketActions'
import { useTranslation } from 'react-i18next'

dayjs().format()

const DashboardEvents = props => {
    const { t } = useTranslation()
    const { lastNotifications } = props

    const getStageName = stage => {
        let stageDate
        if (stage.day) {
            stageDate = dayjs()
                .year(stage.year)
                .dayOfYear(stage.day)
            return stageDate.format('dddd D MMM YYYY')
        } else if (stage.week) {
            stageDate = dayjs()
                .year(stage.year)
                .week(stage.week)
            return (
                stageDate
                    .startOf('week')
                    .format('wo [' + t('g.week') + '] D MMM YYYY - ') +
                stageDate.endOf('week').format('D MMM YYYY')
            )
        } else if (stage.month) {
            stageDate = dayjs()
                .year(stage.year)
                .month(stage.month)
            return stageDate.format('MMMM YYYY')
        } else if (stage.year) {
            stageDate = dayjs().year(stage.year)
            return stageDate.format('YYYY')
        }
    }

    useEffect(() => {
        wsActions.setLastSeenNot(
            props.account._id,
            lastNotifications.reduce(function(p, v) {
                const vN = parseInt(v.notId)
                if (isNaN(vN)) return p
                return p > vN ? p : vN
            }, 0)
        )
    }, [lastNotifications])
    return (
        <Card>
            <CardContent>
                {lastNotifications.map((item, index) => {
                    let text = null,
                        stageName

                    switch (item.code) {
                        case 'change name':
                            text = (
                                <span>
                                    {' '}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.changedName')}
                                </span>
                            )
                            break
                        case 'delete account':
                            text = (
                                <span>
                                    {item.user}
                                    {t('dash.deletedAccount')}
                                </span>
                            )
                            break
                        case 'friend request':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.wantsToBecomeFriends')}
                                </span>
                            )
                            break
                        case 'friend add':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.and')}

                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.friend}
                                    />
                                    {t('dash.becameFriends')}
                                </span>
                            )
                            break
                        case 'unfriend':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.and')}

                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.friend}
                                    />
                                    {t('dash.stoppedFriends')}
                                    {' stopped being friends.'}
                                </span>
                            )
                            break
                        case 'add wishlist item':
                        case 'edit wishlist item':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {item.code === 'add wishlist item'
                                        ? t('dash.addedToWish')
                                        : t('dash.editedWish')}{' '}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/wishlist/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'delete wishlist item':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.deleted')}
                                    {item.details.itemName} {t('dash.fromWish')}
                                </span>
                            )
                            break
                        case 'add goal':
                        case 'edit goal':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {item.code === 'add goal'
                                        ? t('dash.addedGoal')
                                        : t('dash.editedGoalND')}{' '}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName}
                                    </span>
                                    {'.'}
                                </span>
                            )
                            break
                        case 'delete goal':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.deleted')}
                                    {item.details.itemName}{' '}
                                    {t('dash.fromGoals')}
                                </span>
                            )
                            break

                        case 'cancel transaction':
                        case 'confirm transaction':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {item.code === 'cancel transaction'
                                        ? t('dash.cancelled')
                                        : t('dash.confirmed')}{' '}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/shop/${item.user}/${
                                                    item.details.itemId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.itemName} ($
                                        {item.details.price})
                                    </span>
                                    {t('dash.by')}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.buyer}
                                    />
                                    .
                                </span>
                            )
                            break
                        case 'start progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.startedParticipating')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${item.details.itemId}`
                                            )
                                        }
                                    >
                                        {item.details.itemName}
                                    </span>
                                    {t('dash.as')}
                                    {item.details.as}.
                                </span>
                            )
                            break
                        case 'stage approve':
                        case 'stage progress':
                        case 'stage fail':
                        case 'stage dismiss':
                            stageName = getStageName(item.details)
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {item.code === 'stage approve'
                                        ? t('dash.approved')
                                        : item.code === 'stage progress'
                                        ? t('dash.putToProgress')
                                        : item.code === 'stage fail'
                                        ? t('dash.markedFailed')
                                        : t('dash.dismissed')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    {stageName && (
                                        <>
                                            {t('dash.for')}
                                            <span
                                                style={{
                                                    fontWeight: 'bold',
                                                }}
                                            >
                                                {stageName}
                                            </span>
                                        </>
                                    )}
                                    .
                                </span>
                            )
                            break

                        case 'stage complete':
                            text = (
                                <span>
                                    {t('dash.goal')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    {stageName && (
                                        <>
                                            {t('dash.for')}
                                            <span
                                                style={{
                                                    fontWeight: 'bold',
                                                }}
                                            >
                                                {stageName}
                                            </span>
                                        </>
                                    )}

                                    {t('dash.wasApprovedAndFinished')}
                                </span>
                            )
                            break

                        case 'leave progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.leftGoal')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'edit progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {t('dash.editedGoalND')}{' '}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    {item.details.addedAccounts.length > 0 ? (
                                        <>
                                            {t('dash.andAdded')}
                                            {item.details.addedAccounts.map(
                                                ac => (
                                                    <UserButton
                                                        span={true}
                                                        noAva={true}
                                                        id={ac}
                                                    />
                                                )
                                            )}{' '}
                                        </>
                                    ) : (
                                        ''
                                    )}
                                    {item.details.droppedAccounts.length > 0 ? (
                                        <>
                                            {t('dash.andRemoved')}
                                            {item.details.droppedAccounts.map(
                                                ac => (
                                                    <UserButton
                                                        span={true}
                                                        noAva={true}
                                                        id={ac}
                                                    />
                                                )
                                            )}
                                            {'.'}
                                        </>
                                    ) : (
                                        '.'
                                    )}
                                </span>
                            )
                            break
                        case 'remove from progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.removed')}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.account}
                                    />
                                    {t('dash.fromGoal')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'add to progress':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />
                                    {t('dash.added')}
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.details.account}
                                    />
                                    {t('dash.toGoal')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'add reward':
                        case 'add activity':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {t('dash.added')} {item.name}{' '}
                                    {item.code === 'add reward' && (
                                        <>
                                            {t('dash.asRewardFor')}
                                            {!item.details.for ||
                                            item.details.for.length === 0
                                                ? t('dash.all')
                                                : item.details.for.map(
                                                      account => (
                                                          <>
                                                              <UserButton
                                                                  noAva={true}
                                                                  span={true}
                                                                  id={account}
                                                              />
                                                              ,
                                                          </>
                                                      )
                                                  )}
                                        </>
                                    )}
                                    {t('dash.inGoal')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break
                        case 'delete reward':
                        case 'delete activity':
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {t('dash.removed')}
                                    {item.name}
                                    {item.code === 'delete reward' && (
                                        <>
                                            {t('dash.asRewardFor')}
                                            {!item.details.for ||
                                            item.details.for.length === 0
                                                ? t('dash.all')
                                                : item.details.for.map(
                                                      account => (
                                                          <>
                                                              <UserButton
                                                                  span={true}
                                                                  noAva={true}
                                                                  id={account}
                                                              />
                                                              ,
                                                          </>
                                                      )
                                                  )}
                                        </>
                                    )}
                                    {t('dash.inGoal')}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/goals/${
                                                    item.details.progressId
                                                }`
                                            )
                                        }
                                    >
                                        {item.details.progressName}
                                    </span>
                                    .
                                </span>
                            )
                            break

                        case 'shared':
                            let action = () =>
                                props.history.push(
                                    item.details.url.split(
                                        'https://addspire.com'
                                    ).length > 0
                                        ? item.details.url.split(
                                              'https://addspire.com'
                                          )[1]
                                        : item.details.url
                                )
                            let title = t('dash.somethingOnAddspire')

                            if (
                                item.details.progressName &&
                                item.details.progressId
                            ) {
                                action = () =>
                                    props.history.push(
                                        `/goals/${item.details.progressId}`
                                    )
                                title = item.details.progressName
                            } else if (
                                item.details.profileId &&
                                item.details.profileName
                            ) {
                                action = () =>
                                    props.history.push(
                                        `/profile/${item.details.profileId}`
                                    )
                                title = item.details.profileName
                            } else if (
                                item.details.rewardId &&
                                item.details.rewardName
                            ) {
                                action = () =>
                                    props.history.push(
                                        `/rewards/${item.details.rewardId}`
                                    )
                                title = item.details.rewardName
                            } else if (
                                item.details.activityId &&
                                item.details.activityName
                            ) {
                                action = () =>
                                    props.history.push(
                                        `/activities/${item.details.activityId}`
                                    )
                                title = item.details.activityName
                            }
                            text = (
                                <span>
                                    <UserButton
                                        span={true}
                                        noAva={true}
                                        id={item.user}
                                    />{' '}
                                    {t('dash.sharedAndInvitedYouTo')}{' '}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                            cursor: 'pointer',
                                        }}
                                        onClick={() => action()}
                                    >
                                        {title}
                                    </span>
                                </span>
                            )
                            break
                        default:
                            text = t('dash.newEvent')
                            break
                    }
                    return (
                        <div key={index}>
                            {index !== 0 && (
                                <Divider style={{ margin: '5px 0px' }} />
                            )}
                            <Comment
                                avatar={
                                    <UserButton
                                        noName={true}
                                        id={item.user}
                                        span={true}
                                    />
                                }
                                content={text}
                            />
                        </div>
                    )

                    // <Card>{text}</Card>
                })}{' '}
            </CardContent>
        </Card>
    )
}
const mapStateToProps = state => {
    return {
        lastNotifications: state.lastNotifications,
    }
}

export default withRouter(connect(mapStateToProps)(DashboardEvents))
