import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const sendMessage = (
    accountId,
    postId,
    messageValue,
    image,
    replyToMessage,
    editedMessage
) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'sendMessage',
                messageValue,
                image,
                postId,
                accountId,
                replyToMessage,
                editedMessage,
            })
        )
    }
}

