import React from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import AddPost from './AddPost'
import Post from './Post'
import { useTranslation } from 'react-i18next'
import { makeStyles, Typography } from '@material-ui/core'
const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))
const ResourcePosts = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const { resource, account, posts, friendData, type } = props

    return (
        <>
            {account._id && (
                <>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.addPost')}
                    </Typography>
                    <AddPost
                        parent={resource._id}
                        account={account}
                        parentType={type}
                        // onSend={() => setEditingMessage('')}
                    />
                </>
            )}
            <Typography
                className={classes.title}
                color="textSecondary"
                gutterBottom
            >
                {t('g.postsAndComments')}
            </Typography>
            {posts &&
                [...resource.posts]
                    .sort((a, b) =>
                        props.history.location.pathname.indexOf(a) > -1
                            ? 1
                            : props.history.location.pathname.indexOf(b) > -1
                            ? -1
                            : 0
                    )
                    .map(postId => {
                        if (posts[postId]) {
                            const post = posts[postId]
                            return (
                                <div
                                    key={postId}
                                    style={{ paddingTop: '10px' }}
                                >
                                    <Post
                                        friendData={friendData}
                                        account={account}
                                        post={post}
                                    />
                                </div>
                            )
                        } else return null
                    })
                    .reverse()}
        </>
    )
}
const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
    }
}

export default withRouter(connect(mapStateToProps)(ResourcePosts))
