import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'
import Cookies from 'universal-cookie'
import classes from './LoginModal.module.css'
import {
    FacebookLoginButton,
    GithubLoginButton,
    GoogleLoginButton,
    InstagramLoginButton,
} from 'react-social-login-buttons'
import {
    Button,
    Checkbox,
    Dialog,
    DialogActions,
    DialogContent,
    DialogContentText,
    DialogTitle,
    Divider,
    FormControlLabel,
    Slide,
} from '@material-ui/core'
import { useTranslation } from 'react-i18next'

const cookies = new Cookies()
const TransitionComponent = props => <Slide direction="up" {...props} />

const LoginModal = props => {
    const { t } = useTranslation()
    const [state, setState] = useState(false)
    const [cookieConsent, setCookieConsent] = useState(false)

    useEffect(() => {
        checkCookieConsent()
    })

    let remeberMeParam = '?rememberme=false'
    if (state) {
        remeberMeParam = '?rememberme=true'
    }

    const checkCookieConsent = () => {
        const cookiesConsent = cookies.get('cookies_consent')
        if (cookiesConsent) {
            setCookieConsent(true)
        }
    }

    const agreeWithCookies = () => {
        cookies.set('cookies_consent', 'true', {
            path: '/',
            maxAge: 31530000,
        })
        checkCookieConsent()
    }
    const basicURL =
        process.env.NODE_ENV !== 'development'
            ? 'https://addspire.com/api/auth/'
            : 'http://my.websiter.test:5000/api/auth/'
    return props.loginVisible ? (
        !cookieConsent ? (
            <Dialog open={!cookieConsent}>
                <DialogTitle>{'Cookies consent'}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {t('login.cookieText')}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button
                        onClick={() => {
                            props.setLoginVisible(false)
                            setCookieConsent(false)
                        }}
                    >
                        {t('g.disagree')}
                    </Button>
                    <Button
                        onClick={agreeWithCookies}
                        color="primary"
                        autoFocus
                    >
                        {t('g.agree')}
                    </Button>
                </DialogActions>
            </Dialog>
        ) : (
            <Dialog
                fullScreen
                open={props.loginVisible}
                onClose={() => props.setLoginVisible(false)}
                TransitionComponent={TransitionComponent}
                scroll="paper"
            >
                <div className="contentDiv">
                    <div
                        className={classes.Container}
                        style={{
                            maxWidth: '500px',
                            margin: '0 auto',
                        }}
                    >
                        <h1
                            style={{
                                fontWeight: 'bold',
                                lineHeight: '1.3em',
                                marginBottom: 0,
                                fontSize: '2.5em',
                            }}
                        >
                            {t('login.welcome')}
                        </h1>
                        <div
                            className={classes.socialButtons}
                            style={{ marginBottom: '30px' }}
                        >
                            <FacebookLoginButton
                                onClick={() =>
                                    (window.location =
                                        basicURL +
                                        `facebook/start${remeberMeParam}`)
                                }
                            />
                            <GoogleLoginButton
                                onClick={() =>
                                    (window.location =
                                        basicURL +
                                        `google/start${remeberMeParam}`)
                                }
                            />
                            <InstagramLoginButton
                                onClick={() =>
                                    (window.location =
                                        basicURL +
                                        `instagram/start${remeberMeParam}`)
                                }
                            />
                            <GithubLoginButton
                                onClick={() =>
                                    (window.location =
                                        basicURL +
                                        `github/start${remeberMeParam}`)
                                }
                            />
                        </div>
                        <FormControlLabel
                            control={
                                <Checkbox
                                    checked={state}
                                    onChange={() => setState(!state)}
                                    color="primary"
                                />
                            }
                            label={t('login.rememberMe')}
                        />

                        <Divider style={{ margin: '20px' }} />

                        <Button
                            onClick={() => props.setLoginVisible(false)}
                            color="secondary"
                        >
                            {t('login.loginLater')}
                        </Button>
                    </div>
                    <div className={classes.Background} />
                </div>
            </Dialog>
        )
    ) : null
}

export default withRouter(LoginModal)
