import React from 'react'

import Form from 'antd/es/form'

import Select from 'antd/es/select'
import 'leaflet/dist/leaflet.css'
import * as wsActions from '../../websocketActions'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import {
    AppBar,
    createStyles,
    IconButton,
    makeStyles,
    Theme,
    Toolbar,
    Typography,
    Button,
    Box,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
import { useTranslation } from 'react-i18next'
import ProfileRewards from '../ProfileRewards/ProfileRewards'
import ProfileActivities from '../ProfileActivities/ProfileActivities'
const { Option } = Select

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            marginLeft: theme.spacing(1),
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'fixed',
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const EditProgressReward = props => {
    const { t } = useTranslation()
    const [form] = Form.useForm()

    const { account, rewardId, progress } = props

    const onFinish = value => {
        wsActions.saveRewardInProgress(
            account._id,
            progress._id,
            rewardId,
            value
        )

        props.cancel()
    }

    const classes = useStyles()
    let initialValue = {
        rewards: [],
        activities: [],
        repeat: 'time',
    }
    if (rewardId !== 'new') {
        const rewardObj = progress.rewards.find(
            item => item.rewardId === rewardId
        )
        initialValue = {
            rewards: [rewardObj.reward],
            activities: rewardObj.activities,
            repeat: rewardObj.repeat,
        }
    }

    return (
        <>
            <AppBar className={classes.appBar} color="primary">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={props.cancel}
                        aria-label="close"
                    >
                        <Close />
                    </IconButton>
                    <Typography
                        variant="h6"
                        className={classes.title}
                        color="inherit"
                    >
                        {t(
                            'progress.' +
                                (rewardId !== 'new' ? 'editGoal' : 'addNewGoal')
                        )}
                    </Typography>
                    <Button
                        autoFocus
                        color="inherit"
                        onClick={() => form.submit()}
                    >
                        {t('g.save')}
                    </Button>
                </Toolbar>
            </AppBar>
            <Toolbar />

            <Form
                form={form}
                style={{
                    maxWidth: '1200px',
                    margin: '0 auto',
                    padding: '15px',
                    width: '100%',
                }}
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                initialValues={initialValue}
            >
                <Form.Item name="rewards">
                    <ProfileRewards owner={account} isSelect multiple />
                </Form.Item>

                <Form.Item name="activities">
                    <ProfileActivities
                        owner={account}
                        isSelect
                        activities={progress.activities}
                    />
                </Form.Item>
                <Form.Item name="repeat">
                    <Select getPopupContainer={trigger => trigger.parentNode}>
                        <Option value="once">{t('g.once')}</Option>
                        <Option value="time">{t('g.everyTime')}</Option>
                        <Option value="manual">{t('g.manual')}</Option>
                        {/* 
                        <Option value="day">{t('g.everyDay')}</Option>
                        <Option value="week">{t('g.everyWeek')}</Option>
                        <Option value="month">{t('g.everyMonth')}</Option>
                        <Option value="year">{t('g.everyYear')}</Option> */}
                    </Select>
                </Form.Item>

                <div style={{ textAlign: 'right', marginTop: '50px' }}>
                    <Button size="large" onClick={() => props.cancel(false)}>
                        {t('g.cancel')}
                    </Button>
                    <Button
                        color="primary"
                        variant="contained"
                        type="submit"
                        size="large"
                        style={{
                            marginLeft: '10px',
                            marginRight: '50px',
                        }}
                    >
                        {t('g.save')}
                    </Button>
                </div>
            </Form>
        </>
    )
}
export default EditProgressReward
