import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import {
    Box,
    CardActionArea,
    CardContent,
    CardMedia,
    Dialog,
    DialogActions,
    DialogContent,
    IconButton,
    Slide,
    Typography,
} from '@material-ui/core'
import { getPH, placeholderImages } from '../MasonryCard'
import Reward from '../../containers/Reward/Reward'
import { Close } from '@material-ui/icons'
import { useTranslation } from 'react-i18next'
import EditResource from '../EditResource/EditResource'

const TransitionComponent = props => <Slide direction="up" {...props} />

const ProfileRewards = props => {
    const { t } = useTranslation()
    const [addRewardOpen, setAddRewardOpen] = useState(false)
    const [rewardPreview, setRewardPreview] = useState('')

    const {
        account,
        owner,
        rewardData,
        wish,
        additionalButton,
        isSelect,
        value,
        onChange,
        multiple,
        setChosenResource,
    } = props
    const isOwner = account && account._id && account._id === owner._id

    const handleSelect = id => {
        if (multiple)
            if (value.includes(id)) {
                onChange(value.filter(item => item !== id))
            } else {
                onChange([...value, id])
            }
        else onChange([id])
    }

    return (
        <>
            <HorizontalScrollList
                title={
                    isSelect
                        ? 'I want to give these rewards:'
                        : wish
                        ? t('g.wishlist')
                        : t('g.rewards')
                }
                items={owner.rewards
                    .map(item => rewardData[item])
                    .filter(item => item)}
                content={item => {
                    const isSelected = isSelect && value.includes(item._id)
                    const content = (
                        <>
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    item.images.length > 0
                                        ? item.images[0]
                                        : placeholderImages[getPH(item._id)]
                                }
                                title={item.name}
                            />
                            <CardContent style={{ padding: '5px' }}>
                                <Typography
                                    variant="body2"
                                    noWrap
                                    color={isSelected ? 'primary' : undefined}
                                    style={{
                                        fontWeight: isSelected
                                            ? 600
                                            : 'initial',
                                    }}
                                >
                                    {item.name}
                                </Typography>
                            </CardContent>
                        </>
                    )
                    return (
                        <CardContent
                            style={{
                                textAlign: 'center',
                                position: 'relative',
                                padding: '5px',
                            }}
                        >
                            <CardActionArea
                                onClick={() =>
                                    isSelect && handleSelect(item._id)
                                }
                            >
                                {isSelect ? (
                                    content
                                ) : props.openInDialog ? (
                                    <Box
                                        onClick={() =>
                                            setRewardPreview(item._id)
                                        }
                                    >
                                        {content}
                                    </Box>
                                ) : (
                                    <span
                                        onClick={() =>
                                            setChosenResource(item._id)
                                        }
                                    >
                                        {content}
                                    </span>
                                )}
                            </CardActionArea>
                            {additionalButton && additionalButton(item)}
                        </CardContent>
                    )
                }}
                withAdd={isOwner}
                addTitle={t('g.addReward')}
                onAdd={() => setAddRewardOpen(true)}
            />
            {isOwner && (
                <Dialog
                    fullScreen
                    open={addRewardOpen}
                    onClose={() => setAddRewardOpen(false)}
                    TransitionComponent={TransitionComponent}
                    scroll="paper"
                >
                    <EditResource
                        type="reward"
                        account={account}
                        friendData={props.friendData}
                        cancel={() => setAddRewardOpen(false)}
                    />
                </Dialog>
            )}
            {props.openInDialog && (
                <Dialog
                    open={Boolean(rewardPreview)}
                    onClose={() => setRewardPreview('')}
                    fullWidth
                    maxWidth="md"
                    scroll="paper"
                >
                    <DialogActions>
                        <IconButton onClick={() => setRewardPreview('')}>
                            <Close />
                        </IconButton>
                    </DialogActions>
                    <DialogContent>
                        {rewardPreview && <Reward id={rewardPreview} />}
                    </DialogContent>
                </Dialog>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
        rewardData: state.rewardData,
    }
}

export default withRouter(connect(mapStateToProps)(ProfileRewards))
