import { webSocket } from '../ConnectWebsocket'

export default (type, ids, state) => {
    const notRequestedIds = ids.filter(id => !state[type].includes(id))
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'requestResource',
                ids: notRequestedIds,
                type,
            })
        )
    }
}
