import React, { useState } from 'react'
import { connect } from 'react-redux'
import { Link, withRouter } from 'react-router-dom'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'
import {
    Box,
    Button,
    CardActionArea,
    CardContent,
    CardMedia,
    Dialog,
    DialogActions,
    DialogTitle,
    Slide,
    Typography,
} from '@material-ui/core'
import { getPH, placeholderImages } from '../MasonryCard'
import ProfileActivities from '../ProfileActivities/ProfileActivities'
import DialogFullscreen from '../UI/DialogFullscreen'
import { useTranslation } from 'react-i18next'
import UserButton from '../UserButton/UserButton'
import EditProgressReward from './EditProgressReward'
import ConfirmAction from '../UI/ConfirmAction'
const TransitionComponent = props => <Slide direction="up" {...props} />

const Rewards = props => {
    const { t } = useTranslation()
    // const [anchorEl, setAnchorEl] = useState(null)
    const [editRewardOpen, setEditRewardOpen] = useState('')
    const [selectedReward, setSelectedReward] = useState('')
    const [deleteRewardVisible, setDeleteRewardVisible] = useState('')

    const { account, progress, rewardData, activityData } = props

    // const handleClose = () => {
    //     setAnchorEl(null)
    // }
    // const handleClick = event => {
    //     setAnchorEl(event.currentTarget)
    // }
    const rewardObj =
        progress &&
        progress.rewards.find(item => item.rewardId === selectedReward)
    const selectedRewardObj =
        progress && rewardObj && rewardData[rewardObj.reward]

    return (
        <>
            <HorizontalScrollList
                title={t('g.rewards')}
                items={progress.rewards.filter(item => rewardData[item.reward])}
                content={item => (
                    <CardContent
                        style={{
                            textAlign: 'center',
                            position: 'relative',
                            padding: '5px',
                        }}
                    >
                        <CardActionArea
                            onClick={() => setSelectedReward(item.rewardId)}
                        >
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    rewardData[item.reward].images.length > 0
                                        ? rewardData[item.reward].images[0]
                                        : placeholderImages[
                                              getPH(item.rewardId)
                                          ]
                                }
                                title={rewardData[item.reward].name}
                            />
                            <CardContent style={{ padding: '5px' }}>
                                <Typography
                                    variant="body2"
                                    noWrap
                                    color={
                                        selectedReward === item.rewardId
                                            ? 'primary'
                                            : undefined
                                    }
                                    style={{
                                        fontWeight:
                                            selectedReward === item.rewardId
                                                ? 600
                                                : 'initial',
                                    }}
                                >
                                    {rewardData[item.reward].name}
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </CardContent>
                )}
                withAdd={Boolean(account._id)}
                addTitle={t('g.addReward')}
                onAdd={() => setEditRewardOpen('new')}
                bottomContent={
                    selectedReward && selectedRewardObj ? (
                        <>
                            <Typography gutterBottom>
                                {selectedRewardObj.name}
                            </Typography>
                            <Button
                                onClick={() =>
                                    props.history.push(
                                        '/rewards/' + selectedRewardObj._id
                                    )
                                }
                                color="primary"
                            >
                                {t('g.goToReward')}
                            </Button>
                            {selectedRewardObj &&
                                account._id &&
                                rewardObj.owner === account._id && (
                                    <>
                                        <Box>
                                            <Button
                                                onClick={() =>
                                                    wsActions.sendReward(
                                                        account._id,
                                                        progress._id,
                                                        selectedReward
                                                    )
                                                }
                                                color="primary"
                                            >
                                                {t('g.sendRewardManually')}
                                            </Button>
                                        </Box>
                                        <Button
                                            onClick={() =>
                                                setEditRewardOpen(
                                                    selectedReward
                                                )
                                            }
                                        >
                                            {t('g.editReward')}
                                        </Button>
                                        <Button
                                            onClick={() =>
                                                setDeleteRewardVisible(
                                                    selectedReward
                                                )
                                            }
                                        >
                                            {t('g.removeReward')}
                                        </Button>
                                    </>
                                )}
                            <Box style={{ marginTop: '10px' }}>
                                <UserButton id={rewardObj.owner} />
                                <span>
                                    {t(
                                        'g.' +
                                            (rewardObj.repeat === 'manual'
                                                ? 'manual'
                                                : rewardObj.repeat === 'once'
                                                ? 'once'
                                                : rewardObj.repeat === 'time'
                                                ? 'everyTime'
                                                : '')
                                    )}
                                    {t('reward.forActivities')}
                                    {rewardObj.activities.map(activity => {
                                        if (activityData[activity]) {
                                            return (
                                                <Link
                                                    to={
                                                        '/activities/' +
                                                        activityData[activity]
                                                            ._id
                                                    }
                                                >
                                                    {
                                                        activityData[activity]
                                                            .name
                                                    }
                                                </Link>
                                            )
                                        }
                                        return ''
                                    })}
                                </span>
                            </Box>
                        </>
                    ) : null
                }
            />
            {progress && (
                <>
                    <DialogFullscreen
                        open={Boolean(editRewardOpen)}
                        close={() => setEditRewardOpen('')}
                        title={t('activity.addActivityToGoal')}
                    >
                        {editRewardOpen && (
                            <EditProgressReward
                                account={account}
                                rewardId={editRewardOpen}
                                rewardData={rewardData}
                                activityData={activityData}
                                progress={progress}
                                cancel={() => setEditRewardOpen('')}
                            />
                        )}
                    </DialogFullscreen>
                    <ConfirmAction
                        open={Boolean(deleteRewardVisible)}
                        title={t('activity.confirmRemove')}
                        cancel={() => setDeleteRewardVisible('')}
                        confirm={() => {
                            wsActions.deleteRewardInProgress(
                                account._id,
                                progress._id,
                                deleteRewardVisible
                            )
                            setSelectedReward('')
                        }}
                    />
                </>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
        rewardData: state.rewardData,
        activityData: state.activityData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Rewards)
)
