export default (props, notFoundResources) => {
    const createEmptyObj = () => ({
        user: [],
        account: [],
        progress: [],
        reward: [],
        activity: [],
        post: [],
        progressData: [],
        friendData: [],
        postData: [],
        transactionData: [],
        rewardData: [],
        activityData: [],
    })
    const { store } = props
    const requestedResources = createEmptyObj()
    const neededResources = createEmptyObj()
    if (store.isAuthed) {
        if (!store.user) {
            requestedResources.user.push(store.isAuthed)
        } else {
            neededResources.user.push(store.user._id)
            if (store.currentAccount) {
                if (store.account._id !== store.currentAccount) {
                    requestedResources.account.push(store.currentAccount)
                } else {
                    neededResources.account.push(store.account._id)
                    for (let progress of [
                        ...store.account.progresses,
                        ...store.account.followProgresses,
                        ...store.account.recent
                            .filter(item => item.resourceType === 'goal')
                            .map(item => item.resourceId),
                        ,
                    ]) {
                        if (!store.progressData[progress]) {
                            requestedResources.progressData.push(progress)
                        }
                        neededResources.progressData.push(progress)
                    }

                    for (let post of store.account.followPosts) {
                        if (!store.postData[post]) {
                            requestedResources.postData.push(post)
                        }
                        neededResources.postData.push(post)
                    }
                    for (let post of store.account.myPosts) {
                        if (!store.postData[post]) {
                            requestedResources.postData.push(post)
                        }
                        neededResources.postData.push(post)
                    }
                    for (let transaction of store.account.transactions) {
                        if (!store.transactionData[transaction]) {
                            requestedResources.transactionData.push(transaction)
                        }
                        neededResources.transactionData.push(transaction)
                    }
                    for (let friend of store.account.friends) {
                        if (!store.friendData[friend.friend]) {
                            requestedResources.friendData.push(friend.friend)
                        }
                        neededResources.friendData.push(friend.friend)
                    }
                    for (let reward of [
                        ...store.account.rewards,
                        ...store.account.recent
                            .filter(item => item.resourceType === 'reward')
                            .map(item => item.resourceId),
                        ,
                    ]) {
                        if (!store.rewardData[reward]) {
                            requestedResources.rewardData.push(reward)
                        }
                        neededResources.rewardData.push(reward)
                    }
                    for (let activity of [
                        ...store.account.activities,
                        ...store.account.recent
                            .filter(item => item.resourceType === 'activity')
                            .map(item => item.resourceId),
                    ]) {
                        if (!store.activityData[activity]) {
                            requestedResources.activityData.push(activity)
                        }
                        neededResources.activityData.push(activity)
                    }
                    for (let item of [
                        ...store.account.followingAccounts,
                        ...store.account.followAccounts,
                    ]) {
                        if (!store.friendData[item]) {
                            requestedResources.friendData.push(item)
                        }
                        neededResources.friendData.push(item)
                    }
                }
            }
            if (store.currentProfile) {
                if (store.profile._id !== store.currentProfile) {
                    requestedResources.account.push(store.currentProfile)
                } else {
                    neededResources.account.push(store.profile._id)
                    for (let progress of store.profile.followProgresses) {
                        if (!store.progressData[progress]) {
                            requestedResources.progressData.push(progress)
                        }
                        neededResources.progressData.push(progress)
                    }

                    for (let progress of store.profile.progresses) {
                        if (!store.progress[progress]) {
                            requestedResources.progress.push(progress)
                        }
                        neededResources.progress.push(progress)
                    }

                    for (let transaction of store.profile.transactions) {
                        if (!store.transactionData[transaction]) {
                            requestedResources.transactionData.push(transaction)
                        } else {
                            const rewardId =
                                store.transactionData[transaction].reward
                            if (!store.rewardData[rewardId]) {
                                requestedResources.rewardData.push(rewardId)
                            }
                            neededResources.rewardData.push(rewardId)
                        }
                        neededResources.transactionData.push(transaction)
                    }

                    for (let friend of store.profile.friends) {
                        if (!store.friendData[friend.friend]) {
                            requestedResources.friendData.push(friend.friend)
                        }
                        neededResources.friendData.push(friend.friend)
                    }
                    for (let reward of store.profile.rewards) {
                        if (!store.reward[reward]) {
                            requestedResources.reward.push(reward)
                        }
                        neededResources.reward.push(reward)
                    }
                    for (let activity of store.profile.activities) {
                        if (!store.activity[activity]) {
                            requestedResources.activity.push(activity)
                        }
                        neededResources.activity.push(activity)
                    }
                    for (let item of [
                        ...store.profile.followingAccounts,
                        ...store.profile.followAccounts,
                    ]) {
                        if (!store.friendData[item]) {
                            requestedResources.friendData.push(item)
                        }
                        neededResources.friendData.push(item)
                    }
                }
            }

            if (store.currentGoal) {
                if (store.progress._id !== store.currentGoal) {
                    requestedResources.progress.push(store.currentGoal)
                } else {
                    neededResources.progress.push(store.progress._id)
                    let participants = []
                    if (store.progress.owner)
                        participants.push(store.progress.owner)
                    if (store.progress.users)
                        participants = participants.concat(store.progress.users)
                    if (store.progress.likes)
                        participants = participants.concat(store.progress.likes)
                    if (store.progress.followingAccounts)
                        participants = participants.concat(
                            store.progress.followingAccounts
                        )

                    for (let friend of participants) {
                        if (!store.friendData[friend]) {
                            requestedResources.friendData.push(friend)
                        }
                        neededResources.friendData.push(friend)
                    }
                    for (let post of store.progress.posts) {
                        if (!store.postData[post]) {
                            requestedResources.postData.push(post)
                        }
                        neededResources.postData.push(post)
                    }

                    for (let reward of [
                        ...store.progress.rewards,
                        ...store.progress.oldRewards,
                    ]) {
                        if (!store.rewardData[reward.reward]) {
                            requestedResources.rewardData.push(reward.reward)
                        }
                        neededResources.rewardData.push(reward.reward)
                    }
                    for (let activity of [
                        ...store.progress.activities,
                        ...store.progress.oldActivities,
                    ]) {
                        if (!store.activityData[activity]) {
                            requestedResources.activityData.push(activity)
                        }
                        neededResources.activityData.push(activity)
                    }
                }
            }
            if (store.currentPosts) {
                for (let post of store.currentPosts) {
                    const postEl = store.post[post]
                    if (!postEl) {
                        requestedResources.post.push(post)
                    } else {
                        neededResources.post.push(post)
                        for (let friend of postEl.users) {
                            if (!store.friendData[friend]) {
                                requestedResources.friendData.push(friend)
                            }
                            neededResources.friendData.push(friend)
                        }
                        if (post.parent) {
                            if (post.parent.parentType === 'progress')
                                neededResources.progressData.push(
                                    post.parent.parentId
                                )
                            if (post.parent.parentType === 'reward')
                                neededResources.rewardData.push(
                                    post.parent.parentId
                                )
                            if (post.parent.parentType === 'activity')
                                neededResources.activityData.push(
                                    post.parent.parentId
                                )
                        }
                    }
                }
            }
            if (store.currentReward) {
                if (store.reward._id !== store.currentReward) {
                    requestedResources.reward.push(store.currentReward)
                } else {
                    neededResources.reward.push(store.reward._id)
                    for (let friend of [
                        ...store.reward.likes,
                        ...store.reward.followingAccounts,
                    ]) {
                        if (!store.friendData[friend]) {
                            requestedResources.friendData.push(friend)
                        }
                        neededResources.friendData.push(friend)
                    }

                    for (let post of store.reward.posts) {
                        if (!store.post[post]) {
                            requestedResources.post.push(post)
                        }
                        neededResources.post.push(post)
                    }
                }
            }
            if (store.currentActivity) {
                if (store.activity._id !== store.currentActivity) {
                    requestedResources.activity.push(store.currentActivity)
                } else {
                    neededResources.activity.push(store.activity._id)
                    for (let friend of [
                        ...store.activity.likes,
                        ...store.activity.followingAccounts,
                    ]) {
                        if (!store.friendData[friend]) {
                            requestedResources.friendData.push(friend)
                        }
                        neededResources.friendData.push(friend)
                    }

                    for (let post of store.activity.posts) {
                        if (!store.post[post]) {
                            requestedResources.post.push(post)
                        }
                        neededResources.post.push(post)
                    }
                }
            }
            if (store.friendSearchResult && store.friendSearchResult.length > 0)
                for (let friend of store.friendSearchResult) {
                    if (!store.friendData[friend._id]) {
                        requestedResources.friendData.push(friend._id)
                    }
                    neededResources.friendData.push(friend._id)
                }
        }
    }

    const reduce = obj => {
        for (let attr in obj) {
            if (obj[attr]) {
                obj[attr] = Array.from(
                    new Set(
                        obj[attr].filter(
                            item => !notFoundResources.includes(item)
                        )
                    )
                )
            }
        }
    }

    reduce(neededResources)
    const notNeededResources = createEmptyObj()
    notNeededResources.profile = []
    let notNeededExist
    const check1 = (key, mode) => {
        if (
            store[mode]._id &&
            !neededResources[key].includes(store[mode]._id)
        ) {
            notNeededResources[mode].push(store[mode]._id)
            notNeededExist = true
        }
    }
    const check2 = key => {
        for (let attr in store[key]) {
            if (!neededResources[key].includes(attr)) {
                notNeededResources[key].push(attr)
                notNeededExist = true
            }
        }
    }

    check1('account', 'account')
    check1('account', 'profile')
    check2('progress')
    check2('reward')
    check2('activity')
    check2('post')
    check2('friendData')
    check2('rewardData')
    check2('activityData')
    check2('progressData')
    check2('transactionData')
    check2('postData')

    if (notNeededExist) {
        props.removeData(notNeededResources)
    }

    reduce(requestedResources)
    return requestedResources
}
