import React, { memo, useState } from 'react'
import { withRouter } from 'react-router-dom'

import ReadMore from '../../../components/UI/ReadMore'
import {
    AppBar,
    Box,
    Divider,
    makeStyles,
    Paper,
    Tab,
    Tabs,
    Typography,
} from '@material-ui/core'

import Rewards from '../../../components/Rewards/Rewards'
import Activities from '../../../components/Activities/Activities'
import { useTranslation } from 'react-i18next'
import ResourceCore from '../../../components/ResourceCore'
import MapPosition from '../../../components/MapPosition/MapPosition'
import SimilarResources from '../../../components/SimilarResources'
import ResourcePosts from '../../../components/Post/ResourcePosts'
import ResourceCoreMM from '../../../components/ResourceCoreMM'
import ActivityTable from '../../Goal/ActivityTable'
import RetweetOutlined from '@ant-design/icons/RetweetOutlined'
import { connect } from 'react-redux'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

const ActivityMM = props => {
    const { t } = useTranslation()
    const { id } = props
    const classes = useStyles()
    const { activity } = props
    const getDay = day => {
        switch (day) {
            case '0':
                return t('g.sunday')
            case '1':
                return t('g.monday')
            case '2':
                return t('g.tuesday')
            case '3':
                return t('g.wednesday')
            case '4':
                return t('g.thursday')
            case '5':
                return t('g.friday')
            case '6':
                return t('g.saturday')

            default:
                return ''
        }
    }

    const getRepeatType = repeat => {
        switch (repeat) {
            case 'day':
                return t('g.everyDay')
            case 'weekday':
                return t('g.everyDayOfWeek')
            case 'week':
                return t('g.everyWeek')
            case 'month':
                return t('g.everyMonth')
            case 'year':
                return t('g.everyYear')
            default:
                return ''
        }
    }
    return (
        <ResourceCoreMM type="activity" resourceId={id} resource={activity}>
            {activity && (
                <>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.tracking')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <ActivityTable activityId={activity._id} />
                    </Paper>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.desc')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        {activity.repeat !== 'no' && (
                            <>
                                <p>
                                    <RetweetOutlined />
                                    {` ${t('g.repeats')} 
                            ${getRepeatType(activity.repeat) +
                                (activity.repeat === 'weekday'
                                    ? ' (' +
                                      activity.days.reduce(
                                          (acc, val, index) =>
                                              acc +
                                              (index > 0 ? ', ' : '') +
                                              getDay(val),
                                          ''
                                      ) +
                                      ').'
                                    : '.')}`}
                                </p>
                                <Divider
                                    light
                                    variant="middle"
                                    style={{ marginBottom: '15px' }}
                                />
                            </>
                        )}

                        <ReadMore
                            initialHeight={200}
                            contentEditor={activity.description}
                        />
                    </Paper>
                    <Typography
                        className={classes.title}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('g.position')}
                    </Typography>
                    <Paper style={{ padding: '15px' }}>
                        <MapPosition position={activity.position} />
                    </Paper>
                    {/* <SimilarResources resource={progress} type="activity" /> */}
                </>
            )}
        </ResourceCoreMM>
    )
}

const mapStateToProps = (state, ownProps) => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        activity: state.activity[ownProps.id],
        friendData: state.friendData,
    }
}

export default withRouter(connect(mapStateToProps)(memo(ActivityMM)))
