import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Helmet from 'react-helmet'

import Layout from '../../components/Layout/Layout'
import {
    Card,
    CardContent,
    CardMedia,
    createStyles,
    makeStyles,
    Typography,
} from '@material-ui/core'
import Welcome from '../../components/Welcome'
import dayjs from 'dayjs'
import ReactHtmlParser from 'react-html-parser'
import ShareButton from '../../components/UI/ShareButton'
import { useTranslation } from 'react-i18next'

dayjs().format()

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
        root: {
            flexGrow: 1,
            marginTop: '20px',
        },
        media: {
            height: 0,
            paddingTop: '56.25%', // 16:9\
            backgroundSize: 'contain',
        },
        backToBlog: {
            left: '16px',
            bottom: '88px',
            position: 'fixed',
            zIndex: 1,
        },
    })
)

const Blog = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [post, setPost] = useState(null)
    // const [comments, setComments] = useState([])
    const [title, setTitle] = useState('')

    useEffect(() => {
        fetch('https://addspire-blog.herokuapp.com/graphql', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                query: `
            query {
  posts (where: { language:"${
      props.history.location.pathname.indexOf('/ru') === 0 ? 'ru' : 'en'
  }",
  _or: [
      {url: "${
          props.history.location.pathname.split('/')[
              props.history.location.pathname.split('/').length - 1
          ]
      }"},
      {url: "${
          props.history.location.pathname.split('/')[
              props.history.location.pathname.split('/').length - 1
          ]
      }.ru"}
  ] }){
    id
    url
    content
    title
    published_at
   previewImage {
    url
  }
    subtitle
  }
}`,
            }),
        })
            .then(res => res.json())
            .then(data => {
                if (data.data.posts.length > 0) {
                    setPost(data.data.posts[0])
                    setTitle(data.data.posts[0].title + t('g.shareText'))
                }
            })
    }, [props.history.location.pathname])

    useEffect(() => {
        window.fbAsyncInit = function() {
            window.FB.init({
                appId: '115517331888071',
                cookie: true, // enable cookies to allow the server to access the session
                xfbml: true, // parse social plugins on this page
                version: 'v2.5', // use version 2.1
            })
        }

        // Load the SDK asynchronously
        ;(function(d, s, id) {
            var js,
                fjs = d.getElementsByTagName(s)[0]
            if (d.getElementById(id)) return
            js = d.createElement(s)
            js.id = id
            js.src = '//connect.facebook.net/en_US/sdk.js'
            fjs.parentNode.insertBefore(js, fjs)
        })(document, 'script', 'facebook-jssdk')
    }, [])
    useEffect(() => {
        if (window.FB) window.FB.XFBML.parse()
    })
    return post ? (
        <>
            {title && (
                <Helmet>
                    <title>{title}</title>
                </Helmet>
            )}

            <Layout white>
                {/* <Welcome /> */}

                <Card
                    style={{
                        margin: '50px auto',
                        maxWidth: '1000px',
                        boxShadow: 'none',
                    }}
                >
                    <CardMedia
                        className={classes.media}
                        image={post.previewImage && post.previewImage.url}
                        title={post.title}
                    />
                    <CardContent style={{ paddingLeft: 0, paddingRight: 0 }}>
                        <Typography
                            align="right"
                            variant="button"
                            color="textSecondary"
                            component="p"
                            gutterBottom
                        >
                            {dayjs(post.published_at).format('MMMM D, YYYY')}
                        </Typography>
                        <div style={{ textAlign: 'right' }}>
                            <ShareButton
                                post
                                title={post.title + t('g.shareText')}
                                media={
                                    post.previewImage && post.previewImage.url
                                }
                            />
                        </div>
                        <Typography gutterBottom variant="h2" component="h1">
                            {post.title}
                        </Typography>
                        <Typography gutterBottom variant="h5">
                            {post.subtitle}
                        </Typography>

                        <Typography
                            variant="body1"
                            className="blogArticle"
                            style={{ fontSize: '1.05rem' }}
                        >
                            {ReactHtmlParser(post.content)}
                        </Typography>
                        <div style={{ textAlign: 'right' }}>
                            <ShareButton
                                post
                                title={post.title + t('g.shareText')}
                                media={
                                    post.previewImage && post.previewImage.url
                                }
                            />
                        </div>
                        <div
                            className="fb-comments"
                            data-href={
                                'https://addspire.com' +
                                props.history.location.pathname
                            }
                            data-numposts="5"
                            data-width=""
                        />
                    </CardContent>
                </Card>
            </Layout>
        </>
    ) : null
}

const mapStateToProps = state => {
    return {
        account: state.account,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Blog)
)
