import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import ReadMore from '../../components/UI/ReadMore'
import {
    Box,
    makeStyles,
    Paper,
    Tab,
    Tabs,
    Typography,
} from '@material-ui/core'

import { useTranslation } from 'react-i18next'
import ResourceCore from '../../components/ResourceCore'
import MapPosition from '../../components/MapPosition/MapPosition'
import ResourcePosts from '../../components/Post/ResourcePosts'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

function TabPanel(props) {
    const { children, value, index, ...other } = props

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    )
}
function a11yProps(index) {
    return {
        id: `scrollable-auto-tab-${index}`,
        'aria-controls': `scrollable-auto-tabpanel-${index}`,
    }
}

const Reward = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [reward, setReward] = useState(null)
    const [posts, setPosts] = useState([])
    const [currentTab, setCurrentTab] = useState(1)
    return (
        <ResourceCore
            type="reward"
            setResource={setReward}
            setPosts={setPosts}
            navi={
                <Tabs
                    value={currentTab}
                    onChange={(event, value) => setCurrentTab(value)}
                    aria-label="simple tabs example"
                    indicatorColor="primary"
                    textColor="primary"
                >
                    <Tab label={t('g.desc')} {...a11yProps(0)} />
                    <Tab label={t('g.posts')} {...a11yProps(1)} />
                </Tabs>
            }
            resourceId={props.id}
        >
            {reward && (
                <>
                    <TabPanel value={currentTab} index={0}>
                        <Typography
                            className={classes.title}
                            color="textSecondary"
                            gutterBottom
                        >
                            {t('g.desc')}
                        </Typography>
                        <Paper style={{ padding: '15px' }}>
                            <ReadMore
                                initialHeight={200}
                                contentEditor={reward.description}
                            />
                        </Paper>
                        <Typography
                            className={classes.title}
                            color="textSecondary"
                            gutterBottom
                        >
                            {t('g.position')}
                        </Typography>
                        <Paper style={{ padding: '15px' }}>
                            <MapPosition position={reward.position} />
                        </Paper>
                    </TabPanel>
                    <TabPanel value={currentTab} index={1}>
                        {/* <SimilarResources resource={progress} type="activity" /> */}

                        <ResourcePosts
                            resource={reward}
                            posts={posts}
                            type="reward"
                        />
                    </TabPanel>
                    {/* Used in goals
                    Associated rewards */}
                </>
            )}
        </ResourceCore>
    )
}

export default withRouter(Reward)
