import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import * as wsActions from '../../websocketActions'
import AddMessage from './AddMessage'
import ImageCarousel from '../UI/ImageCarousel/ImageCarousel'
import UserButton from '../UserButton/UserButton'
import {
    Badge,
    Box,
    Button,
    Chip,
    IconButton,
    Menu,
    MenuItem,
} from '@material-ui/core'
import dayjs from 'dayjs'
import DialogFullscreen from '../UI/DialogFullscreen'
import MasonryCard from '../MasonryCard'
import FollowButton from '../FollowButton'
import { MoreVertOutlined, ThumbUp } from '@material-ui/icons'
import ReadMore from '../UI/ReadMore'
import EditPost from './EditPost'
import { useTranslation } from 'react-i18next'
import ConfirmAction from '../UI/ConfirmAction'
import message from 'antd/es/message'

dayjs().format()

export const spanStyle = {
    fontSize: '12px',
    color: '#666',
    textTransform: 'none',
}
const Message = props => {
    const { t } = useTranslation()
    const { account, message, friendData, post, replies, isStart } = props
    const [replyVisible, setReplyVisible] = useState(false)
    const [deleteMessageOpen, setDeleteMessageOpen] = useState(false)
    const [likes, setLikes] = useState(null)
    const [replyToUser, setReplyToUser] = useState('')
    const [editingMessage, setEditingMessage] = useState('')
    const [showReplies, setShowReplies] = useState(false)
    const [anchorEl, setAnchorEl] = useState(null)
    const [editMessageOpen, setEditMessageOpen] = useState(false)

    const handleEditPostSave = (images, messageText) => {
        wsActions.editPost(account._id, post._id, messageText, images)
        if (props.onSend) props.onSend()
        handleEditPostCancel()
    }

    const handleEditPostCancel = () => {
        setEditMessageOpen(false)
    }

    return account && friendData ? (
        <>
            {account._id !== message.author || !editingMessage ? (
                <>
                    {props.isStart ? (
                        <Box
                            style={{
                                display: 'flex',
                                width: '100%',
                                margin: '10px 0px',
                            }}
                        >
                            <Box style={{ marginRight: '10px' }}>
                                <UserButton
                                    noName={true}
                                    id={message.author}
                                    span={true}
                                />
                            </Box>
                            <Box style={{ flex: 1 }}>
                                <span
                                    style={{
                                        color: '#333',
                                        fontWeight: 'bold',
                                        fontSize: '14px',
                                    }}
                                >
                                    <UserButton
                                        id={message.author}
                                        span={true}
                                        noAva={true}
                                    />{' '}
                                    <div
                                        style={spanStyle}
                                        onClick={() =>
                                            props.history.push(
                                                post.parent.parentType ===
                                                    'progress' &&
                                                    '/goals/' +
                                                        post.parent.parentId +
                                                        '/post/' +
                                                        post._id
                                            )
                                        }
                                    >
                                        {dayjs(message.date).fromNow()}
                                    </div>
                                    {/* <span style={{ fontWeight: 'normal' }}>in</span>{' '}
                {post.parent &&
                    progressData &&
                    progressData[post.parent.parentId] && (
                        <span
                            style={{ cursor: 'pointer' }}
                            onClick={() =>
                                props.history.push(
                                    '/goals/' + post.parent.parentId
                                )
                            }
                        >
                            {progressData[post.parent.parentId].goal.name}
                        </span>
                    )} */}
                                    {post.startMessage &&
                                        ['wishlist', 'perk', 'goal'].includes(
                                            post.startMessage.messageType
                                        ) && (
                                            <span
                                                style={{
                                                    cursor: 'pointer',
                                                }}
                                                onClick={() =>
                                                    props.history.push(
                                                        '/profile/' +
                                                            message.author
                                                    )
                                                }
                                            >
                                                {post.startMessage
                                                    .messageType === 'wishlist'
                                                    ? t('g.wishlist')
                                                    : post.startMessage
                                                          .messageType ===
                                                      'goals'
                                                    ? t('g.goals')
                                                    : t('g.rewards')}
                                            </span>
                                        )}
                                </span>
                            </Box>
                            {account._id === message.author && (
                                <Box>
                                    <IconButton
                                        onClick={event =>
                                            setAnchorEl(event.currentTarget)
                                        }
                                    >
                                        <MoreVertOutlined />
                                    </IconButton>
                                    <Menu
                                        id="simple-menu"
                                        anchorEl={anchorEl}
                                        keepMounted
                                        open={Boolean(anchorEl)}
                                        onClose={() => setAnchorEl(null)}
                                    >
                                        <MenuItem
                                            onClick={() => {
                                                setEditMessageOpen(true)
                                                setAnchorEl(null)
                                            }}
                                        >
                                            {t('g.edit')}
                                        </MenuItem>
                                        <MenuItem
                                            onClick={() => {
                                                setDeleteMessageOpen(true)
                                                setAnchorEl(null)
                                            }}
                                        >
                                            {t('g.delete')}
                                        </MenuItem>
                                    </Menu>
                                    <EditPost
                                        open={editMessageOpen}
                                        cancel={handleEditPostCancel}
                                        title={t('post.editPost')}
                                        save={handleEditPostSave}
                                        images={message.image}
                                        content={message.text}
                                    />
                                    <ConfirmAction
                                        open={deleteMessageOpen}
                                        cancel={() =>
                                            setDeleteMessageOpen(false)
                                        }
                                        title={t('post.confirmDelete')}
                                        confirm={() =>
                                            wsActions.deletePost(
                                                account._id,
                                                post._id
                                            )
                                        }
                                    />
                                </Box>
                            )}
                        </Box>
                    ) : (
                        <>
                            <Box style={{ display: 'flex', width: '100%' }}>
                                <Box style={{ marginRight: '10px' }}>
                                    <UserButton
                                        noName={true}
                                        id={message.author}
                                        span={true}
                                        size={props.isTop ? 36 : 25}
                                    />
                                </Box>
                                <Box style={{ flex: 1 }}>
                                    <Badge
                                        anchorOrigin={{
                                            vertical: 'bottom',
                                            horizontal: 'right',
                                        }}
                                        overlap="circle"
                                        badgeContent={
                                            message.likes.length ? (
                                                <Chip
                                                    icon={<ThumbUp />}
                                                    color="primary"
                                                    label={message.likes.length}
                                                    size="small"
                                                    onClick={() =>
                                                        setLikes(message.likes)
                                                    }
                                                />
                                            ) : (
                                                0
                                            )
                                        }
                                    >
                                        <Box
                                            style={{
                                                background: '#efefef',
                                                padding: '5px 15px',
                                                borderRadius: '15px',
                                            }}
                                        >
                                            <UserButton
                                                id={message.author}
                                                span={true}
                                                noAva={true}
                                            />
                                            {message.image &&
                                                message.image.length > 0 && (
                                                    <ImageCarousel
                                                        images={message.image}
                                                        height="60vmin"
                                                        type="contain"
                                                    />
                                                )}
                                            {message.text && (
                                                <p
                                                    style={{
                                                        wordBreak: 'break-word',
                                                    }}
                                                >
                                                    {message.text}
                                                </p>
                                            )}
                                        </Box>
                                    </Badge>
                                    <Box>
                                        <span
                                            style={{
                                                ...spanStyle,
                                                marginRight: '15px',
                                            }}
                                        >
                                            {dayjs(message.date).fromNow()}
                                        </span>

                                        <Button
                                            onClick={() => {
                                                if (account._id)
                                                    wsActions.changeLikesMessage(
                                                        message.likes.includes(
                                                            account._id
                                                        )
                                                            ? 'dislikeMessage'
                                                            : 'likeMessage',
                                                        message.messageId,
                                                        account._id,
                                                        post._id
                                                    )
                                                else
                                                    message.warning(
                                                        t('g.needLogin')
                                                    )
                                            }}
                                        >
                                            <span style={spanStyle}>
                                                {message.likes.includes(
                                                    account._id
                                                )
                                                    ? t('g.unlike')
                                                    : t('g.like')}
                                            </span>
                                        </Button>

                                        {message.author !== account._id && (
                                            <Button
                                                onClick={() => {
                                                    if (!account._id)
                                                        message.warning(
                                                            t('g.needLogin')
                                                        )
                                                    if (account._id)
                                                        if (
                                                            props.setReplyVisible
                                                        ) {
                                                            props.setReplyVisible(
                                                                true
                                                            )
                                                            props.setReplyToUser(
                                                                props.message
                                                                    .author
                                                            )
                                                        } else {
                                                            setReplyVisible(
                                                                true
                                                            )
                                                            setReplyToUser('')
                                                        }
                                                    setShowReplies(true)
                                                }}
                                            >
                                                <span style={spanStyle}>
                                                    {t('post.reply')}
                                                </span>
                                            </Button>
                                        )}
                                        {message.author === account._id && (
                                            <Button
                                                onClick={() =>
                                                    setEditingMessage(
                                                        message.messageId
                                                    )
                                                }
                                            >
                                                <span style={spanStyle}>
                                                    {t('g.edit')}
                                                </span>
                                            </Button>
                                        )}
                                    </Box>
                                    {props.isTop &&
                                    replies &&
                                    replies.length > 0 ? (
                                        showReplies ? (
                                            replies.map((reply, index) => (
                                                <Message
                                                    key={index}
                                                    message={reply}
                                                    post={post}
                                                    account={account}
                                                    friendData={friendData}
                                                    setReplyVisible={
                                                        setReplyVisible
                                                    }
                                                    setReplyToUser={
                                                        setReplyToUser
                                                    }
                                                />
                                            ))
                                        ) : (
                                            <Button
                                                onClick={() =>
                                                    setShowReplies(true)
                                                }
                                            >
                                                <span
                                                    style={{
                                                        ...spanStyle,
                                                        fontSize: '15px',
                                                    }}
                                                >
                                                    {`${t('g.show')} ${
                                                        replies.length
                                                    } ${t('post.replies')}`}
                                                </span>
                                            </Button>
                                        )
                                    ) : null}

                                    {props.isTop && replyVisible ? (
                                        <AddMessage
                                            post={post}
                                            account={account}
                                            replyTo={message.messageId}
                                            replyToUser={replyToUser}
                                            cancel={() =>
                                                setReplyVisible(false)
                                            }
                                        />
                                    ) : null}
                                </Box>
                            </Box>
                        </>
                    )}

                    {isStart ? (
                        <>
                            {message.messageType === 'wishlist' && (
                                <>
                                    {' '}
                                    <span
                                        style={{
                                            fontWeight: 'bold',
                                            cursor: 'pointer',
                                            margin: '10px 0px 0px ',
                                        }}
                                        onClick={() =>
                                            props.history.push(
                                                `/wishlist/${
                                                    message.details.owner
                                                }/${message.details.itemId}`
                                            )
                                        }
                                    >
                                        {message.details.name}
                                    </span>
                                </>
                            )}
                            {message.text && (
                                <ReadMore
                                    initialHeight={700}
                                    contentEditor={message.text}
                                />
                            )}
                            {message.image && message.image.length > 0 && (
                                <ImageCarousel
                                    images={message.image}
                                    height="60vmin"
                                    type="contain"
                                />
                            )}
                        </>
                    ) : (
                        <></>
                    )}
                </>
            ) : (
                <AddMessage
                    post={post}
                    account={account}
                    replyTo={message.messageId}
                    editingMessage={editingMessage}
                    content={message.text}
                    images={message.image}
                    onSend={() => setEditingMessage('')}
                    cancel={() => setEditingMessage('')}
                />
            )}
            <DialogFullscreen
                open={Boolean(likes)}
                close={() => setLikes(null)}
                title="Message likes"
            >
                {likes &&
                    likes.map(item => (
                        <MasonryCard
                            friends={true}
                            noLink={true}
                            key={item}
                            actions={
                                <FollowButton
                                    account={account}
                                    id={item}
                                    type="account"
                                />
                            }
                            body={
                                <UserButton span={true} id={item} size={50} />
                            }
                        />
                    ))}
            </DialogFullscreen>
        </>
    ) : null
}

export default withRouter(Message)
