import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import isEqual from 'lodash.isequal'

import Post from '../../components/Post/Post'

import UserButton from '../../components/UserButton/UserButton'
import Layout from '../../components/Layout/Layout'
import {
    Badge,
    Button,
    createStyles,
    makeStyles,
    Typography,
} from '@material-ui/core'
import DashboardEvents from './DashboardEvents'
import { useTranslation } from 'react-i18next'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            fontSize: 13,
            margin: '30px 15px 10px',
            fontWeight: 600,
        },
    })
)

const DashboardPosts = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const {
        account,
        friendData,
        statePosts,
        progressData,
        currentPosts,
    } = props

    const [posts, setPosts] = useState([])
    const [currentTab, setCurrentTab] = useState('posts')
    const [startTime] = useState(new Date().getTime())

    const sortPosts = (a, b) => {
        const aN = a.notifications.filter(item => item.user !== account._id)
        const bN = b.notifications.filter(item => item.user !== account._id)
        if (aN.length === 0 && bN.length > 0) return 1
        else if (aN.length > 0 && bN.length === 0) return -1
        else if (aN.length === 0 && bN.length === 0) return 0
        else
            return new Date(aN[0].date).getTime() <
                new Date(bN[0].date).getTime()
                ? 1
                : -1
    }

    const sortNotifications = (a, b) =>
        new Date(a.date).getTime() < new Date(b.date).getTime() ? 1 : -1

    let numberOfUnseenNots = 0

    for (let not of props.lastNotifications) {
        if (parseInt(not.notId) > parseInt(account.lastSeenNot || 0))
            numberOfUnseenNots++
    }

    useEffect(() => {
        if (currentPosts) {
            const newPosts = currentPosts
                .filter(item => statePosts[item])
                .map(item => statePosts[item])

            const newUnseenPosts = []
            const newOldPosts = []
            let oldPosts = [...posts]
            for (let post of newPosts) {
                let oldPostIndex = oldPosts.findIndex(item => {
                    return item._id.toString() === post._id.toString()
                })
                if (oldPostIndex === -1) {
                    if (
                        post.notifications &&
                        post.notifications.length > 0 &&
                        new Date(post.notifications[0].date).getTime() >
                            startTime
                    ) {
                        newUnseenPosts.push(post)
                    } else {
                        newOldPosts.push(post)
                    }
                } else {
                    oldPosts[oldPostIndex] = post
                }
            }

            for (let post of newOldPosts) {
                const index = oldPosts.findIndex(item => {
                    if (
                        item &&
                        item.notifications &&
                        item.notifications.length > 0
                    ) {
                        return (
                            new Date(item.notifications[0]).getTime() >
                            new Date(post.notifications[0]).getTime()
                        )
                    } else return false
                })
                if (index === -1) {
                    oldPosts.push(post)
                } else {
                    oldPosts.splice(index, 0, post)
                }
            }

            let resultPosts = [...newUnseenPosts, ...oldPosts]
            if (posts.length === 0) {
                resultPosts = resultPosts.sort(sortPosts)
            }
            setPosts(resultPosts)
        }
    }, [currentPosts, statePosts])
    if (!account._id) return null

    let lastNotifications = []
    for (let progress of account.followProgresses) {
        if (progressData[progress])
            lastNotifications = [
                ...lastNotifications,
                ...progressData[progress].notifications.filter(
                    item =>
                        item.code === 'add post' && item.user !== account._id
                ),
            ]
    }

    lastNotifications = lastNotifications
        .sort(sortNotifications)
        .map(item => item.details.postId)
        .slice(0, 10)

    const neededPosts = [
        ...new Set([
            ...account.followPosts,
            ...account.myPosts,
            ...posts.map(post => post._id),
            ...lastNotifications,
        ]),
    ]
    if (!isEqual(currentPosts, neededPosts))
        props.setData({ currentPosts: neededPosts })

    props.setPageTitle({ main: 'Dashboard', sub: '' })

    return (
        <Layout
            header={
                <>
                    <Button
                        style={{ margin: '0px 20px' }}
                        color={currentTab === 'posts' ? 'primary' : 'default'}
                        onClick={() => setCurrentTab('posts')}
                        variant="contained"
                    >
                        {t('g.posts')}
                    </Button>
                    <Badge badgeContent={numberOfUnseenNots} color="secondary">
                        <Button
                            color={
                                currentTab === 'events' ? 'primary' : 'default'
                            }
                            onClick={() => setCurrentTab('events')}
                            variant="contained"
                        >
                            {t('g.events')}
                        </Button>
                    </Badge>
                </>
            }
        >
            {currentTab === 'posts' ? (
                posts.map((item, index) => {
                    let text = null
                    const nots = item.notifications.filter(
                        not => not.user !== account._id
                    )
                    const not = nots.length > 0 ? nots[0] : null
                    if (not) {
                        switch (not.code) {
                            case 'edit wishlist item':
                                text = t('dash.editedItem')
                                break
                            case 'edit goal':
                                text = t('dash.editedGoal')
                                break
                            case 'comment':
                                text = t('dash.addedComment')
                                break
                            case 'edit comment':
                                text = t('dash.editedComment')
                                break
                            case 'like':
                                text = t('dash.addedLike')
                                break
                            case 'dislike':
                                text = t('dash.removedLike')
                                break
                            default:
                                text = null
                                break
                        }
                    }
                    return (
                        <div key={index} style={{ paddingTop: '10px' }}>
                            {text && (
                                <>
                                    <Typography
                                        className={classes.title}
                                        color="textSecondary"
                                        gutterBottom
                                    >
                                        <span>
                                            <UserButton
                                                id={not.user}
                                                noAva={true}
                                                span={true}
                                                style={{
                                                    fontWeight: 'bold',
                                                }}
                                            />{' '}
                                            {text}
                                        </span>
                                    </Typography>
                                </>
                            )}
                            <Post
                                friendData={friendData}
                                account={account}
                                post={item}
                                progressData={progressData}
                            />
                        </div>
                    )
                })
            ) : (
                <DashboardEvents account={account} />
            )}
        </Layout>
    )
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        statePosts: state.post,
        progress: state.progress,
        friendData: state.friendData,
        progressData: state.progressData,
        currentPosts: state.currentPosts,
        lastNotifications: state.lastNotifications,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(DashboardPosts)
)
