import { Button } from '@material-ui/core'
import React, { useEffect, useRef, useState } from 'react'
import TinyEditor from './TinyEditor'
import { useTranslation } from 'react-i18next'

const ReadMore = props => {
    const { t } = useTranslation()
    const [overflow, setOverflow] = useState(false)
    const [open, setOpen] = useState(false)
    const descRef = useRef(null)

    useEffect(() => {
        if (
            descRef.current &&
            descRef.current.offsetHeight > props.initialHeight
        ) {
            setOverflow(true)
        }
    }, [
        descRef.current && descRef.current.offsetHeight,
        props.content,
        props.contentEditor,
        props.children,
    ])
    return (
        <div
            style={{
                maxHeight: open ? 'initial' : props.initialHeight + 'px',
                overflow: 'hidden',
                position: 'relative',
                paddingBottom: overflow && open ? '50px' : '0px',
            }}
        >
            {props.content && (
                <div
                    ref={descRef}
                    dangerouslySetInnerHTML={{ __html: props.content }}
                />
            )}
            {props.contentEditor && (
                <div ref={descRef}>
                    <TinyEditor readOnly value={props.contentEditor} />
                </div>
            )}
            {props.children && <div ref={descRef}>{props.children}</div>}
            {overflow && (
                <div
                    style={{
                        position: 'absolute',
                        bottom: 0,
                        width: '100%',
                        // zIndex: 1,
                    }}
                >
                    <div
                        style={{
                            transition: 'opacity 0.25s',
                            opacity: open ? 0 : 1,
                            backgroundImage:
                                'linear-gradient(to bottom, rgba(255, 255, 255, 1), #ffffff )',
                            content: '',
                            height: '50px',
                            width: '100%',

                            bottom: '0',
                            left: '0',
                            position: 'absolute',
                        }}
                    />

                    <Button color="primary" onClick={() => setOpen(!open)}>
                        {open
                            ? t('g.readLess')
                            : t('g.readMore') + (props.addTitle || '')}
                    </Button>
                </div>
            )}
        </div>
    )
}

export default ReadMore
