import message from 'antd/es/message'
import axios from 'axios'

export const uploadFile = (file, size, filename, callback) => {
    if (file == null) {
        return alert('No file selected.')
    }

    const reader = new FileReader()
    reader.onload = async event => {
        resizeImageToSpecificSize(file, { width: size, height: size }, data => {
            if (!data) {
                callback()
            } else {
                urltoFile(data, '/120').then(function(file) {
                    getSignedRequest(file, filename, callback)
                })
            }
        })
    }

    reader.readAsArrayBuffer(file)
}

const urltoFile = (url, filename, mimeType) => {
    mimeType = mimeType || (url.match(/^data:([^;]+);/) || '')[1]
    return fetch(url)
        .then(function(res) {
            return res.arrayBuffer()
        })
        .then(function(buf) {
            return new File([buf], filename, { type: 'image/jpeg' })
        })
}

const resizeImageToSpecificSize = (file, size, cb) => {
    if (file) {
        var reader = new FileReader()
        reader.onload = function(event) {
            resizeImageFromSrcToSpecificSize(event.target.result, size, cb)
        }
        reader.readAsDataURL(file)
    }
}

const resizeImageFromSrcToSpecificSize = (src, size, cb) => {
    try {
        var data
        var img = new Image()
        img.onload = function() {
            const widthCoef = img.width / size.width
            const heightCoef = img.height / size.height
            var oc = document.createElement('canvas'),
                octx = oc.getContext('2d')
            oc.width = img.width
            oc.height = img.height
            octx.drawImage(img, 0, 0)
            if (widthCoef > 1 || heightCoef > 1) {
                if (img.width > img.height) {
                    oc.height = (img.height / img.width) * size.width
                    oc.width = size.width
                } else {
                    oc.width = (img.width / img.height) * size.width
                    oc.height = size.width
                }
            }
            octx.drawImage(oc, 0, 0, oc.width, oc.height)
            octx.drawImage(img, 0, 0, oc.width, oc.height)
            data = oc.toDataURL('image/jpeg', 0.6)
            cb(data)
        }
        img.onerror = () => {
            cb()
        }
        img.src = src
    } catch {}
}

const getSignedRequest = (file, fileName, callback) => {
    return axios
        .post('/api/sign-s3', {
            fileName,
        })
        .then(response => {
            doUploadFile(file, response.data, callback)
        })
        .catch(err => {})
}

const doUploadFile = (file, data, callback) => {
    return axios
        .put(data.signedRequest, file, {
            headers: {
                'Content-Type': 'multipart/form-data',
            },
        })
        .then(response => {
            callback(data.url)
            message.success('The image has been saved.')
        })
        .catch(err => {})
}
