import React, { useState } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'
import {
    Box,
    Button,
    CardActionArea,
    CardContent,
    CardMedia,
    Dialog,
    DialogActions,
    DialogTitle,
    Slide,
    Typography,
} from '@material-ui/core'
import { getPH, placeholderImages } from '../MasonryCard'
import ProfileActivities from '../ProfileActivities/ProfileActivities'
import DialogFullscreen from '../UI/DialogFullscreen'
import { useTranslation } from 'react-i18next'
import ActivityTable from '../../containers/Goal/ActivityTable'
import EditResource from '../EditResource/EditResource'
const TransitionComponent = props => <Slide direction="up" {...props} />

const Activities = props => {
    const { t } = useTranslation()
    // const [anchorEl, setAnchorEl] = useState(null)
    const [addActivityOpen, setAddActivityOpen] = useState(false)
    const [selectedActivity, setSelectedActivity] = useState('')
    const [deleteActivityVisible, setDeleteActivityVisible] = useState(false)

    const {
        activityData,
        account,
        progress,
        activities,
        setChosenResource,
    } = props

    // const handleClose = () => {
    //     setAnchorEl(null)
    // }
    // const handleClick = event => {
    //     setAnchorEl(event.currentTarget)
    // }

    const isOwner =
        account && account._id && progress && account._id === progress.owner

    const activitiesIds = (progress ? progress.activities : activities) || []
    return (
        <>
            <HorizontalScrollList
                title={t('g.activities')}
                items={activitiesIds
                    .map(item => activityData[item])
                    .filter(item => item)}
                content={item => (
                    <CardContent
                        style={{
                            textAlign: 'center',
                            position: 'relative',
                            padding: '5px',
                        }}
                    >
                        <CardActionArea
                            onClick={() => setSelectedActivity(item._id)}
                        >
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    item.images.length > 0
                                        ? item.images[0]
                                        : placeholderImages[getPH(item._id)]
                                }
                                title={item.name}
                            />
                            <CardContent style={{ padding: '5px' }}>
                                <Typography
                                    variant="body2"
                                    noWrap
                                    color={
                                        selectedActivity === item._id
                                            ? 'primary'
                                            : undefined
                                    }
                                    style={{
                                        fontWeight:
                                            selectedActivity === item._id
                                                ? 600
                                                : 'initial',
                                    }}
                                >
                                    {item.name}
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </CardContent>
                )}
                withAdd={Boolean(
                    account &&
                        (props.isOwner ||
                            (progress && progress.owner === account._id))
                )}
                addTitle={t('g.addActivity')}
                onAdd={() => setAddActivityOpen(true)}
                bottomContent={
                    selectedActivity && activityData[selectedActivity] ? (
                        <>
                            <Typography gutterBottom>
                                {activityData[selectedActivity]
                                    ? activityData[selectedActivity].name
                                    : 'Selected activity'}
                            </Typography>
                            <Button
                                onClick={() =>
                                    setChosenResource(selectedActivity)
                                }
                                color="primary"
                            >
                                {t('g.goToActivity')}
                            </Button>
                            {isOwner && (
                                <Button
                                    onClick={() =>
                                        setDeleteActivityVisible(
                                            selectedActivity
                                        )
                                    }
                                >
                                    {t('g.removeActivity')}
                                </Button>
                            )}
                            <Box style={{ marginTop: '10px' }}>
                                <ActivityTable activityId={selectedActivity} />
                            </Box>
                        </>
                    ) : null
                }
            />
            {progress && (
                <>
                    <DialogFullscreen
                        open={addActivityOpen}
                        close={() => setAddActivityOpen(false)}
                        title={t('activity.addActivityToGoal')}
                    >
                        <ProfileActivities
                            owner={account}
                            isSelect
                            value={[]}
                            onChange={value => {
                                if (value.length > 0)
                                    wsActions.addActivityToProgress(
                                        account._id,
                                        progress._id,
                                        value[0]
                                    )
                                setAddActivityOpen(false)
                            }}
                            // openInDialog
                            // additionalButton={item => (
                            //     <Button
                            //         color="primary"
                            //         onClick={() => {
                            //             wsActions.addActivityToProgress(
                            //                 account._id,
                            //                 progress._id,
                            //                 item._id
                            //             )
                            //             setAddActivityOpen(false)
                            //         }}
                            //     >
                            //         {t('g.choose')}
                            //     </Button>
                            // )}
                        />
                    </DialogFullscreen>
                    <Dialog
                        open={Boolean(deleteActivityVisible)}
                        onClose={() => setDeleteActivityVisible(false)}
                    >
                        <DialogTitle>{t('activity.confirmRemove')}</DialogTitle>

                        <DialogActions>
                            <Button
                                onClick={() => setDeleteActivityVisible(false)}
                                color="primary"
                            >
                                {t('g.cancel')}
                            </Button>
                            <Button
                                onClick={() => {
                                    wsActions.deleteActivityFromProgress(
                                        account._id,
                                        progress._id,
                                        deleteActivityVisible
                                    )
                                    setDeleteActivityVisible(false)
                                    setSelectedActivity('')
                                }}
                                color="primary"
                            >
                                {t('g.confirm')}
                            </Button>
                        </DialogActions>
                    </Dialog>
                </>
            )}
            {props.isOwner && (
                <Dialog
                    fullScreen
                    open={addActivityOpen}
                    onClose={() => setAddActivityOpen(false)}
                    TransitionComponent={TransitionComponent}
                >
                    <EditResource
                        type="activity"
                        account={account}
                        friendData={props.friendData}
                        cancel={() => setAddActivityOpen(false)}
                    />
                </Dialog>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
        activityData: state.activityData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Activities)
)
