import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../store/actions'
import * as wsActions from '../websocketActions'

import pullData from '../utils/pullData'
import Layout from './Layout/Layout'
import { createStyles, makeStyles, Typography } from '@material-ui/core'
import { useTranslation } from 'react-i18next'
import ResourcePosts from './Post/ResourcePosts'
import SimilarResources from './SimilarResources'
import Helmet from 'react-helmet'
import ResourceHeader from './ResourceHeader'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const ResourceCore = props => {
    const { t } = useTranslation()
    const classes = useStyles()

    const [data, setData] = useState({})
    const pathParts = props.history.location.pathname.split('/')
    const resourceId =
        props.resourceId || (pathParts.length > 2 ? pathParts[2] : '')
    if (!resourceId) return null
    const { type } = props
    useEffect(() => {
        if (props.isAuthed) {
            props.setData({
                [type === 'goal'
                    ? 'currentGoal'
                    : type === 'reward'
                    ? 'currentReward'
                    : 'currentActivity']: resourceId,
            })
            if (props.account._id)
                wsActions.addRecent(props.account._id, resourceId, props.type)
        }
        // else {
        //     pullData(props, data, setData, { forceAuth: false }, data => {
        //         const friendData = {}
        //         for (let friend of data.friendData)
        //             friendData[friend._id] = friend
        //         const activityData = {}
        //         if (data.activityData)
        //             for (let activity of data.activityData)
        //                 activityData[activity._id] = activity
        //         const rewardData = {}
        //         if (data.rewardData)
        //             for (let reward of data.rewardData)
        //                 rewardData[reward._id] = reward
        //         const post = {}
        //         if (data.posts)
        //             for (let item of data.posts) post[item._id] = item

        //         props.setData({
        //             friendData,
        //             post,
        //             account: {},
        //             activityData,
        //             rewardData,
        //             onlineUsers: data.onlineUsers,
        //             [type === 'goal' ? 'progress' : type]: data.resource,
        //         })
        //     })
        // }
    }, [props.isAuthed, props.history.location.pathname])

    const { resource, account, friendData, posts } = props

    useEffect(() => {
        if (resource && resource._id) props.setResource(resource)
    }, [resource])
    useEffect(() => {
        props.setPosts(posts)
    }, [posts])

    if (!resource || !resource._id) return null

    props.setData({ currentPosts: resource.posts })

    return friendData ? (
        props.resourceId ? (
            <Layout noHeader>
                <div className="contentDiv">
                    <ResourceHeader
                        account={account}
                        resource={resource}
                        friendData={friendData}
                        type={type}
                        navi={props.navi}
                    />

                    {props.children}
                </div>
            </Layout>
        ) : (
            <Layout
                header={
                    <Typography
                        variant="h6"
                        className={classes.title}
                        color="inherit"
                        noWrap
                    >
                        {resource.name}
                    </Typography>
                }
            >
                <div className="contentDiv">
                    <Helmet>
                        <title>
                            {`${resource.name} ${t('g.by')}${
                                resource.owner
                            } - Addspire`}
                        </title>
                        <meta
                            name="description"
                            content={resource.descriptionText}
                        />
                    </Helmet>
                    <ResourceHeader
                        account={account}
                        resource={resource}
                        friendData={friendData}
                        type={type}
                        navi={props.navi}
                    />

                    {props.children}
                </div>
            </Layout>
        )
    ) : null
}
const mapStateToProps = (state, ownProps) => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        posts: state.post,
        resource:
            state[ownProps.type === 'goal' ? 'progress' : ownProps.type][
                ownProps.resourceId
            ],
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(ResourceCore)
)
