import React from 'react'
import Masonry from 'react-masonry-css'
import {
    cardBreakPoints,
    friendsBreakPoints,
} from '../utils/masonryBreakPoints'

export default props => (
    <div style={{ ...props.style }}>
        <Masonry
            breakpointCols={3}
            className="my-masonry-grid"
            columnClassName="my-masonry-grid_column"
            breakpointCols={
                props.friends ? friendsBreakPoints : cardBreakPoints
            }
        >
            {props.children}
        </Masonry>
    </div>
)
