import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import Button from 'antd/es/button'
import message from 'antd/es/message'

import Input from 'antd/es/input'
import Form from 'antd/es/form'
import { withRouter } from 'react-router-dom'

import { nicknameRules, accountNameRules } from '../../constants/fieldRules'
import * as actions from '../../store/actions'

import { createStyles, makeStyles, Typography } from '@material-ui/core'
import axios from 'axios'
import { useTranslation } from 'react-i18next'
import Layout from '../../components/Layout/Layout'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)
const ActivateAccount = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    if (!props.isAuthed) props.history.push('/logout')
    useEffect(() => {
        if (!props.accountNotActive) props.history.push('/settings')
    }, [props.accountNotActive])
    const onFinish = value => {
        axios
            .post('/api/activate-account', {
                ...value,
            })
            .then(res => {
                if (res.data.success) {
                    message.success(t('activate.accountActivated'))
                    props.setAccountActivated(false)
                } else {
                    if (res.data.nicknameNotUnique) {
                        message.warning(t('activate.accountTaken'))
                    }
                    if (res.data.errorCode) {
                        message.error(t('activate.wrongData'))
                    }
                }
            })
            .catch(err => {})
    }

    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    }
    const tailLayout = {
        wrapperCol: { offset: 8, span: 16 },
    }

    return (
        <Layout
            header={
                <Typography
                    variant="h6"
                    className={classes.title}
                    color="inherit"
                    noWrap
                >
                    {t('activate.activateAccount')}
                </Typography>
            }
        >
            <Typography variant="h4" align="center">
                Addspire
            </Typography>
            <Typography variant="h4" align="center">
                {t('activate.accountActivation')}
            </Typography>
            <Form
                name="dynamic_form_nest_item"
                onFinish={onFinish}
                autoComplete="off"
                {...layout}
                style={{
                    maxWidth: '1200px',
                    margin: '50px auto 0px',
                    padding: '15px',
                    width: '90%',
                }}
            >
                <Form.Item
                    label={t('g.name')}
                    name="name"
                    rules={accountNameRules}
                >
                    <Input placeholder={t('activate.minLength')} />
                </Form.Item>
                <Form.Item
                    label={t('activate.nickname')}
                    name="nickname"
                    rules={nicknameRules}
                >
                    <Input placeholder={t('activate.minLength')} />
                </Form.Item>
                <Form.Item {...tailLayout}>
                    <Button type="primary" htmlType="submit" size="large">
                        {t('g.save')}
                    </Button>
                </Form.Item>
            </Form>
        </Layout>
    )
}

const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        user: state.user,
        account: state.account,
        accountNotActive: state.accountNotActive,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        setAccountActivated: value =>
            dispatch(actions.setAccountActivated(value)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(ActivateAccount)
)
