import { Chip } from '@material-ui/core'
import React from 'react'
import { useTranslation } from 'react-i18next'
import goalCategories from '../../constants/goalCategories'
const GoalCategories = props => {
    const { t } = useTranslation()
    const handleClick = category => {
        let value = props.value ? [...props.value] : []
        if (value.includes(category))
            props.onChange(value.filter(item => item !== category))
        else {
            if (!props.nolim) while (value.length > 4) value.pop()
            props.onChange([...value, category])
        }
    }
    return (
        <>
            {goalCategories.map(category => (
                <Chip
                    label={t('g.' + category)}
                    onClick={() => handleClick(category)}
                    color={
                        props.value && props.value.includes(category)
                            ? 'primary'
                            : 'default'
                    }
                    style={{ margin: '5px' }}
                />
            ))}
        </>
    )
}

export default GoalCategories
