import React, { Component } from 'react'

import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import message from 'antd/es/message'
import getListOfNeededResources from './methods/getListOfNeededResources'
import compareVersionsAndRerequest from './methods/compareVersionsAndRerequest'
import getResourcesToMonitor from './methods/getResourcesToMonitor'
import Cookies from 'universal-cookie'
import axios from 'axios'
import i18n from '../../translations/i18n'

export let webSocket = false
const cookies = new Cookies()
const listOfGroups = [
    'user',
    'account',
    'progress',
    'reward',
    'activity',
    'post',
    'postData',
    'progressData',
    'friendData',
    'transactionData',
    'rewardData',
    'activityData',
]
class ConnectWebsocket extends Component {
    state = {
        user: [],
        account: [],
        progress: [],
        reward: [],
        activity: [],
        post: [],
        friendData: [],
        progressData: [],
        postData: [],
        transactionData: [],
        rewardData: [],
        activityData: [],
        notFoundResources: [],
    }
    markNotFoundResource = ids => {
        const newNotFound = [...this.state.notFoundResources, ...ids]
        this.setState({ notFoundResources: newNotFound })
    }
    start = () => {
        let url
        if (process.env.NODE_ENV === 'development') {
            url = 'ws://my.websiter.test:5000'
        } else {
            url = 'wss://addspire.com'
        }
        let pingTimeout
        webSocket = new WebSocket(url)

        const heartbeat = versions => {
            let notNeededResources
            if (versions) {
                notNeededResources = compareVersionsAndRerequest(
                    versions,
                    this.state,
                    this.props.store,
                    webSocket
                )
            }
            clearTimeout(pingTimeout)

            pingTimeout = setTimeout(() => {
                webSocket.close()
            }, 30000 + 1000)
            if (webSocket && webSocket.readyState === 1) {
                webSocket.send(
                    JSON.stringify({
                        messageCode: 'heartbeat',
                        notNeededResources,
                    })
                )
            }
        }

        webSocket.onopen = event => {
            const resourcesToMonitor = getResourcesToMonitor(this.props)
            webSocket.send(
                JSON.stringify({
                    messageCode: 'auth',
                    user: axios.defaults.headers.common['x-auth-token'],
                    resourcesToMonitor,
                    // progressId: this.props.progress._id,
                })
            )
            this.initializeRequestResources(this.props)
            heartbeat()
        }

        webSocket.onmessage = event => {
            const news = JSON.parse(event.data)

            switch (news.messageCode) {
                case 'heartbeat':
                    heartbeat(news.versions)
                    this.props.setData({
                        onlineUsers: news.onlineUsers,
                    })
                    break
                case 'logout':
                    this.props.logout()
                    this.props.history.push('/logout')
                    break

                case 'successMessage':
                    message.success(i18n.t('processRes.success'))
                    break
                case 'errorMessage':
                    message.error(news.messageText)
                    break
                case 'friendsData':
                    this.props.setFriendsData(news.friendsData)
                    break
                case 'redirectToAccount':
                    this.props.history.push(
                        '/profile/' + this.props.store.account._id
                    )
                    break
                case 'addResource':
                    this.props.saveResource(
                        news.resources,
                        news.type,
                        news.newOnlineUsers
                    )
                    break
                case 'updateResource':
                    this.props.updateResource(
                        news.code,
                        news.id,
                        news.update,
                        this.state
                    )
                    break
                case 'friendSearchResult':
                    this.props.setFriendSearchResult(news.data)
                    break
                case 'goTo':
                    this.props.history.push(news.messageText || '/')
                    break
                case '404':
                    message.error(i18n.t('processRes.404'))
                    this.props.history.push(news.messageText || '/')
                    break

                // case 'update':
                //     let newProgress
                //     let oldProgress = cloneDeep(this.props.progress)
                //     if (oldProgress.__v === news.progress.__v - 1) {
                //         newProgress = diffpatcher.patch(
                //             oldProgress,
                //             news.progress.patch
                //         )
                //         newProgress.__v = news.progress.__v
                //         this.props.setData(
                //             {
                //                 progress: newProgress,
                //             },
                //             this.props.history.location.pathname
                //         )
                //     }
                //     wsActions.getFriendsData(
                //         newProgress || this.props.progress,
                //         this.props.account.friends
                //     )
                //     break
                case 'authSuccess':
                    axios.defaults.headers.common['x-auth-token'] &&
                        cookies.set(
                            'auth_token',
                            axios.defaults.headers.common['x-auth-token'],
                            {
                                path: '/',
                                expires: new Date(
                                    new Date().getTime() +
                                        6 * 24 * 60 * 60 * 1000
                                ),
                            }
                        )
                    break
                case 'structureSaved':
                    this.props.structureIsSaved()
                    break
                case 'error':
                    message.error(i18n.t('processRes.error'))
                    break
                case 'notFoundResource':
                    this.markNotFoundResource(news._id)
                    break
                case 'addToStructure':
                    this.props.addToStructure(
                        news.accountId,
                        news.structureId,
                        news.resourceId
                    )
                    break
                default:
                    break
            }
        }
        webSocket.onclose = event => {
            clearTimeout(pingTimeout)
        }
    }

    checkInterval = null

    check = () => {
        if (!webSocket || webSocket.readyState === 3) {
            clearInterval(this.checkInterval)
            this.checkInterval = setInterval(this.check, 5000)
            this.start()
        }
    }
    // componentWillMount() {
    //     this.checkInterval = setInterval(this.check, 50)
    // }

    reRequestResourcesInterval = null
    reRequestResources = () => {
        for (let group of listOfGroups) {
            if (this.state[group].length > 0) {
                if (webSocket && webSocket.readyState === 1) {
                    webSocket.send(
                        JSON.stringify({
                            messageCode: 'requestResource',
                            ids: this.state[group],
                            type: group,
                        })
                    )
                }
            }
        }
    }

    componentWillUnmount() {
        clearInterval(this.checkInterval)
        if (webSocket) {
            webSocket.close()
        }
        this.props.setConnectingToWS(false)
    }
    componentDidMount() {
        this.checkInterval = setInterval(this.check, 50)
        this.props.setConnectingToWS(true)
    }
    componentDidUpdate(prevProps, prevState) {
        if (prevProps.store.currentAccount !== this.props.store.currentAccount)
            if (webSocket && webSocket.readyState === 1) {
                webSocket.send(
                    JSON.stringify({
                        messageCode: 'currentAccount',
                        id: this.props.store.currentAccount,
                    })
                )
            }
    }
    // componentWillReceiveProps(nextProps) {
    //     this.initializeRequestResources(nextProps)
    // }
    // componentDidUpdate() {
    //     this.initializeRequestResources(this.props)
    // }
    initializeRequestResources = nextProps => {
        const newRequestedResources = getListOfNeededResources(
            nextProps,
            this.state.notFoundResources
        )
        for (let group of listOfGroups) {
            if (newRequestedResources[group].length > 0) {
                if (webSocket && webSocket.readyState === 1) {
                    webSocket.send(
                        JSON.stringify({
                            messageCode: 'requestResource',
                            ids: newRequestedResources[group].filter(
                                item => !this.state[group].includes(item)
                            ),
                            type: group,
                        })
                    )
                }
            }
        }
        this.setState(() => newRequestedResources)
        clearInterval(this.reRequestResourcesInterval)
        this.reRequestResourcesInterval = setInterval(
            this.reRequestResources,
            3000
        )
    }

    static getDerivedStateFromProps(nextProps, state) {
        const newRequestedResources = getListOfNeededResources(
            nextProps,
            state.notFoundResources
        )
        for (let group of listOfGroups) {
            if (newRequestedResources[group].length > 0) {
                if (webSocket && webSocket.readyState === 1) {
                    webSocket.send(
                        JSON.stringify({
                            messageCode: 'requestResource',
                            ids: newRequestedResources[group].filter(
                                item => !state[group].includes(item)
                            ),
                            type: group,
                        })
                    )
                }
            }
        }
        // clearInterval(this.reRequestResourcesInterval)
        // this.reRequestResourcesInterval = setInterval(
        //     this.reRequestResources,
        //     3000
        // )
        return newRequestedResources
    }
    render() {
        return null
    }
}
const mapStateToProps = state => {
    return {
        store: state,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: (data, pulledFrom) =>
            dispatch(actions.setData(data, pulledFrom)),
        removeData: data => dispatch(actions.removeData(data)),
        saveResource: (data, type, newOnlineUsers) =>
            dispatch(actions.saveResource(data, type, newOnlineUsers)),
        setFriendsData: data => dispatch(actions.setFriendsData(data)),

        updateResource: (code, id, update, state) =>
            dispatch(actions.updateResource(code, id, update, state)),
        setFriendSearchResult: data =>
            dispatch(actions.setFriendSearchResult(data)),
        setConnectingToWS: data => dispatch(actions.setConnectingToWS(data)),
        addToStructure: (accountId, structureId, resourceId) =>
            dispatch(
                actions.addToStructure(accountId, structureId, resourceId)
            ),
        logout: () => dispatch(actions.logout()),
        structureIsSaved: () => dispatch(actions.structureIsSaved()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(ConnectWebsocket)
)
