import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'

import ReadMore from '../../components/UI/ReadMore'
import {
    AppBar,
    Box,
    makeStyles,
    Paper,
    Tab,
    Tabs,
    Typography,
} from '@material-ui/core'

import Rewards from '../../components/Rewards/Rewards'
import Activities from '../../components/Activities/Activities'
import { useTranslation } from 'react-i18next'
import ResourceCore from '../../components/ResourceCore'
import MapPosition from '../../components/MapPosition/MapPosition'
import SimilarResources from '../../components/SimilarResources'
import ResourcePosts from '../../components/Post/ResourcePosts'
import ImageCarousel from '../../components/UI/ImageCarousel/ImageCarousel'

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: 13,
        margin: '30px 15px 10px',
        textTransform: 'uppercase',
        fontWeight: 600,
    },
}))

function TabPanel(props) {
    const { children, value, index, ...other } = props

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`full-width-tabpanel-${index}`}
            aria-labelledby={`full-width-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    )
}
function a11yProps(index) {
    return {
        id: `scrollable-auto-tab-${index}`,
        'aria-controls': `scrollable-auto-tabpanel-${index}`,
    }
}
const Goal = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [progress, setProgress] = useState(null)
    const [posts, setPosts] = useState([])

    const [currentTab, setCurrentTab] = useState(1)
    const [currentTabD, setCurrentTabD] = useState(0)

    return (
        <ResourceCore
            type="goal"
            setResource={setProgress}
            setPosts={setPosts}
            navi={
                <Tabs
                    value={currentTab}
                    onChange={(event, value) => setCurrentTab(value)}
                    aria-label="simple tabs example"
                    indicatorColor="primary"
                    textColor="primary"
                >
                    <Tab label={t('g.desc')} {...a11yProps(0)} />
                    <Tab label={t('g.posts')} {...a11yProps(1)} />
                    <Tab label={t('g.tracking')} {...a11yProps(2)} />
                </Tabs>
            }
            resourceId={props.id}
        >
            {progress && progress._id && (
                <>
                    <TabPanel value={currentTab} index={0}>
                        <Typography
                            className={classes.title}
                            color="textSecondary"
                            gutterBottom
                        >
                            {t('g.detailsFor')}
                        </Typography>
                        <Paper square>
                            <Tabs
                                value={currentTabD}
                                onChange={(event, value) =>
                                    setCurrentTabD(value)
                                }
                                indicatorColor="primary"
                                textColor="primary"
                            >
                                <Tab label={t('g.fromStart')} />
                                <Tab label={t('g.toGoal')} />
                                <Tab label={t('g.resultFinish')} />
                            </Tabs>
                        </Paper>

                        {progress[
                            currentTabD === 0
                                ? 'startImages'
                                : currentTabD === 1
                                ? 'images'
                                : 'finishImages'
                        ].length > 0 && (
                            <>
                                <Typography
                                    className={classes.title}
                                    color="textSecondary"
                                    gutterBottom
                                >
                                    {t('g.images')}
                                </Typography>
                                <ImageCarousel
                                    images={
                                        progress[
                                            currentTabD === 0
                                                ? 'startImages'
                                                : currentTabD === 1
                                                ? 'images'
                                                : 'finishImages'
                                        ]
                                    }
                                />
                            </>
                        )}

                        <Typography
                            className={classes.title}
                            color="textSecondary"
                            gutterBottom
                        >
                            {t('g.desc')}
                        </Typography>
                        <Paper style={{ padding: '15px' }}>
                            <ReadMore
                                initialHeight={200}
                                contentEditor={
                                    progress[
                                        currentTabD === 0
                                            ? 'startDescription'
                                            : currentTabD === 1
                                            ? 'description'
                                            : 'finishDescription'
                                    ]
                                }
                            />
                        </Paper>

                        <Typography
                            className={classes.title}
                            color="textSecondary"
                            gutterBottom
                        >
                            {t('g.position')}
                        </Typography>
                        <Paper style={{ padding: '15px' }}>
                            <MapPosition position={progress.position} />
                        </Paper>
                    </TabPanel>
                    <TabPanel value={currentTab} index={1}>
                        {/* <SimilarResources resource={progress} type="goal" /> */}

                        <ResourcePosts
                            resource={progress}
                            posts={posts}
                            type="goal"
                        />
                    </TabPanel>
                    <TabPanel value={currentTab} index={2}>
                        <Activities progress={progress} />
                        <Rewards progress={progress} />
                    </TabPanel>
                </>
            )}
        </ResourceCore>
    )
}

export default withRouter(Goal)
