import React from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import isEqual from 'lodash.isequal'
const NotificationsTracker = props => {
    const { account, friendData, progressData } = props

    const sortNotifiacations = (a, b) =>
        new Date(a.date).getTime() < new Date(b.date).getTime() ? 1 : -1

    if (!account._id) return null

    let lastNotifications = []
    for (let progress of account.followProgresses) {
        if (progressData[progress])
            lastNotifications = [
                ...lastNotifications,
                ...progressData[progress].notifications,
            ]
    }

    for (let friend of account.friends) {
        if (
            friendData[friend.friend] &&
            friend.status === 'friend' &&
            friendData[friend.friend].notifications
        )
            lastNotifications = [
                ...lastNotifications,
                ...friendData[friend.friend].notifications,
            ]
    }
    const filterSameNot = (res, not, index, arr) => {
        if (!res.find(item => item.notId === not.notId)) {
            res.push(not)
        }
        return res
    }
    lastNotifications = lastNotifications.reduce(filterSameNot, [])

    lastNotifications = lastNotifications.sort(sortNotifiacations).slice(0, 100)

    lastNotifications = [
        ...lastNotifications,
        ...account.myNotifications,
    ].filter(item => item.code !== 'add post')

    lastNotifications = lastNotifications.reduce(filterSameNot, [])
    lastNotifications = lastNotifications.sort(sortNotifiacations)
    if (!isEqual(lastNotifications, props.lastNotifications)) {
        props.setData({ lastNotifications })
    }

    return <span />
}
const mapStateToProps = state => {
    return {
        isAuthed: state.isAuthed,
        account: state.account,
        statePosts: state.post,
        progress: state.progress,
        friendData: state.friendData,
        progressData: state.progressData,
        lastNotifications: state.lastNotifications,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => {
            dispatch(actions.setData(data))
        },
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(NotificationsTracker)
)
