import dayjs from 'dayjs'
dayjs().format()
export default activityObj => {
    let lastStages = activityObj.stages
        .slice(Math.max(activityObj.stages.length - 7, 0))
        .sort((a, b) => {
            let aS, aF, bS, bF
            // return -1
            if (a.old && !b.old) return 1
            else if (b.old && !a.old) return -1
            else {
                if (a.day) {
                    aS = dayjs()
                        .year(a.year)
                        .dayOfYear(a.day)
                        .startOf('day')
                    aF = dayjs()
                        .year(a.year)
                        .dayOfYear(a.day)
                        .endOf('day')
                    bS = dayjs()
                        .year(b.year)
                        .dayOfYear(b.day)
                        .startOf('day')
                    bF = dayjs()
                        .year(b.year)
                        .dayOfYear(b.day)
                        .endOf('day')
                } else if (a.week) {
                    aS = dayjs()
                        .year(a.year)
                        .week(a.week)
                        .startOf('week')
                    aF = dayjs()
                        .year(a.year)
                        .week(a.week)
                        .endOf('week')
                    bS = dayjs()
                        .year(b.year)
                        .week(b.week)
                        .startOf('week')
                    bF = dayjs()
                        .year(b.year)
                        .week(b.week)
                        .endOf('week')
                } else if (a.month) {
                    aS = dayjs()
                        .year(a.year)
                        .month(a.month)
                        .startOf('month')
                    aF = dayjs()
                        .year(a.year)
                        .month(a.month)
                        .endOf('month')
                    bS = dayjs()
                        .year(b.year)
                        .month(b.month)
                        .startOf('month')
                    bF = dayjs()
                        .year(b.year)
                        .month(b.month)
                        .endOf('month')
                } else if (a.year) {
                    aS = dayjs()
                        .year(a.year)
                        .startOf('year')
                    aF = dayjs()
                        .year(a.year)
                        .endOf('year')
                    bS = dayjs()
                        .year(b.year)
                        .startOf('year')
                    bF = dayjs()
                        .year(b.year)
                        .endOf('year')
                }
                if (aS) {
                    const now = dayjs()

                    if (
                        Math.min(
                            Math.abs(now.diff(aS)),
                            Math.abs(now.diff(aF))
                        ) <=
                        Math.min(Math.abs(now.diff(bS)), Math.abs(now.diff(bF)))
                    )
                        return -1
                    else return 1
                }
            }
            return 0
        })

    return lastStages.length > 0 ? lastStages[0].stageId : 0
}
