import React, { useEffect, useState, useRef } from 'react'

import { CircleMarker, Map, Marker, TileLayer } from 'react-leaflet'
import 'leaflet/dist/leaflet.css'

import * as ELG from 'esri-leaflet-geocoder'
import 'esri-leaflet-geocoder/dist/esri-leaflet-geocoder.css'
import LocateControl from './LocateControl'
import { GestureHandling } from 'leaflet-gesture-handling'
import 'leaflet-gesture-handling/dist/leaflet-gesture-handling.css'
import {
    AppBar,
    createStyles,
    Dialog,
    IconButton,
    makeStyles,
    Slide,
    Theme,
    Toolbar,
    Typography,
    Button,
    Grid,
    Slider,
    MenuItem,
    Select,
    Switch,
} from '@material-ui/core'
import { Close } from '@material-ui/icons'
import 'react-leaflet-fullscreen/dist/styles.css'
import FullscreenControl from 'react-leaflet-fullscreen'
import { useTranslation } from 'react-i18next'

const TransitionComponent = props => <Slide direction="up" {...props} />

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
        },
        input: {
            marginLeft: theme.spacing(1),
            flex: 1,
        },
        iconButton: {
            padding: 10,
        },
        divider: {
            height: 28,
            margin: 4,
        },
        appBar: {
            position: 'fixed',
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
    })
)

const MapSettings = props => {
    const { t } = useTranslation()
    const {
        mapVisible,
        setMapVisible,
        onSearch,
        setSearch,
        search,
        positionChanged,
        setPositionChanged,
    } = props
    const map = useRef(null)
    const [radius, setRadius] = useState(false)
    const [changed, setChanged] = useState(false)

    useEffect(() => {
        if (map && map.current && map.current.leafletElement) {
            new ELG.Geosearch({
                useMapBounds: false,
                placeholder: t('g.mapPlaceholder'),
                title: t('g.mapTitle'),
            }).addTo(map.current.leafletElement)
            new GestureHandling(map.current.leafletElement)
        }
    }, [])
    const [initialPosition, setInitialPosition] = useState(search.position)

    const mapClick = e => {
        setSearch({
            ...search,
            position: [e.latlng.lat, e.latlng.lng],
            withMap: true,
        })
        setPositionChanged(true)
    }
    const mapDragEnd = e => {
        const latlng = e.target.getLatLng()
        setSearch({
            ...search,
            position: [latlng.lat, latlng.lng],
            withMap: true,
        })
        setPositionChanged(true)
    }

    useEffect(() => {
        getRadius()
    }, [props.search])

    useEffect(() => setChanged(false), [mapVisible])

    const getRadius = () => {
        if (map && map.current && map.current.leafletElement) {
            const metresPerPixel =
                (40075016.686 *
                    Math.abs(
                        Math.cos(
                            (map.current.leafletElement.getCenter().lat *
                                Math.PI) /
                                180
                        )
                    )) /
                Math.pow(2, map.current.leafletElement.getZoom() + 8)

            setRadius(
                search.units === 'mi'
                    ? (search.distance * 1609) / metresPerPixel
                    : (search.distance * 1000) / metresPerPixel
            )
        }
    }

    const classes = useStyles()

    return (
        <Dialog
            fullScreen
            open={mapVisible}
            onClose={() => {
                setMapVisible(false)
                if (changed) onSearch(search)
            }}
            TransitionComponent={TransitionComponent}
            scroll="paper"
        >
            <AppBar className={classes.appBar} color="primary">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={() => {
                            setMapVisible(false)
                            if (changed) onSearch(search)
                        }}
                        aria-label="close"
                    >
                        <Close />
                    </IconButton>
                    <Typography
                        variant="h6"
                        className={classes.title}
                        color="inherit"
                    >
                        {t('explore.searchWithLocation')}
                    </Typography>
                    <Button
                        autoFocus
                        color="inherit"
                        onClick={() => {
                            setMapVisible(false)
                            if (changed) onSearch(search)
                        }}
                    >
                        {t('g.done')}
                    </Button>
                </Toolbar>
            </AppBar>
            <Toolbar />

            <Grid
                container
                style={{ padding: '15px', maxWidth: '800px' }}
                alignItems="center"
            >
                <Grid item xs={4} sm={4}>
                    {t('explore.searchWithLocation')}
                </Grid>
                <Grid item xs={8} sm={8}>
                    <Switch
                        checked={search.withMap}
                        color="primary"
                        onChange={e => {
                            setSearch({
                                ...search,
                                withMap: e.target.checked,
                            })
                            setChanged(true)
                        }}
                    />
                </Grid>
                <Grid item xs={4}>
                    {t('g.distance')}
                </Grid>
                <Grid item xs={8}>
                    <Slider
                        valueLabelDisplay="auto"
                        min={1}
                        max={200}
                        onChange={(e, distance) => {
                            setSearch({ ...search, distance })
                            setChanged(true)
                        }}
                        value={search.distance}
                    />
                </Grid>
                <Grid item xs={4}>
                    {t('g.units')}
                </Grid>
                <Grid item xs={8}>
                    <Select
                        value={search.units}
                        onChange={event => {
                            setSearch({
                                ...search,
                                units: event.target.value,
                            })
                            setChanged(true)
                        }}
                    >
                        <MenuItem value="mi">{t('g.miles')}</MenuItem>
                        <MenuItem value="km">{t('g.km')}</MenuItem>
                    </Select>
                </Grid>
            </Grid>

            <div
                style={{
                    position: 'relative',
                    minHeight: '300px',
                    flex: 1,
                    marginTop: '30px',
                    overflow: 'hidden',
                }}
            >
                <Map
                    whenReady={() => getRadius()}
                    ref={map}
                    center={initialPosition}
                    zoom={8}
                    style={{
                        width: '100%',
                        height: '100%',
                        position: 'relative',
                        zIndex: 1,
                    }}
                    onClick={mapClick}
                    gestureHandling={true}
                    onzoomend={() => getRadius()}
                    onlocationfound={e => {
                        if (!positionChanged) {
                            mapClick(e)
                            setInitialPosition([e.latlng.lat, e.latlng.lng])
                        }
                    }}
                >
                    <TileLayer
                        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <LocateControl />
                    <CircleMarker center={search.position} radius={radius} />
                    <Marker
                        position={search.position}
                        draggable
                        ondragend={mapDragEnd}
                    />
                    <FullscreenControl position="topright" />
                </Map>
            </div>
        </Dialog>
    )
}

export default MapSettings
