import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import * as wsActions from '../../websocketActions'

import FriendCard from '../../components/FriendCard'
import {
    Container,
    createStyles,
    IconButton,
    InputBase,
    makeStyles,
    Paper,
    Typography,
} from '@material-ui/core'
import { SearchOutlined } from '@material-ui/icons'
import { useTranslation } from 'react-i18next'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        root: {
            padding: '2px 4px',
            display: 'flex',
            alignItems: 'center',
            maxWidth: 400,
            width: '100%',
            margin: '30px auto',
        },
        input: {
            marginLeft: theme.spacing(1),
            flex: 1,
            margin: 'auto',
        },
        iconButton: {
            padding: 10,
        },
    })
)
const AddFriends = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    useEffect(() => {
        props.setFriendSearchResult({})
        setSearchField('')
    }, [props.open])
    const [searchField, setSearchField] = useState('')
    const { account, friendData, searchResult } = props

    if (!account._id) return null
    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []

    if (account.friends) {
        for (let friend of account.friends) {
            const fullFriend = { ...friendData[friend.friend], ...friend }
            if (fullFriend) {
                if (fullFriend.status === 'friend') realFriends.push(fullFriend)
                else if (fullFriend.status === 'invited')
                    invitedFriends.push(fullFriend)
                else if (fullFriend.status === 'inviting')
                    invitingFriends.push(fullFriend)
            }
        }
    }

    return (
        <Container style={{ paddingTop: '20px', maxWidth: '800px' }}>
            <Typography variant="body2" align="center">
                {t('friends.instructions')}
            </Typography>
            <Paper className={classes.root}>
                <InputBase
                    className={classes.input}
                    placeholder={t('friends.searchFriends')}
                    onChange={e => setSearchField(e.target.value)}
                />
                <IconButton
                    className={classes.iconButton}
                    onClick={() => wsActions.searchFriends(searchField)}
                >
                    <SearchOutlined />
                </IconButton>
            </Paper>
            {searchResult.length > 0 &&
                searchResult.map(friend => (
                    <FriendCard
                        {...props}
                        friend={friend}
                        realFriends={realFriends}
                        invitingFriends={invitingFriends}
                        invitedFriends={invitedFriends}
                    />
                ))}
        </Container>
    )
}

const mapStateToProps = state => {
    return {
        searchResult: state.friendSearchResult,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setFriendSearchResult: data =>
            dispatch(actions.setFriendSearchResult(data)),
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(AddFriends)
