import React, { useEffect, useState } from 'react'
import { Link, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as wsActions from '../../websocketActions'
import HorizontalScrollList from '../UI/HorizontalScrollList'
import {
    Box,
    CardActionArea,
    CardContent,
    CardMedia,
    Dialog,
    DialogActions,
    DialogContent,
    IconButton,
    Slide,
    Typography,
} from '@material-ui/core'
import { getPH, placeholderImages } from '../MasonryCard'
import AddEditActivity from './AddEditActivity'
import Reward from '../../containers/Reward/Reward'
import { Close } from '@material-ui/icons'
import { useTranslation } from 'react-i18next'
import EditResource from '../EditResource/EditResource'
const TransitionComponent = props => <Slide direction="up" {...props} />

const ProfileRewards = props => {
    const { t } = useTranslation()
    const [addActivityOpen, setAddActivityOpen] = useState(false)
    const [activityPreview, setActivityPreview] = useState('')

    const {
        account,
        owner,
        activityData,
        wish,
        additionalButton,
        isSelect,
        value,
        onChange,
        activities,
        multiple,
    } = props
    const isOwner = account && account._id && account._id === owner._id
    const handleSelect = id => {
        if (multiple)
            if (value.includes(id)) {
                onChange(value.filter(item => item !== id))
            } else {
                onChange([...value, id])
            }
        else onChange([id])
    }
    return (
        <>
            <HorizontalScrollList
                title={isSelect ? t('g.forTheseActivities') : t('g.activities')}
                items={(activities || owner.activities)
                    .map(item => activityData[item])
                    .filter(
                        item => item && Boolean(wish) === Boolean(item.wish)
                    )}
                content={item => {
                    const isSelected = isSelect && value.includes(item._id)
                    const content = (
                        <>
                            {' '}
                            <CardMedia
                                style={{
                                    height: '150px',
                                    backgroundSize: 'contain',
                                }}
                                image={
                                    item.images.length > 0
                                        ? item.images[0]
                                        : placeholderImages[getPH(item._id)]
                                }
                                title={item.name}
                            />
                            <CardContent style={{ padding: '5px' }}>
                                <Typography
                                    variant="body2"
                                    noWrap
                                    color={isSelected ? 'primary' : undefined}
                                    style={{
                                        fontWeight: isSelected
                                            ? 600
                                            : 'initial',
                                    }}
                                >
                                    {item.name}
                                </Typography>
                            </CardContent>
                        </>
                    )
                    return (
                        <CardContent
                            style={{
                                textAlign: 'center',
                                position: 'relative',
                                padding: '5px',
                            }}
                        >
                            <CardActionArea
                                onClick={() =>
                                    isSelect && handleSelect(item._id)
                                }
                            >
                                {isSelect ? (
                                    content
                                ) : props.openInDialog ? (
                                    <Box
                                        onClick={() =>
                                            setActivityPreview(item._id)
                                        }
                                    >
                                        {content}
                                    </Box>
                                ) : (
                                    <Link to={'/activities/' + item._id}>
                                        {content}
                                    </Link>
                                )}
                            </CardActionArea>
                            {additionalButton && additionalButton(item)}
                        </CardContent>
                    )
                }}
                withAdd={isOwner}
                addTitle={t('g.addItem')}
                onAdd={() => setAddActivityOpen(true)}
            />
            {isOwner && (
                <Dialog
                    fullScreen
                    open={addActivityOpen}
                    onClose={() => setAddActivityOpen(false)}
                    TransitionComponent={TransitionComponent}
                    scroll="paper"
                >
                    <EditResource
                        type="activity"
                        account={account}
                        friendData={props.friendData}
                        cancel={() => setAddActivityOpen(false)}
                    />
                </Dialog>
            )}
            {props.openInDialog && (
                <Dialog
                    open={Boolean(activityPreview)}
                    onClose={() => setActivityPreview('')}
                    fullWidth
                    maxWidth="md"
                    scroll="paper"
                >
                    <DialogActions>
                        <IconButton onClick={() => setActivityPreview('')}>
                            <Close />
                        </IconButton>
                    </DialogActions>
                    <DialogContent>
                        {activityPreview && (
                            <Reward activityId={activityPreview} />
                        )}
                    </DialogContent>
                </Dialog>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        friendData: state.friendData,
        activityData: state.activityData,
    }
}

export default withRouter(connect(mapStateToProps)(ProfileRewards))
