import { webSocket } from '../containers/ConnectWebsocket/ConnectWebsocket'

export const saveActivity = (accountId, activity) => {
    if (webSocket && webSocket.readyState === 1) {
        webSocket.send(
            JSON.stringify({
                messageCode: 'saveActivity',
                accountId,
                activity,
            })
        )
    }
}

