import requestResource from './requestResource'

export default (versions, state, store, webSocket) => {
    const notNeededResources = {
        user: [],
        account: [],
        progress: [],
        post: [],
        reward: [],
        activity: [],
        progressData: [],
        friendData: [],
        postData: [],
        transactionData: [],
        rewardData: [],
        activityData: [],
    }

    const checkData = key => {
        for (let attr in versions[key]) {
            if (typeof versions[key][attr] !== 'undefined') {
                if (store[key][attr]) {
                    if (store[key][attr].__v !== versions[key][attr]) {
                        requestResource(key, [attr], state)
                    }
                } else notNeededResources[key].push(attr)
            }
        }
    }

    const check = key => {
        for (let attr in versions[key]) {
            if (typeof versions[key][attr] !== 'undefined') {
                if (store[key]._id === attr) {
                    if (store[key].__v !== versions[key][attr]) {
                        requestResource(key, [attr], state)
                    }
                    if (!store[key]._id) notNeededResources[key].push(attr)
                } else notNeededResources[key].push(attr)
            }
        }
    }

    for (let attr in versions.user) {
        if (typeof versions.user[attr] !== 'undefined') {
            if (store.user._id === attr) {
                if (store.user.__v !== versions.user[attr]) {
                    requestResource('user', [attr], state)
                }
            } else notNeededResources.user.push(attr)
        }
    }

    for (let attr in versions.account) {
        if (typeof versions.account[attr] !== 'undefined') {
            if (store.account._id === attr) {
                if (store.account.__v !== versions.account[attr]) {
                    requestResource('account', [attr], state)
                }
            }
            if (store.profile._id === attr) {
                if (store.profile.__v !== versions.account[attr]) {
                    requestResource('account', [attr], state)
                }
                if (!store.account._id) notNeededResources.account.push(attr)
            }
        }
        if (store.account._id !== attr && store.profile._id !== attr)
            notNeededResources.account.push(attr)
    }

    checkData('progress')
    checkData('reward')
    checkData('activity')

    checkData('post')
    checkData('friendData')
    checkData('progressData')
    checkData('rewardData')
    checkData('activityData')
    checkData('postData')
    checkData('transactionData')

    if (
        notNeededResources.user.length > 0 ||
        notNeededResources.account.length > 0 ||
        notNeededResources.progress.length > 0 ||
        notNeededResources.post.length > 0 ||
        notNeededResources.friendData.length > 0 ||
        notNeededResources.reward.length > 0 ||
        notNeededResources.rewardData.length > 0 ||
        notNeededResources.activity.length > 0 ||
        notNeededResources.activityData.length > 0 ||
        notNeededResources.progressData.length > 0 ||
        notNeededResources.postData.length > 0 ||
        notNeededResources.transactionData.length > 0
    ) {
        return notNeededResources
    }
}
