import React, { useState } from 'react'
import {
    Box,
    Button,
    Chip,
    createMuiTheme,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    Divider,
    fade,
    IconButton,
    Menu,
    MenuItem,
    MuiThemeProvider,
    Paper,
    Select,
    Slide,
    Typography,
} from '@material-ui/core'
import ImageCarousel from './UI/ImageCarousel/ImageCarousel'
import ShareButton from './UI/ShareButton'
import * as wsActions from '../websocketActions'
import { useTranslation } from 'react-i18next'
import EditResource from '../components/EditResource/EditResource'
import MasonryCard from './MasonryCard'
import FollowButton from './FollowButton'
import UserButton from './UserButton/UserButton'
import { MoreVertOutlined } from '@material-ui/icons'
import ConfirmAction from './UI/ConfirmAction'
import dayjs from 'dayjs'
import 'dayjs/locale/ru'
import { blue, green, orange, red } from '@material-ui/core/colors'

dayjs().format()
const TransitionComponent = props => <Slide direction="up" {...props} />

const redTheme = createMuiTheme({
    palette: { primary: { main: red[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: red[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(red[600], 0.05),
                },
            },
        },
    },
})
const blueTheme = createMuiTheme({
    palette: { primary: { main: blue[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: blue[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(blue[600], 0.05),
                },
            },
        },
    },
})
const greenTheme = createMuiTheme({
    palette: { primary: { main: green[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: green[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(green[600], 0.05),
                },
            },
        },
    },
})
const orangeTheme = createMuiTheme({
    palette: { primary: { main: orange[600] } },
    overrides: {
        MuiSelect: {
            selectMenu: { color: orange[600] },
            select: {
                '&:focus': {
                    backgroundColor: fade(orange[600], 0.05),
                },
            },
        },
    },
})

const ResourceHeader = props => {
    const { t } = useTranslation()
    const { resource, account, friendData, type } = props
    const [followersVisible, setFollowersVisible] = useState('')
    const [editResourceOpen, setEditResourceOpen] = useState(false)
    const [deleteResourceOpen, setDeleteResourceOpen] = useState(false)
    const [leaveResourceOpen, setLeaveResourceOpen] = useState(false)

    const [anchorEl, setAnchorEl] = useState(null)
    const accountProp =
        type === 'goal'
            ? 'progresses'
            : type === 'reward'
            ? 'rewards'
            : 'activities'
    const followProp =
        type === 'goal'
            ? 'followProgresses'
            : type === 'reward'
            ? 'followRewards'
            : 'followActivities'
    const getDropDownMeuItems = () => {
        const items = []
        if (account._id === resource.owner)
            items.push(
                <MenuItem
                    onClick={() => {
                        setEditResourceOpen(true)
                        setAnchorEl(null)
                    }}
                >
                    {t('g.edit')}
                </MenuItem>
            )

        if (account[followProp].includes(resource._id))
            items.push(
                <MenuItem
                    onClick={() => {
                        wsActions.unfollowResource(
                            account._id,
                            resource._id,
                            type
                        )
                        setAnchorEl(null)
                    }}
                >
                    {t('g.unfollow')}
                </MenuItem>
            )

        if (resource.likes.includes(account._id))
            items.push(
                <MenuItem
                    onClick={() => {
                        wsActions.changeLikesResource(
                            account._id,
                            resource._id,
                            false,
                            type
                        )
                        setAnchorEl(null)
                    }}
                >
                    {t('g.unlike')}
                </MenuItem>
            )
        if (
            type !== 'reward' &&
            account[accountProp] &&
            account[accountProp].includes(resource._id.toString())
        )
            items.push(
                <MenuItem
                    onClick={() => {
                        setLeaveResourceOpen(true)
                        setAnchorEl(null)
                    }}
                >
                    {t('g.leave')}
                </MenuItem>
            )
        if (
            account[accountProp] &&
            account[accountProp].includes(resource._id.toString())
        )
            items.push(
                <MenuItem
                    onClick={() => {
                        setDeleteResourceOpen(true)
                        setAnchorEl(null)
                    }}
                >
                    {t('g.delete')}
                </MenuItem>
            )
        return items.length > 0 && items
    }

    const dropDownMeuItems = account._id ? getDropDownMeuItems() : null

    const getStatusText = () => {
        const status = resource.status
        switch (status) {
            case 'fail':
                return t('progress.failed')
            case 'complete':
                return t('progress.completed')
            case 'dismissed':
                return t('progress.dismissed')
            default:
                return t('progress.inProcess')
        }
    }

    const getTag = () => {
        return (
            <MuiThemeProvider
                theme={
                    resource.status === 'process'
                        ? blueTheme
                        : resource.status === 'complete'
                        ? greenTheme
                        : resource.status === 'fail'
                        ? redTheme
                        : orangeTheme
                }
            >
                <Box style={{ margin: '10px 0' }}>
                    {resource.owner === account._id ? (
                        <Select
                            value={resource.status}
                            onChange={e =>
                                wsActions.changeResourceStatus(
                                    e.target.value,
                                    resource._id
                                )
                            }
                        >
                            <MenuItem value="complete">
                                {t('progress.complete')}
                            </MenuItem>
                            <MenuItem value="process">
                                {t('progress.inProcess')}
                            </MenuItem>
                            <MenuItem value="fail">
                                {t('progress.fail')}
                            </MenuItem>
                            <MenuItem value="dismissed">
                                {t('progress.dismiss')}
                            </MenuItem>
                        </Select>
                    ) : (
                        <Chip color="primary" label={getStatusText()} />
                    )}
                </Box>
            </MuiThemeProvider>
        )
    }

    return (
        <>
            <Paper style={{ padding: '15px 15px 0px 15px' }}>
                {resource.images.length > 0 && (
                    <ImageCarousel images={resource.images} />
                )}
                <div style={{ textAlign: 'center', marginTop: '10px' }}>
                    <Typography variant="h4" gutterBottom>
                        {resource.name}
                    </Typography>
                    <Typography variant="body1" gutterBottom>
                        {t('g.' + type) + ' ' + t('g.by')}
                        <UserButton id={resource.owner} />
                    </Typography>
                    {resource.category.map(category => (
                        <Chip
                            label={t('g.' + category)}
                            variant="outlined"
                            style={{ margin: '5px' }}
                        />
                    ))}
                    <Box
                        style={{
                            margin: '5px 0px',
                        }}
                    >
                        <Button
                            onClick={() => setFollowersVisible('followers')}
                            style={{ marginRight: '20px' }}
                        >
                            <span
                                style={{
                                    textTransform: 'none',
                                }}
                            >
                                {`${resource.followingAccounts.length} ${t(
                                    'g.followers'
                                )}`}
                            </span>
                        </Button>
                        <Button onClick={() => setFollowersVisible('likes')}>
                            <span
                                style={{
                                    textTransform: 'none',
                                }}
                            >
                                {`${resource.likes.length} ${t('g.likes')}`}
                            </span>
                        </Button>
                        <ShareButton
                            title={resource.name + t('g.shareText')}
                            media={resource.images && resource.images[0]}
                            item={{
                                progressId: resource._id,
                                progressName: resource.name,
                                type,
                            }}
                            profile={resource.owner}
                            linkHash={resource._id}
                        />
                        {dropDownMeuItems && account && account._id && (
                            <>
                                <IconButton
                                    onClick={event =>
                                        setAnchorEl(event.currentTarget)
                                    }
                                >
                                    <MoreVertOutlined />
                                </IconButton>
                                <Menu
                                    anchorEl={anchorEl}
                                    keepMounted
                                    open={Boolean(anchorEl)}
                                    onClose={() => setAnchorEl(null)}
                                >
                                    {dropDownMeuItems}
                                </Menu>
                            </>
                        )}
                    </Box>
                    <Box
                        style={{
                            margin: '0px 0px 20px',
                        }}
                    >
                        {account &&
                            account._id &&
                            !account[followProp].includes(resource._id) && (
                                <Button
                                    color="primary"
                                    onClick={() =>
                                        wsActions.followResource(
                                            account._id,
                                            resource._id,
                                            type
                                        )
                                    }
                                    style={{ marginRight: '20px' }}
                                    variant="contained"
                                >
                                    {t('g.follow')}
                                </Button>
                            )}

                        {account &&
                            account._id &&
                            !resource.likes.includes(account._id) && (
                                <Button
                                    color="primary"
                                    variant="contained"
                                    onClick={() =>
                                        wsActions.changeLikesResource(
                                            account._id,
                                            resource._id,
                                            true,
                                            type
                                        )
                                    }
                                >
                                    {t('g.like')}
                                </Button>
                            )}
                        {type === 'goal' && (
                            <Box>
                                {getTag()}
                                {resource.startDate && (
                                    <Typography>
                                        {t('progress.startDate')}:{' '}
                                        {dayjs(resource.startDate).format(
                                            'ddd, MMM D, YYYY'
                                        )}
                                    </Typography>
                                )}
                                {resource.dueDate && (
                                    <Typography>
                                        {t('progress.dueDate')}:{' '}
                                        {dayjs(resource.dueDate).format(
                                            'ddd, MMM D, YYYY'
                                        )}
                                    </Typography>
                                )}
                                {resource.status !== 'process' &&
                                    resource.finishDate && (
                                        <Typography>
                                            {t('progress.finishDate')}:{' '}
                                            {dayjs(resource.finishDate).format(
                                                'ddd, MMM D, YYYY'
                                            )}
                                        </Typography>
                                    )}
                            </Box>
                        )}
                    </Box>
                </div>

                <Divider />
                {props.navi}
            </Paper>
            <Dialog
                fullScreen
                open={editResourceOpen}
                onClose={() => setEditResourceOpen(false)}
                TransitionComponent={TransitionComponent}
                scroll="paper"
            >
                <EditResource
                    type={type}
                    account={account}
                    friendData={friendData}
                    cancel={() => setEditResourceOpen(false)}
                    resource={resource}
                />
            </Dialog>
            <Dialog
                open={Boolean(followersVisible)}
                onClose={() => setFollowersVisible('')}
                title={
                    followersVisible === 'likes'
                        ? t('g.' + type + 'Likes')
                        : t('g.' + type + 'Followers')
                }
                fullWidth
                maxWidth="md"
            >
                <DialogTitle>
                    {followersVisible === 'likes'
                        ? t('g.' + type + 'Likes')
                        : t('g.' + type + 'Followers')}
                </DialogTitle>
                <DialogContent>
                    {resource[
                        followersVisible === 'likes'
                            ? 'likes'
                            : 'followingAccounts'
                    ].map(item => (
                        <MasonryCard
                            friends={true}
                            noLink={true}
                            key={item}
                            actions={
                                <FollowButton
                                    account={account}
                                    id={item}
                                    type="account"
                                />
                            }
                            body={
                                <UserButton span={true} id={item} size={50} />
                            }
                        />
                    ))}
                </DialogContent>
                <DialogActions>
                    <Button
                        onClick={() => setFollowersVisible('')}
                        color="primary"
                    >
                        {t('g.close')}
                    </Button>
                </DialogActions>
            </Dialog>
            <ConfirmAction
                open={deleteResourceOpen}
                cancel={() => setDeleteResourceOpen(false)}
                title={t('resource.confirmDelete')}
                confirm={() => wsActions.deleteResource(resource._id, type)}
            />
            <ConfirmAction
                open={leaveResourceOpen}
                cancel={() => setLeaveResourceOpen(false)}
                title={t('resource.confirmLeave')}
                confirm={() => wsActions.leaveResource(resource._id, type)}
            />
        </>
    )
}

export default ResourceHeader
