import requestResource from '../../containers/ConnectWebsocket/methods/requestResource'
import * as actions from './index'
import cloneDeep from 'lodash.clonedeep'

const applyDelta = (source, delta, watchFields) => {
    // console.log('---------------------------APLY DELTA------------------------')
    // console.log('----------------source----------------')
    // console.log(source)
    // console.log('----------------delta-------------------')
    // console.log(delta)
    const result = cloneDeep(source)

    const applyUpdateToPart = (res, del, watchFields) => {
        const applyWithDot = (res, keys, del) => {
            if (keys.length > 1) {
                const key = keys.shift()
                applyWithDot(res[key], keys, del)
            } else {
                if (typeof res === 'array') {
                    res.push(del)
                } else {
                    res[keys[0]] = del
                }
            }
        }
        for (let attr in del) {
            const keys = attr.split('.')
            if (!watchFields || watchFields.includes(keys[0])) {
                if (typeof del[attr] !== 'undefined') {
                    applyWithDot(res, keys, del[attr])
                }
            }
        }
    }

    applyUpdateToPart(result, delta.updatedFields, watchFields)

    const applyDeleteToPart = (result, keys) => {
        if (keys.length > 1) {
            const key = keys.shift()
            applyDeleteToPart(result[key], keys)
        } else {
            if (typeof result === 'object') {
                delete result[keys[0]]
            } else if (typeof result === 'array') {
                result.splice(keys[0], 1)
            }
        }
    }

    for (let field of delta.removedFields) {
        if (field.indexOf('.') > -1) {
            const keys = field.split('.')
            if (!watchFields || watchFields.includes(keys[0]))
                applyDeleteToPart(result, keys)
        } else {
            delete result[field]
        }
    }
    // console.log('--------------------------------result----------------')
    // console.log(result)
    return result
}

export const updateResource = (code, id, update, parState) => (
    dispatch,
    getState
) => {
    if (update && id && code) {
        const state = getState()
        const check = key => {
            if (state[key]._id === id) {
                if (state[key].__v === update.updatedFields.__v - 1) {
                    const element = applyDelta(state[key], update)
                    dispatch(actions.setData({ [key]: element }))
                } else {
                    if (state[key].id) requestResource(code, [id], parState)
                }
            }
        }
        const check2 = (key, watchFields) => {
            let updateData = {
                [key]: cloneDeep(state[key]),
            }
            const friend = updateData[key][id]
            if (friend) {
                if (friend.__v === update.updatedFields.__v - 1) {
                    updateData[key][id] = applyDelta(
                        friend,
                        update,
                        watchFields
                    )
                    dispatch(actions.setData(updateData))
                } else {
                    requestResource(code, [id], parState)
                }
            }
        }
        switch (code) {
            case 'user':
                check('user')
                break
            case 'account':
                check('account')
                check('profile')
                break
            case 'progress':
            // check('progress')
            // break
            case 'reward':
            // check('reward')
            // break
            case 'activity':
                // check('activity')
                // break
                dispatch(actions.setData(check2(code)))
                break
            case 'post':
                dispatch(actions.setData(check2(code)))
                break
            case 'friendData':
                dispatch(
                    actions.setData(
                        check2(code, ['name', 'image', 'notifications', '__v'])
                    )
                )
                break
            case 'progressData':
                dispatch(
                    actions.setData(
                        check2(code, [
                            'owner',
                            'worker',
                            'goal',
                            'name',
                            'notifications',
                            'likes',
                            'followingAccounts',
                            'activities',
                            'status',
                            '__v',
                        ])
                    )
                )
                break
            case 'rewardData':
                dispatch(
                    actions.setData(
                        check2(code, [
                            'name',
                            'owner',
                            'images',
                            'likes',
                            'followingAccounts',
                            '__v',
                        ])
                    )
                )
                break
            case 'activityData':
                dispatch(
                    actions.setData(
                        check2(code, [
                            'name',
                            'owner',
                            'images',
                            'likes',
                            'stages',
                            'followingAccounts',
                            '__v',
                        ])
                    )
                )
                break
            case 'postData':
                dispatch(
                    actions.setData(check2(code, ['notifications', '__v']))
                )
                break

            case 'transactionData':
                dispatch(actions.setData(check2(code)))
                break
            default:
                break
        }
    }
}
