import React, { useState, useEffect } from 'react'
import { withRouter } from 'react-router-dom'

import Upload from 'antd/es/upload'
import { uploadFile } from '../../utils/uploadFile'
import * as wsActions from '../../websocketActions'
import { Box, TextField, Button, makeStyles } from '@material-ui/core'
import UserButton from '../UserButton/UserButton'
import PlusOutlined from '@ant-design/icons/PlusOutlined'
import { spanStyle } from './Message'
import ImagePickerMulti from '../UI/ImagePickerMulti'
import { useTranslation } from 'react-i18next'

const useStyles = makeStyles({
    text: {
        '&>div::after': { border: 'none' },
        '&>div::before': { border: 'none !important' },
        '& textarea': {
            fontSize: '14px',
        },
    },
})
const AddMessage = props => {
    const { t } = useTranslation()
    const classes = useStyles()
    const [previewImage, setPreviewImage] = useState('')
    const [imageLoading, setImageLoading] = useState(false)
    const [images, setImages] = useState(props.images || [])
    const { post, account } = props

    const [messageText, setMessageText] = useState(
        props.content ||
            (props.replyToUser ? '@' + props.replyToUser + ' ' : '')
    )

    useEffect(() => {
        setMessageText(
            props.content ||
                (props.replyToUser ? '@' + props.replyToUser + ' ' : '')
        )
    }, [props.replyToUser, props.content])

    const uploadImage = file => {
        uploadFile(file, 800, undefined, url => {
            setImages([...images, url])
        })
    }

    return post && account._id ? (
        <Box style={{ display: 'flex', width: '100%', margin: '10px 0px' }}>
            <Box style={{ marginRight: '10px' }}>
                <UserButton
                    noName={true}
                    id={account._id}
                    span={true}
                    size={props.isTop ? 36 : 25}
                />
            </Box>
            <Box style={{ flex: 1 }}>
                <Box
                    style={{
                        background: '#efefef',
                        padding: '5px 15px',
                        borderRadius: '15px',
                    }}
                >
                    <TextField
                        multiline
                        value={messageText}
                        onChange={e => setMessageText(e.target.value)}
                        margin="dense"
                        placeholder={t('post.addComment')}
                        className={classes.text}
                        fullWidth
                    />
                    {images.length > 0 && (
                        <ImagePickerMulti
                            images={images}
                            onValueChange={value => setImages(value)}
                        />
                    )}
                    <Button
                        onClick={() => {
                            wsActions.sendMessage(
                                account._id,
                                post._id,
                                messageText,
                                images,
                                props.replyTo,
                                props.editingMessage
                            )
                            setMessageText('')
                            setImages([])
                            if (props.onSend) props.onSend()
                        }}
                    >
                        <span style={spanStyle}>{t('g.send')}</span>
                    </Button>
                    <Upload
                        accept="image/*"
                        showUploadList={false}
                        customRequest={({ file }) => {
                            if (images.length > 8) {
                                message.warning(t('post.imageLimit'))
                                return
                            }
                            setImageLoading(true)
                            uploadFile(file, 800, undefined, url => {
                                setImageLoading(false)
                                if (!url) {
                                    setImages([...images])
                                } else {
                                    setImages([...images, url])
                                }
                            })
                        }}
                    >
                        <Button
                            onClick={e => {
                                if (window.isRNWebView) {
                                    e.stopPropagation()
                                    window.ReactNativeWebView.postMessage(
                                        'uploadFile'
                                    )
                                    window.callback = url => {
                                        setImageLoading(false)
                                        setImages([...images, url])
                                    }
                                }
                            }}
                        >
                            <span style={spanStyle}>{t('g.image')}</span>
                        </Button>
                    </Upload>
                    {props.cancel && (
                        <Button onClick={props.cancel}>
                            <span style={spanStyle}>{t('g.cancel')}</span>
                        </Button>
                    )}
                </Box>
            </Box>
        </Box>
    ) : (
        <Box style={{ height: '10px' }} />
    )
}

export default withRouter(AddMessage)
