import { setData } from './auth'

export const saveResource = (data, type, newOnlineUsers) => (
    dispatch,
    getState
) => {
    if (data.length > 0) {
        const state = getState()
        switch (type) {
            case 'user':
                const user = data[0]
                let currentAccount = user.currentAccount
                if (!currentAccount) currentAccount = user.myAccount
                // if(!currentAccount)
                //activate-account
                dispatch(setData({ user, currentAccount }))
                break
            case 'account':
                const account = data.find(
                    item => item._id === state.currentAccount
                )
                if (account) {
                    dispatch(setData({ account, newOnlineUsers }))
                }

                const profile = data.find(
                    item => item._id === state.currentProfile
                )
                if (profile) {
                    dispatch(setData({ profile, newOnlineUsers }))
                }

                break
            case 'progress':
                let progress = {}
                for (let item of data) {
                    progress[item._id] = item
                }
                dispatch(setData({ progress }))
                break

            case 'post':
                let post = {}
                for (let item of data) {
                    post[item._id] = item
                }
                dispatch(setData({ post }))
                break

            case 'reward':
                let reward = {}
                for (let item of data) {
                    reward[item._id] = item
                }
                dispatch(setData({ reward }))
                break
            case 'activity':
                let activity = {}
                for (let item of data) {
                    activity[item._id] = item
                }
                dispatch(setData({ activity }))
                break

            case 'friendData':
                let friendData = {}
                for (let item of data) {
                    friendData[item._id] = item
                }
                dispatch(setData({ friendData, newOnlineUsers }))
                break
            case 'progressData':
                let progressData = {}
                for (let item of data) {
                    progressData[item._id] = item
                }
                dispatch(setData({ progressData }))
                break
            case 'transactionData':
                let transactionData = {}
                for (let item of data) {
                    transactionData[item._id] = item
                }
                dispatch(setData({ transactionData }))
                break
            case 'postData':
                let postData = {}
                for (let item of data) {
                    postData[item._id] = item
                }
                dispatch(setData({ postData }))
                break
            case 'rewardData':
                let rewardData = {}
                for (let item of data) {
                    rewardData[item._id] = item
                }
                dispatch(setData({ rewardData }))
                break
            case 'activityData':
                let activityData = {}
                for (let item of data) {
                    activityData[item._id] = item
                }
                dispatch(setData({ activityData }))
                break

            default:
                break
        }
    }
}
