import AddFriends from '../Friends/AddFriends'
import DialogFullscreen from '../../components/UI/DialogFullscreen'
import ShareButton from '../../components/UI/ShareButton'
import { sortProgresses } from '../Explore/Explore'
import ProfileRewards from '../../components/ProfileRewards/ProfileRewards'

import Badge from 'antd/es/badge'
import LanguageSwitcher from '../../components/LanguageSwitcher/LanguageSwitcher'
import { useTranslation } from 'react-i18next'
import Activities from '../../components/Activities/Activities'
import EditResource from '../../components/EditResource/EditResource'
import Transactions from '../../components/Transactions/Transactions'
import { addUnsorted } from './actions'

dayjs().format()
const TransitionComponent = props => <Slide direction="up" {...props} />

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        title: {
            marginLeft: theme.spacing(2),
            flex: 1,
        },
        sectionTitle: {
            fontSize: 13,
            margin: '30px 15px 10px',
            textTransform: 'uppercase',
            fontWeight: 600,
        },
    })
)
const ProfileMM = props => {
    const { t } = useTranslation()
    const [currentTab, setCurrentTab] = useState('accounts')
    const classesM = useStyles()
    const [data, setData] = useState({})
    const [followersVisible, setFollowersVisible] = useState('')

    const { profile, account, progressData } = props
    const [deleteVisible, setDeleteVisible] = useState()
    const [deleteField, setDeleteField] = useState('')
    const [startProgressVisible, setStartProgressVisible] = useState(false)
    const [addFriendOpen, setAddFriendOpen] = useState(false)

    useEffect(() => {
        const pathParts = props.history.location.pathname.split('/')
        if (pathParts.length > 2) {
            const profileId = pathParts[2]
            if (props.isAuthed) {
                props.setData({ currentProfile: profileId })
            } else {
                pullData(props, data, setData, { forceAuth: false }, data => {
                    const getData = key => {
                        const d = {}
                        for (let i of data[key]) d[i._id] = i
                        return d
                    }

                    const friendData = getData('friendData')
                    const progressData = getData('progressData')
                    const activityData = getData('activityData')
                    const rewardData = getData('rewardData')
                    const progress = getData('progress')
                    const activity = getData('activity')
                    const reward = getData('reward')
                    const post = getData('post')
                    props.setData({
                        profile: data.profile,
                        friendData,
                        // post,
                        account: {},
                        progressData,
                        progress,
                        activityData,
                        activity,
                        rewardData,
                        reward,
                        onlineUsers: data.onlineUsers,
                        currentProfile: profileId,
                    })
                })
            }
        } else {
            if (props.isAuthed) {
                props.setData({ currentProfile: account._id })
            }
        }
    }, [props.isAuthed, props.history.location.pathname, props.account._id])

    const isOwner =
        account &&
        account._id &&
        profile &&
        profile._id &&
        account._id === profile._id
    useEffect(() => {
        if (isOwner && props.structure) {
            addUnsorted(props.structure, props.setStructure, profile)
        }
    }, [props.structure, profile])

    if (!profile || !profile._id) return null

    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []
    if (profile && profile.friends && account && account._id) {
        for (let friend of account.friends) {
            if (friend.status === 'friend') realFriends.push(friend)
            else if (friend.status === 'invited') invitedFriends.push(friend)
            else if (friend.status === 'inviting') invitingFriends.push(friend)
        }
    }

    const loading = false

    const { progresses } = profile
    let loadedProgresses
    if (progresses) {
        loadedProgresses = progresses
            .filter(item => progressData[item])
            .map(item => progressData[item])
    }

    // const newTransactions =
    //     (transactions &&
    //         transactions
    //             .filter(item => transactionData[item])
    //             .map(item => transactionData[item])
    //             .filter(item => item.status !== 'Confirmed')) ||
    //     []

    // const recievedTransactions =
    //     (transactions &&
    //         transactions
    //             .filter(item => transactionData[item])
    //             .map(item => transactionData[item])
    //             .filter(item => item.status === 'Confirmed')) ||
    //     []

    return (
        <>
            <Helmet>
                <title>
                    {profile.name} {t('g.shareText')}
                </title>
                <meta
                    name="description"
                    content="Addspire is a social network where you can inspire and motivate yourself or the people you care."
                />
            </Helmet>

            <Paper style={{ padding: '15px', whiteSpace: 'nowrap' }}>
                <div style={{ textAlign: 'center', marginTop: '10px' }}>
                    {isOwner ? (
                        <Upload
                            name="avatar"
                            accept="image/*"
                            listType="picture-card"
                            showUploadList={false}
                            customRequest={({ file }) => {
                                uploadFile(file, 800, 'avatar', url => {
                                    if (url) props.increaseAvatarV()
                                })
                            }}
                        >
                            <div
                                style={{
                                    flex: 1,
                                    display: 'flex',
                                    height: '100%',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    flexDirection: 'column',
                                }}
                                onClick={e => {
                                    if (window.isRNWebView) {
                                        e.stopPropagation()
                                        window.ReactNativeWebView.postMessage(
                                            'uploadFile'
                                        )
                                        window.callback = url => {
                                            props.increaseAvatarV()
                                        }
                                    }
                                }}
                            >
                                {account.image ? (
                                    <Avatar
                                        src={getAvatar(
                                            account._id,
                                            account.image
                                        )}
                                        size={150}
                                        style={{ margin: '25px' }}
                                    />
                                ) : (
                                    <>
                                        {loading ? (
                                            <LoadingOutlined />
                                        ) : (
                                            <PlusOutlined />
                                        )}
                                        <div className="ant-upload-text">
                                            {t('g.upload')}
                                        </div>
                                    </>
                                )}
                            </div>
                        </Upload>
                    ) : (
                        <Avatar
                            src={getAvatar(profile._id, profile.image)}
                            size={200}
                        />
                    )}

                    <Box style={{ padding: '0 15px' }}>
                        {props.onlineUsers.includes(profile._id) && (
                            <Badge color="green" text="online" />
                        )}
                        <TypographyA.Title
                            level={2}
                            editable={
                                isOwner
                                    ? {
                                          onChange: res =>
                                              wsActions.editAccount(
                                                  { name: res },
                                                  account._id
                                              ),
                                          icon: (
                                              <IconButton color="primary">
                                                  <Edit />
                                              </IconButton>
                                          ),
                                          maxLength: fieldLength.name,
                                          autoSize: {
                                              minRows: 1,
                                              maxRows: 1,
                                          },
                                      }
                                    : false
                            }
                        >
                            {profile.name}
                        </TypographyA.Title>
                    </Box>
                    <Typography variant="h5">({profile._id})</Typography>
                    <Box
                        style={{
                            margin: '5px 0px',
                        }}
                    >
                        <Button
                            onClick={() => setFollowersVisible('followers')}
                            style={{ marginRight: '20px' }}
                        >
                            <span
                                style={{
                                    textTransform: 'none',
                                }}
                            >
                                {`${profile.followingAccounts.length} ${t(
                                    'g.followers'
                                )}`}
                            </span>
                        </Button>
                        <Button
                            onClick={() => setFollowersVisible('following')}
                        >
                            <span
                                style={{
                                    textTransform: 'none',
                                }}
                            >
                                {`${profile.followProgresses.length +
                                    profile.followAccounts.length} 
                                    ${t('g.following')}`}
                            </span>
                        </Button>
                    </Box>

                    <div>
                        <ShareButton
                            link={'https://addspire.com/profile/' + profile._id}
                            media={
                                profile.image &&
                                getAvatar(profile._id, profile.image)
                            }
                            title={profile.name + t('profile.shareText')}
                            item={{
                                profileId: profile._id,
                                profileName: profile.name,
                            }}
                        />
                        {account && account._id !== profile._id && (
                            <span style={{ marginRight: '10px' }}>
                                {realFriends.find(
                                    item => item.friend === profile._id
                                ) ? (
                                    <Chip color="primary" label="Friends" />
                                ) : null}
                                {account.followAccounts &&
                                    !account.followAccounts.includes(
                                        profile._id
                                    ) && (
                                        <Button
                                            color="primary"
                                            onClick={() =>
                                                wsActions.followAccount(
                                                    account._id,
                                                    profile._id
                                                )
                                            }
                                            style={{
                                                marginRight: '20px',
                                            }}
                                            variant="contained"
                                        >
                                            {t('g.follow')}
                                        </Button>
                                    )}
                            </span>
                        )}
                        <FriendActionButton
                            account={account}
                            realFriends={realFriends}
                            invitedFriends={invitedFriends}
                            invitingFriends={invitingFriends}
                            _id={profile._id}
                            additionalButtons={
                                isOwner
                                    ? [
                                          {
                                              onClick: () =>
                                                  props.history.push('/logout'),
                                              title: t('g.logout'),
                                          },
                                      ]
                                    : null
                            }
                            asText={true}
                        />
                    </div>
                    <Box />
                </div>
            </Paper>

            {/* {isOwner && (
                <>
                    <Typography
                        className={classesM.sectionTitle}
                        color="textSecondary"
                        gutterBottom
                    >
                        {t('profile.settings')}
                    </Typography>
                    <Paper
                        style={{
                            padding: '15px',
                        }}
                    >
                        <Box>
                            <LanguageSwitcher settings />
                        </Box>
                        <Box style={{ margin: '20px auto' }}>
                            <Button
                                onClick={() => props.history.push('/logout')}
                                color="secondary"
                            >
                                {t('g.logout')}
                            </Button>
                        </Box>
                        <Button
                            onClick={() => setDeleteVisible(true)}
                            color="secondary"
                        >
                            {t('profile.deleteAccount')}
                        </Button>
                    </Paper>

                    <Dialog open={Boolean(deleteVisible)} scroll="paper">
                        <DialogTitle>{t('profile.deleteAccount')}</DialogTitle>
                        <DialogContent>
                            <DialogContentText>
                                <p>
                                    {t('profile.pleaseEnterNickname')}
                                    {account._id}
                                    {t('profile.andPressOk')}
                                </p>
                                <TextField
                                    onChange={e =>
                                        setDeleteField(e.target.value)
                                    }
                                />
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={() => setDeleteVisible(false)}>
                                {t('g.cancel')}
                            </Button>
                            <Button
                                onClick={() => {
                                    if (deleteField !== account._id) {
                                        message.warning(
                                            'The nickmanme is not correct.'
                                        )
                                    } else {
                                        wsActions.deleteAccount(account._id)
                                    }
                                }}
                                color="primary"
                            >
                                {t('g.delete')}
                            </Button>
                        </DialogActions>
                    </Dialog>
                </>
            )} */}
            <Dialog
                open={Boolean(followersVisible)}
                onClose={() => setFollowersVisible('')}
                title={
                    followersVisible === 'following'
                        ? t('g.following')
                        : t('g.followers')
                }
                fullWidth
                maxWidth="md"
            >
                <DialogTitle>
                    {followersVisible !== 'following'
                        ? t('g.followers')
                        : t('g.following')}
                    {followersVisible === 'following' && (
                        <Tabs
                            value={currentTab}
                            onChange={(event, newValue) =>
                                setCurrentTab(newValue)
                            }
                            indicatorColor="primary"
                            textColor="primary"
                            variant="scrollable"
                            scrollButtons="auto"
                        >
                            <Tab label={t('g.users')} value="accounts" />
                            <Tab label={t('g.goals')} value="goals" />
                            <Tab label={t('g.activities')} value="activities" />
                            <Tab label={t('g.rewards')} value="rewards" />
                        </Tabs>
                    )}
                </DialogTitle>
                <DialogContent>
                    {followersVisible === 'following' ? (
                        currentTab === 'accounts' ? (
                            profile.followAccounts.map(item => (
                                <MasonryCard
                                    friends={true}
                                    noLink={true}
                                    key={item}
                                    actions={
                                        <FollowButton
                                            account={account}
                                            id={item}
                                            type="account"
                                        />
                                    }
                                    body={
                                        <UserButton
                                            span={true}
                                            id={item}
                                            size={50}
                                        />
                                    }
                                />
                            ))
                        ) : (
                            <MasonryGrid>
                                {profile[
                                    currentTab === 'goals'
                                        ? 'followProgresses'
                                        : currentTab === 'rewards'
                                        ? 'followRewards'
                                        : 'followActivities'
                                ].map(item => (
                                    <MasonryCard
                                        id={item}
                                        onClickLink={
                                            '/' + currentTab + '/' + item
                                        }
                                        // noLink
                                        key={item}
                                        actions={
                                            <FollowButton
                                                type={
                                                    currentTab === 'goals'
                                                        ? 'progress'
                                                        : currentTab ===
                                                          'rewards'
                                                        ? 'reward'
                                                        : 'activity'
                                                }
                                                account={account}
                                                id={item}
                                            />
                                        }
                                        body={
                                            <ProgressButton
                                                span={true}
                                                id={item}
                                                size={50}
                                                type={currentTab}
                                            />
                                        }
                                        style={{
                                            // maxHeight: '170px',
                                            marginTop: '20px',
                                        }}
                                        images={
                                            props[
                                                currentTab === 'goals'
                                                    ? 'progressData'
                                                    : currentTab === 'rewards'
                                                    ? 'rewardData'
                                                    : 'activityData'
                                            ][item] &&
                                            props[
                                                currentTab === 'goals'
                                                    ? 'progressData'
                                                    : currentTab === 'rewards'
                                                    ? 'rewardData'
                                                    : 'activityData'
                                            ][item].images
                                        }
                                    />
                                ))}
                            </MasonryGrid>
                        )
                    ) : (
                        profile.followingAccounts.map(item => (
                            <MasonryCard
                                friends={true}
                                noLink={true}
                                key={item}
                                actions={
                                    <FollowButton
                                        account={account}
                                        id={item}
                                        type="account"
                                    />
                                }
                                body={
                                    <UserButton
                                        span={true}
                                        id={item}
                                        size={50}
                                    />
                                }
                            />
                        ))
                    )}
                </DialogContent>
                <DialogActions>
                    <Button
                        onClick={() => setFollowersVisible('')}
                        color="primary"
                    >
                        {t('g.close')}
                    </Button>
                </DialogActions>
            </Dialog>
            {isOwner && (
                <Dialog
                    fullScreen
                    open={startProgressVisible}
                    onClose={() => {
                        setStartProgressVisible(false)
                    }}
                    TransitionComponent={TransitionComponent}
                    scroll="paper"
                >
                    <EditResource
                        account={account}
                        friendData={props.friendData}
                        cancel={() => setStartProgressVisible(false)}
                        type="goal"
                    />
                </Dialog>
            )}
            {isOwner && (
                <DialogFullscreen
                    open={addFriendOpen}
                    close={() => {
                        setAddFriendOpen(false)
                    }}
                    title={t('g.addFriend')}
                >
                    <AddFriends
                        close={() => setAddFriendOpen(false)}
                        open={addFriendOpen}
                        account={account}
                        friendData={props.friendData}
                    />
                </DialogFullscreen>
            )}
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        isAuthed: state.isAuthed,
        friendData: state.friendData,
        progressData: state.progressData,
        transactionData: state.transactionData,
        rewardData: state.rewardData,
        activityData: state.activityData,
        onlineUsers: state.onlineUsers,
        structure: state.structureMM,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
        increaseAvatarV: () => dispatch(actions.increaseAvatarV()),
        setStructure: (structure, noNeedToSave) =>
            dispatch(actions.setStructure(structure, noNeedToSave)),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(memo(ProfileMM))
)
